import React, { Component } from "react";
import Table from "../common/table";
import {Button, Form, Modal} from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	  faEye,
	  faPencilAlt,
	  faCheck
	} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class CustomerOrderViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" onClick={this.props.onClick}>
        <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
          	<Form>
          	  <div className="form-group row">
        <div className="col-md-6">
          <b><label>Customer Order Number</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.customerOrderNumber}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Order Date</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.orderDate}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Delivery Date</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.deliveryDate}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Total Amount</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.totalAmount}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Remarks</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.remarks}
        </div>
      </div>
	
	          {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
	        </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class CustomerOrderTable extends UxCrudTable {
  state = {
	DATA_CREATE: "CUSTOMER_ORDER_CREATE",
    DATA_READ: "CUSTOMER_ORDER_READ",
    DATA_UPDATE: "CUSTOMER_ORDER_UPDATE",
    DATA_DELETE: "CUSTOMER_ORDER_DELETE",
    DATA_APPROVE: "CUSTOMER_ORDER_APPROVE",
	userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    translations: {
	  titleLabel: "Customer Order",
	  actionsLabel: "Actions",
	  idLabel: "Id",
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      customerOrderNumberLabel: "Customer Order Number",
customerIdLabel: "Customer",
orderDateLabel: "Order Date",
deliveryDateLabel: "Delivery Date",
customerOrderItemLabel: "Customer Order Items",
totalAmountLabel: "Total Amount",
paymentMethodIdLabel: "Payment Method",
remarksLabel: "Remarks",

    },
  };

 
  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          idLabel: translationsObj["i18n.common.idLabel"]
            ? translationsObj["i18n.common.idLabel"]
            : thisContext.state.translations.idLabel,
         actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          customerOrderNumberLabel: translationsObj["i18n.customerOrder.customerOrderNumber.label"] ? translationsObj["i18n.customerOrder.customerOrderNumber.label"] : thisContext.state.translations.customerOrderNumberLabel,
customerIdLabel: translationsObj["i18n.customerOrder.customerId.label"] ? translationsObj["i18n.customerOrder.customerId.label"] : thisContext.state.translations.customerIdLabel,
orderDateLabel: translationsObj["i18n.customerOrder.orderDate.label"] ? translationsObj["i18n.customerOrder.orderDate.label"] : thisContext.state.translations.orderDateLabel,
deliveryDateLabel: translationsObj["i18n.customerOrder.deliveryDate.label"] ? translationsObj["i18n.customerOrder.deliveryDate.label"] : thisContext.state.translations.deliveryDateLabel,
customerOrderItemLabel: translationsObj["i18n.customerOrder.customerOrderItem.label"] ? translationsObj["i18n.customerOrder.customerOrderItem.label"] : thisContext.state.translations.customerOrderItemLabel,
totalAmountLabel: translationsObj["i18n.customerOrder.totalAmount.label"] ? translationsObj["i18n.customerOrder.totalAmount.label"] : thisContext.state.translations.totalAmountLabel,
paymentMethodIdLabel: translationsObj["i18n.customerOrder.paymentMethodId.label"] ? translationsObj["i18n.customerOrder.paymentMethodId.label"] : thisContext.state.translations.paymentMethodIdLabel,
remarksLabel: translationsObj["i18n.customerOrder.remarks.label"] ? translationsObj["i18n.customerOrder.remarks.label"] : thisContext.state.translations.remarksLabel,

        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

 async loadColumns() {
	this.columns = [
	    { path: "id", label: this.state.translations.idLabel },
	    {
        path: "customerOrderNumber",
        label: this.state.translations.customerOrderNumberLabel,
      },
{
        path: "orderDate",
        label: this.state.translations.orderDateLabel,
      },
{
        path: "deliveryDate",
        label: this.state.translations.deliveryDateLabel,
      },
{
        path: "totalAmount",
        label: this.state.translations.totalAmountLabel,
      },
{
        path: "remarks",
        label: this.state.translations.remarksLabel,
      },

	  // {
      //   key: "view",
      //   label: this.state.translations.viewLabel,
      //   content: (item) => (
      //     <CustomerOrderViewModal
      //       itemId={this.state.itemId}
      //       show={this.state.showView}
      //       title={this.state.title}
      //       body={this.state.body}
      //       data={this.state.data}
      //       onClick={() => this.handleViewShow(item)}
      //       onHide={this.handleViewClose}
      //     />
      //   ),
      // },
  	];

    this.loadActionColumns();
	this.setState({ columns: this.columns });
  }

}

export default CustomerOrderTable;
