class AppPermissionTable extends UxCrudTable {
  state = {
    userPermissions: [],
    DATA_VIEW: "APP_PERMISSION_VIEW",
    DATA_CREATE: "APP_PERMISSION_CREATE",
    DATA_READ: "APP_PERMISSION_READ",
    DATA_UPDATE: "APP_PERMISSION_UPDATE",
    DATA_DELETE: "APP_PERMISSION_DELETE",
    DATA_APPROVE: "APP_PERMISSION_APPROVE",
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    body: "",
    data: [],
    title: "App Permission Details",
    translations: {
      actionsLabel: "Actions",
      idLabel: "Id", viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
     deleteLabel: "Delete",
      permissionNameLabel: "Permission Name",
      isActiveLabel: "Is Active",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          idLabel: translationsObj["i18n.common.idLabel"]
            ? translationsObj["i18n.common.idLabel"]
            : thisContext.state.translations.idLabel,

          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

 deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          permissionNameLabel: translationsObj[
            "i18n.appPermission.permissionName.label"
          ]
            ? translationsObj["i18n.appPermission.permissionName.label"]
            : thisContext.state.translations.permissionNameLabel,

          permissionNamePlaceholder: translationsObj[
            "i18n.appPermission.permissionName.placeholder"
          ]
            ? translationsObj["i18n.appPermission.permissionName.placeholder"]
            : thisContext.state.translations.permissionNamePlaceholder,
          isActiveLabel: translationsObj["i18n.appPermission.isActive.label"]
            ? translationsObj["i18n.appPermission.isActive.label"]
            : thisContext.state.translations.isActiveLabel,
          isActivePlaceholder: translationsObj[
            "i18n.appPermission.isActive.placeholder"
          ]
            ? translationsObj["i18n.appPermission.isActive.placeholder"]
            : thisContext.state.translations.isActivePlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  async loadColumns() {
    this.columns = [
      { path: "id", label: this.state.translations.idLabel },
      {
        path: "permissionName",
        label: this.state.translations.permissionNameLabel,
      },
      {
        path: "isActive",
        label: this.state.translations.isActiveLabel,
      },
    ];


    this.loadActionColumns();
    this.setState({ columns: this.columns });
  }

}

export default AppPermissionTable;
