import React, { Component } from "react";

// Create a new context
import AdminPanelSideBar from "../../components/sidebar/AdminPanelSideBar";
import AdminPanelContent from "../content/AdminPanelContent";
import PublicFooter from "../../components/public/includes/PublicFooter";

import { loadTranslations } from "../../components/common/authenticationService";
// const StateContext = createContext();

class AdminLayout extends Component {
  constructor(props) {
    super(props);

    // Moblie first
    this.state = {
      activeLanguage: this.props.activeLanguage,
      eventFired: this.props.eventFired,
      isRtl: this.props.isRtl,
      isDark: this.props.isDark,
      isOpen: this.props.isOpen,
      isMobile: this.props.isMobile,
    };

    this.previousWidth = -1;
  }

  updateWidth() {
    const width = window.innerWidth;
    const widthLimit = 576;
    const isMobile = width <= widthLimit;
    const wasMobile = this.previousWidth <= widthLimit;

    if (isMobile !== wasMobile) {
      this.setState({
        isOpen: !isMobile,
      });
    }

    this.previousWidth = width;
  }

  /**
   * Add event listener
   */
  componentDidMount() {
    this.updateWidth();
    window.addEventListener("resize", this.updateWidth.bind(this));
  }

  /**
   * Remove event listener
   */
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWidth.bind(this));
  }

  toggle = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  reloadSiblingComponents = (updatedLanguage) => {
    localStorage.setItem("activeLanguage", updatedLanguage);
    this.setState({ activeLanguage: updatedLanguage });
    this.setState({ eventFired: 'languageChange' });
    this.sideBarComponent.reloadTranslations(updatedLanguage);
    this.footerComponent.reloadTranslations(updatedLanguage);
  };

  toggleRtl = () => {
    let thisContext = this;
    if (this.state.isRtl) {
      // load default english labels
      loadTranslations(this.state.activeLanguage).then(function () {
        localStorage.setItem("activeLayout", "LTR");
        console.log("LTR...");
        thisContext.setState({ isRtl: false });
      });
    } else {
      // load default arabic labels
      loadTranslations("ar").then(function () {
        localStorage.setItem("activeLayout", "RTL");
        console.log("LTR...");
        thisContext.setState({ isRtl: true });
      });
    }
  };

  renderChildrenWithProps() {
    // State you want to share
    const stateValues = {
      isRtl: this.state.isRtl,
      isDark: this.state.isDark,
      isOpen: this.state.isOpen,
      isMobile: this.state.isMobile,
    };

    // Functions you want to share
    const stateFunctions = {
      toggle: this.toggle,
      toggleRtl: this.toggleRtl,
      reloadSiblingComponents: this.reloadSiblingComponents,
    };

    // Pass the shared state and functions as arguments to the children
    return this.props.children(stateValues, stateFunctions);
  }

  render() {
    const { children } = this.props;
    const stateValues = {
      eventFired: this.state.eventFired,
      activeLanguage: this.state.activeLanguage,
      isRtl: this.state.isRtl,
      isDark: this.state.isDark,
      isOpen: this.state.isOpen,
      isMobile: this.state.isMobile,
      toggle: this.toggle,
      toggleRtl: this.toggleRtl,
      reloadSiblingComponents: this.reloadSiblingComponents,
    };
    // children.props.staticContext = {
    //   test:"Arifur Rahman"
    // }
    // console.log(children);
    return (
      <>
        <div className="App wrapper">
          {this.state.isRtl && (
            <>
              <AdminPanelContent
                reloadSiblingComponents={this.reloadSiblingComponents}
                toggle={this.toggle}
                toggleRtl={this.toggleRtl}
                isOpen={this.state.isOpen}
                isRtl={this.state.isRtl}
              >
                {React.Children.map(children, (child) =>
                  React.cloneElement(child, { stateValues })
                )}
                {/* {children} */}
              </AdminPanelContent>
              <AdminPanelSideBar
                activeLanguage={this.state.activeLanguage}
                toggle={this.toggle}
                toggleRtl={this.toggleRtl}
                isOpen={this.state.isOpen}
                isRtl={this.state.isRtl}
                ref={(child) => { this.sideBarComponent = child; }}
              />
            </>
          )}
          {!this.state.isRtl && (
            <>
              <AdminPanelSideBar
                activeLanguage={this.state.activeLanguage}
                toggle={this.toggle}
                toggleRtl={this.toggleRtl}
                isOpen={this.state.isOpen}
                isRtl={this.state.isRtl}
                ref={(child) => { this.sideBarComponent = child; }}
              />
              <AdminPanelContent
                activeLanguage={this.state.activeLanguage}
                reloadSiblingComponents={this.reloadSiblingComponents}
                toggle={this.toggle}
                toggleRtl={this.toggleRtl}
                isOpen={this.state.isOpen}
                isRtl={this.state.isRtl}
              >
                {React.Children.map(children, (child) =>
                  React.cloneElement(child, { stateValues })
                )}
                <PublicFooter ref={(child) => { this.footerComponent = child; }} />
              </AdminPanelContent>
            </>
          )}

        </div>


      </>
    );
  }
}
export default AdminLayout;
