import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./purchaseOrderService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';
import { getAll as getAllSupplier } from "../supplier/supplierService";
import { getAll as getAllProduct } from "../product/productService";import { getAll as getAllPaymentMethod } from "../paymentMethod/paymentMethodService";

class PurchaseOrderForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["PURCHASE_ORDER_CREATE","PURCHASE_ORDER_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/purchaseOrders/",
	userPermissions: [],
    data: { id: null , purchaseOrderNumber: "", supplierId: "", orderDate: "", expectedDeliveryDate: "", orderVoucharImage: "", purchaseOrderItems: [{productId:"",quantity:"",unitPrice:"",totalPrice:"",}], totalAmount: "", paymentMethodId: "", remarks: "" },
    errors: {},
    validationMessage: {
      purchaseOrderNumber: '',
supplier: '',
orderDate: '',
expectedDeliveryDate: '',
totalAmount: '',
remarks: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Purchase Order",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	purchaseOrderNumberLabel: "Purchase Order Number",
purchaseOrderNumberPlaceholder: "Enter Purchase Order Number",
supplierIdLabel: "Supplier",
supplierIdPlaceholder: "Select Supplier",
orderDateLabel: "Order Date",
orderDatePlaceholder: "Enter Order Date",
expectedDeliveryDateLabel: "Expected Delivery Date",
expectedDeliveryDatePlaceholder: "Enter Expected Delivery Date",
orderVoucharImageLabel: "Order Voucher Image",
orderVoucharImagePlaceholder: "Enter Order Voucher Image",
purchaseOrderItemLabel: "Purchase Order Items",
purchaseOrderItemPlaceholder: "Create Purchase Order Items",
totalAmountLabel: "Total Amount",
totalAmountPlaceholder: "Enter Total Amount",
paymentMethodIdLabel: "Payment Method",
paymentMethodIdPlaceholder: "Select Payment Method",
remarksLabel: "Remarks",
remarksPlaceholder: "Enter Remarks",

    },
	  supplierOptions : [],productOptions : [],purchaseOrderItemOptions : [],paymentMethodOptions : [],
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    purchaseOrderNumber: Joi.string().required().label({this.state.translations.purchaseOrderNumberLabel}),supplier: Joi.number().label("Supplier"),orderDate: Joi.string().required().label({this.state.translations.orderDateLabel}),expectedDeliveryDate: Joi.string().required().label({this.state.translations.expectedDeliveryDateLabel}),orderVoucharImage: Joi.string().label({this.state.translations.orderVoucharImageLabel}),totalAmount: Joi.string().required().label({this.state.translations.totalAmountLabel}),paymentMethod: Joi.number().allow(null),remarks: Joi.string().required().label({this.state.translations.remarksLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      await getAllSupplier().then(function (result) {
        let supplierOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          supplierOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ supplierOptions: supplierOptions });
      });
await getAllProduct().then(function (result) {
        let productOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          productOptions.push({
            value: result.data[i].id,
            label: result.data[i].title,
          });
        }
        thisContext.setState({ productOptions: productOptions });
      });
await getAllPaymentMethod().then(function (result) {
        let paymentMethodOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          paymentMethodOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ paymentMethodOptions: paymentMethodOptions });
      });

      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.purchaseOrder.titleLabel"] ? translationsObj["i18n.purchaseOrder.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          purchaseOrderNumberLabel: translationsObj["i18n.purchaseOrder.purchaseOrderNumber.label"] ? translationsObj["i18n.purchaseOrder.purchaseOrderNumber.label"] : thisContext.state.translations.purchaseOrderNumberLabel,
purchaseOrderNumberPlaceholder: translationsObj["i18n.purchaseOrder.purchaseOrderNumber.placeholder"] ? translationsObj["i18n.purchaseOrder.purchaseOrderNumber.placeholder"] : thisContext.state.translations.purchaseOrderNumberPlaceholder,
supplierIdLabel: translationsObj["i18n.purchaseOrder.supplierId.label"] ? translationsObj["i18n.purchaseOrder.supplierId.label"] : thisContext.state.translations.supplierIdLabel,
supplierIdPlaceholder: translationsObj["i18n.purchaseOrder.supplierId.placeholder"] ? translationsObj["i18n.purchaseOrder.supplierId.placeholder"] : thisContext.state.translations.supplierIdPlaceholder,
orderDateLabel: translationsObj["i18n.purchaseOrder.orderDate.label"] ? translationsObj["i18n.purchaseOrder.orderDate.label"] : thisContext.state.translations.orderDateLabel,
orderDatePlaceholder: translationsObj["i18n.purchaseOrder.orderDate.placeholder"] ? translationsObj["i18n.purchaseOrder.orderDate.placeholder"] : thisContext.state.translations.orderDatePlaceholder,
expectedDeliveryDateLabel: translationsObj["i18n.purchaseOrder.expectedDeliveryDate.label"] ? translationsObj["i18n.purchaseOrder.expectedDeliveryDate.label"] : thisContext.state.translations.expectedDeliveryDateLabel,
expectedDeliveryDatePlaceholder: translationsObj["i18n.purchaseOrder.expectedDeliveryDate.placeholder"] ? translationsObj["i18n.purchaseOrder.expectedDeliveryDate.placeholder"] : thisContext.state.translations.expectedDeliveryDatePlaceholder,
orderVoucharImageLabel: translationsObj["i18n.purchaseOrder.orderVoucharImage.label"] ? translationsObj["i18n.purchaseOrder.orderVoucharImage.label"] : thisContext.state.translations.orderVoucharImageLabel,
orderVoucharImagePlaceholder: translationsObj["i18n.purchaseOrder.orderVoucharImage.placeholder"] ? translationsObj["i18n.purchaseOrder.orderVoucharImage.placeholder"] : thisContext.state.translations.orderVoucharImagePlaceholder,
purchaseOrderItemLabel: translationsObj["i18n.purchaseOrder.purchaseOrderItem.label"] ? translationsObj["i18n.purchaseOrder.purchaseOrderItem.label"] : thisContext.state.translations.purchaseOrderItemLabel,
purchaseOrderItemPlaceholder: translationsObj["i18n.purchaseOrder.purchaseOrderItem.placeholder"] ? translationsObj["i18n.purchaseOrder.purchaseOrderItem.placeholder"] : thisContext.state.translations.purchaseOrderItemPlaceholder,
totalAmountLabel: translationsObj["i18n.purchaseOrder.totalAmount.label"] ? translationsObj["i18n.purchaseOrder.totalAmount.label"] : thisContext.state.translations.totalAmountLabel,
totalAmountPlaceholder: translationsObj["i18n.purchaseOrder.totalAmount.placeholder"] ? translationsObj["i18n.purchaseOrder.totalAmount.placeholder"] : thisContext.state.translations.totalAmountPlaceholder,
paymentMethodIdLabel: translationsObj["i18n.purchaseOrder.paymentMethodId.label"] ? translationsObj["i18n.purchaseOrder.paymentMethodId.label"] : thisContext.state.translations.paymentMethodIdLabel,
paymentMethodIdPlaceholder: translationsObj["i18n.purchaseOrder.paymentMethodId.placeholder"] ? translationsObj["i18n.purchaseOrder.paymentMethodId.placeholder"] : thisContext.state.translations.paymentMethodIdPlaceholder,
remarksLabel: translationsObj["i18n.purchaseOrder.remarks.label"] ? translationsObj["i18n.purchaseOrder.remarks.label"] : thisContext.state.translations.remarksLabel,
remarksPlaceholder: translationsObj["i18n.purchaseOrder.remarks.placeholder"] ? translationsObj["i18n.purchaseOrder.remarks.placeholder"] : thisContext.state.translations.remarksPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("PURCHASE_ORDER_CREATE") ||
      thisContext.state.userPermissions.includes("PURCHASE_ORDER_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      purchaseOrderNumber: obj.purchaseOrderNumber,supplierId: obj.supplierId,orderDate: obj.orderDate,expectedDeliveryDate: obj.expectedDeliveryDate,orderVoucharImage: obj.orderVoucharImage,purchaseOrderItems: obj.purchaseOrderItems,totalAmount: obj.totalAmount,paymentMethodId: obj.paymentMethodId,remarks: obj.remarks,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

    addPurchaseOrderItemRow = (e) => {
	   e.preventDefault();    let updatedPurchaseOrderItems = [...this.state.data.purchaseOrderItems, { productId:"",quantity:"",unitPrice:"",totalPrice:"", }];
    this.setState({
      data: {
        ...this.state.data, purchaseOrderItems: updatedPurchaseOrderItems
      }
    });
    return;
  };

  handlePurchaseOrderItemDelete = (e,index) => {
  e.preventDefault();
    const updatedPurchaseOrderItems = [...this.state.data.purchaseOrderItems];
    updatedPurchaseOrderItems.splice(index, 1);
    // this.state.data.purchaseOrderItems = updatedPurchaseOrderItems;
this.setState({
      data: {
        ...this.state.data, purchaseOrderItems: updatedPurchaseOrderItems
      }
    });
return;
  };

  handlePurchaseOrderItemMultiRowChange = (index, e) => {
    const { name, value } = e.target;
    const updatedPurchaseOrderItems = [...this.state.data.purchaseOrderItems];
    updatedPurchaseOrderItems[index][name] = value;
    //this.state.data.purchaseOrderItems = updatedPurchaseOrderItems;
    this.setState({
      purchaseOrderItems: updatedPurchaseOrderItems
    });
    return;
  };
  handlePurchaseOrderItemMultiRowSelectChange = (index, name, value) => {
    const updatedPurchaseOrderItems = [...this.state.data.purchaseOrderItems];
    updatedPurchaseOrderItems[index][name] = value;
    this.setState({
      productVariants: updatedPurchaseOrderItems
    });
    return;
  };
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-light">
                <Row>
                  <Col md='1'>
                    {
                      this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                        e.preventDefault();
                        thisContext.props.history.push(thisContext.state.basicUrl);
                      })
                    }
                  </Col>
                  <Col md='11'>
                    <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                  </Col>
                </Row>
              </Card.Title>
			{/*<Card.Header>
				<p>Hello Purchase Order please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}

                {this.renderInput(false,false,"id", "Id", "hidden")}
                <Row><Col>{this.renderInput(true,true,"purchaseOrderNumber", this.state.translations.purchaseOrderNumberLabel,this.state.translations.purchaseOrderNumberPlaceholder  ,"text",false,this.state.validationMessage.purchaseOrderNumber)}</Col></Row>
<Row><Col>{this.renderSelect(true,false,"supplierId", this.state.translations.supplierIdLabel, this.state.translations.supplierIdPlaceholder, thisContext.state.supplierOptions, this.state.validationMessage.supplierId)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"orderDate", this.state.translations.orderDateLabel,this.state.translations.orderDatePlaceholder  ,"date",false,this.state.validationMessage.orderDate)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"expectedDeliveryDate", this.state.translations.expectedDeliveryDateLabel,this.state.translations.expectedDeliveryDatePlaceholder  ,"date",false,this.state.validationMessage.expectedDeliveryDate)}</Col></Row>
<Row><Col>{this.renderImageInput(false,"orderVoucharImage",this.state.translations.orderVoucharImageLabel, this.state.translations.orderVoucharImagePlaceholder, this.state.validationMessage.orderVoucharImage)}</Col></Row><table className="table table-striped">
                      <thead>
                        <tr>
 <th>Product Id</th>
 <th>Quantity</th>
 <th>Unit Price</th>
 <th>Total Price</th>
<th className="text-right"><button className="btn btn-sm btn-outline-success" onClick={this.addPurchaseOrderItemRow}><FontAwesomeIcon icon={faPlus}/> Purchase Order Item</button></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.data.purchaseOrderItems.map((purchaseOrderItem, index) => (
                          <tr key={index}>
<td><UxSelect
                                isRequired={false}
                                isMultiple={false}
                                name={'productId'}
                                label={""}
                                placeholder={"Please Select"}
                                options={thisContext.state.productOptions}
                                validationMessage={""}
                                value={purchaseOrderItem.productId}
                                updateSelected={(name, value) => this.handlePurchaseOrderItemMultiRowSelectChange(index, name, value)}
                              /></td>
             <td>
                              <input
                                type="number" class="form-control" placeholder = "Enter Quantity"
                                name="quantity"
                                value={purchaseOrderItem.quantity}
                                onChange={(e) => this.handlePurchaseOrderItemMultiRowChange(index, e)}
                              />
                            </td>
             <td>
                              <input
                                type="number" class="form-control" placeholder = "Enter Unit Price"
                                name="unitPrice"
                                value={purchaseOrderItem.unitPrice}
                                onChange={(e) => this.handlePurchaseOrderItemMultiRowChange(index, e)}
                              />
                            </td>
             <td>
                              <input
                                type="number" class="form-control" placeholder = "Enter Total Price"
                                name="totalPrice"
                                value={purchaseOrderItem.totalPrice}
                                onChange={(e) => this.handlePurchaseOrderItemMultiRowChange(index, e)}
                              />
                            </td>
                            <td className="text-right">
                              <button className="btn btn-sm btn-outline-danger" onClick={(e) => this.handlePurchaseOrderItemDelete(e,index)}><FontAwesomeIcon icon={faTrash}/></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
<Row><Col>{this.renderInput(true,true,"totalAmount", this.state.translations.totalAmountLabel,this.state.translations.totalAmountPlaceholder  ,"number",false,this.state.validationMessage.totalAmount)}</Col></Row>
<Row><Col>{this.renderSelect(true,false,"paymentMethodId", this.state.translations.paymentMethodIdLabel, this.state.translations.paymentMethodIdPlaceholder, thisContext.state.paymentMethodOptions, this.state.validationMessage.paymentMethodId)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"remarks", this.state.translations.remarksLabel,this.state.translations.remarksPlaceholder  ,"textarea",false,this.state.validationMessage.remarks)}</Col></Row>

                {/* {this.renderInput("dueDate", "Due Date")} */}

                
            </Card.Body>

				{thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
          		</Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default PurchaseOrderForm;
