import React from "react";
import { Nav } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faCaretUp, faCopy, faImage } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";

class SubMenuUserManager extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeMenu: null // Stores the active menu item
    };
    this.toggleNavbar = this.toggleNavbar.bind(this);
  }

  componentDidMount() {
    const currentPath = window.location.pathname;

    // Check the current URL path to determine which menu should be active
    if (currentPath.startsWith("/adminpanel/admin/users")) {
      this.setState({ activeMenu: "users" });
    } else if (currentPath.startsWith("/adminpanel/admin/userRequests")) {
      this.setState({ activeMenu: "userRequests" });
    } else if (currentPath.startsWith("/adminpanel/admin/authoritys")) {
      this.setState({ activeMenu: "authoritys" });
    } else if (currentPath.startsWith("/adminpanel/admin/permissionGroups")) {
      this.setState({ activeMenu: "permissionGroups" });
    } else if (currentPath.startsWith("/adminpanel/admin/appPermissions")) {
      this.setState({ activeMenu: "appPermissions" });
    }
    if (currentPath.startsWith("/adminpanel/admin/users")
      || currentPath.startsWith("/adminpanel/admin/userRequests")
      || currentPath.startsWith("/adminpanel/admin/authoritys")
      || currentPath.startsWith("/adminpanel/admin/permissionGroups")
      || currentPath.startsWith("/adminpanel/admin/appPermissions")) {
      this.toggleNavbar("settings");
    }

  }

  toggleNavbar(menu) {
    // Toggle the specific menu passed
    this.setState((prevState) => ({
      activeMenu: prevState.activeMenu === menu ? null : menu
    }));
  }

  renderMenuItem(to, label, icon, menuKey) {
    const { activeMenu } = this.state;
    return (
      <Nav.Item>
        <Nav.Link as="div">
          <NavLink
            to={to}
            className={classNames("nav-link", "nav-item", { open: activeMenu === menuKey })}
          >
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {label}
          </NavLink>
        </Nav.Link>
      </Nav.Item>
    );
  }

  render() {
    const { icon, title, translations, userPermissions } = this.props;
    const { activeMenu } = this.state;

    return (
      <Nav.Item className={classNames({ open: activeMenu })}>
        <Nav.Link
          variant="link"
          className={classNames("d-flex justify-content-between align-items-center nav-link nav-item", { active: activeMenu })}
          onClick={() => this.toggleNavbar("settings")}
        >
          <div>
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {title}
          </div>
          <FontAwesomeIcon icon={activeMenu === "settings" ? faCaretUp : faCaretDown} />
        </Nav.Link>

        {activeMenu === "settings" && (
          <nav className="nav flex-column">
            {userPermissions.includes("USER_REQUEST_READ") &&
              this.renderMenuItem("/admin/userRequests", translations.userRequestMenuLabel, faImage, "userRequests")}

            {userPermissions.includes("USER_READ") && this.renderMenuItem("/admin/users", translations.userMenuLabel, faCopy, "users")}

            {userPermissions.includes("AUTHORITY_READ") &&
              this.renderMenuItem("/admin/authoritys", translations.authorityMenuLabel, faImage, "authoritys")}

            {userPermissions.includes("PERMISSION_GROUP_READ") &&
              this.renderMenuItem("/admin/permissionGroups", translations.permissionGroupMenuLabel, faImage, "permissionGroups")}

            {userPermissions.includes("APP_PERMISSION_READ") &&
              this.renderMenuItem("/admin/appPermissions", translations.appPermissionMenuLabel, faImage, "appPermissions")}
          </nav>
        )}
      </Nav.Item>
    );
  }
}

export default SubMenuUserManager;
