import React, { Component } from "react";
import Table from "../common/table";
import {Button, Form, Modal} from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	  faEye,
	  faPencilAlt,
	  faCheck
	} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class CustomerReviewViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" onClick={this.props.onClick}>
        <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
          	<Form>
          	  <div className="form-group row">
        <div className="col-md-6">
          <b><label>Customer Name</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.customerName}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Customer Image</label></b>
        </div>
        <div className="col-md-6">
          <img src={this.props.data.customerImage} width={'200px'} height={'100px'}/>
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Name</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.name}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Contact Number</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.contactNumber}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Customer Profession</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.customerProfession}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Rating</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.rating}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Review Details</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.reviewDetails}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Suggestions</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.suggestions}
        </div>
      </div>
	
	          {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
	        </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class CustomerReviewTable extends UxCrudTable {
  state = {
	DATA_CREATE: "CUSTOMER_REVIEW_CREATE",
    DATA_READ: "CUSTOMER_REVIEW_READ",
    DATA_UPDATE: "CUSTOMER_REVIEW_UPDATE",
    DATA_DELETE: "CUSTOMER_REVIEW_DELETE",
    DATA_APPROVE: "CUSTOMER_REVIEW_APPROVE",
	userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    translations: {
	  titleLabel: "Customer Review",
	  actionsLabel: "Actions",
	  idLabel: "Id",
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      customerNameLabel: "Customer Name",
customerImageLabel: "Customer Image",
nameLabel: "Name",
contactNumberLabel: "Contact Number",
customerProfessionLabel: "Customer Profession",
ratingLabel: "Rating",
reviewDetailsLabel: "Review Details",
suggestionsLabel: "Suggestions",

    },
  };

 
  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          idLabel: translationsObj["i18n.common.idLabel"]
            ? translationsObj["i18n.common.idLabel"]
            : thisContext.state.translations.idLabel,
         actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          customerNameLabel: translationsObj["i18n.customerReview.customerName.label"] ? translationsObj["i18n.customerReview.customerName.label"] : thisContext.state.translations.customerNameLabel,
customerImageLabel: translationsObj["i18n.customerReview.customerImage.label"] ? translationsObj["i18n.customerReview.customerImage.label"] : thisContext.state.translations.customerImageLabel,
nameLabel: translationsObj["i18n.customerReview.name.label"] ? translationsObj["i18n.customerReview.name.label"] : thisContext.state.translations.nameLabel,
contactNumberLabel: translationsObj["i18n.customerReview.contactNumber.label"] ? translationsObj["i18n.customerReview.contactNumber.label"] : thisContext.state.translations.contactNumberLabel,
customerProfessionLabel: translationsObj["i18n.customerReview.customerProfession.label"] ? translationsObj["i18n.customerReview.customerProfession.label"] : thisContext.state.translations.customerProfessionLabel,
ratingLabel: translationsObj["i18n.customerReview.rating.label"] ? translationsObj["i18n.customerReview.rating.label"] : thisContext.state.translations.ratingLabel,
reviewDetailsLabel: translationsObj["i18n.customerReview.reviewDetails.label"] ? translationsObj["i18n.customerReview.reviewDetails.label"] : thisContext.state.translations.reviewDetailsLabel,
suggestionsLabel: translationsObj["i18n.customerReview.suggestions.label"] ? translationsObj["i18n.customerReview.suggestions.label"] : thisContext.state.translations.suggestionsLabel,

        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

 async loadColumns() {
	this.columns = [
	    { path: "id", label: this.state.translations.idLabel },
	    {
        path: "customerName",
        label: this.state.translations.customerNameLabel,
      },
{
      key: "customerImage",
      label: this.state.translations.customerImageLabel,
      content: (item) => (
        <img src={item.customerImage}  width={'60px'} height={'30px'} />
      ),
    },{
        path: "name",
        label: this.state.translations.nameLabel,
      },
{
        path: "contactNumber",
        label: this.state.translations.contactNumberLabel,
      },
{
        path: "customerProfession",
        label: this.state.translations.customerProfessionLabel,
      },
{
        path: "rating",
        label: this.state.translations.ratingLabel,
      },
{
        path: "reviewDetails",
        label: this.state.translations.reviewDetailsLabel,
      },
{
        path: "suggestions",
        label: this.state.translations.suggestionsLabel,
      },

	  // {
      //   key: "view",
      //   label: this.state.translations.viewLabel,
      //   content: (item) => (
      //     <CustomerReviewViewModal
      //       itemId={this.state.itemId}
      //       show={this.state.showView}
      //       title={this.state.title}
      //       body={this.state.body}
      //       data={this.state.data}
      //       onClick={() => this.handleViewShow(item)}
      //       onHide={this.handleViewClose}
      //     />
      //   ),
      // },
  	];

    this.loadActionColumns();
	this.setState({ columns: this.columns });
  }

}

export default CustomerReviewTable;
