import React from "react";
import Modal from 'react-bootstrap/Modal';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import userDefault from "../../images/user_default.png";
import {
  faAngleLeft,
  faAlignLeft,
  faAlignRight,
  faAngleRight,
} from "@fortawesome/free-solid-svg-icons";
import { Navbar, Button, Nav, NavDropdown, Row, Col } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import classNames from "classnames";
import { loadTranslations as loadLanguageTranslations } from "../../components/common/authenticationService";

class AdminPanelNavBar extends React.Component {
  constructor(props) {
    super(props);
  }
  state = {
    currentLanguage: "en",
    isEnglishActivated: true,
    isBanglaActivated: false,
    userPermissions: [],
    loginUserFirstName: "Arifur",
    loginUserLastName: "Rahman",
    lanEng: "en",
    lanBn: "bn",
    translations: {
      // dashboardMenuLabel: "Dashboard",
      cancelLabel: "Cancel",
      profileMenuLabel: "Profile",
      logoutLabel: "Logout",
      englishLangLabel: "English",
      banglaLangLabel: "Bangla",
      arabicLangLabel: "Arabic",
      confirmLogoutHeaderLabel: "Confirm Logut",
      logoutConfirmLabel: "Are you sure you want to logout?",
    },
    loginUserUsername: "",
    loginUserEmail: "",
    loginUserDesignation: "",
    showModal: false,
    loginUserProfilePicture: userDefault,
  };

  handleLogoutClick = (e) => {
    e.preventDefault();
    this.setState({ showModal: true });
  };

  handleConfirmLogout = () => {
    this.setState({ showModal: false });
    // Perform the actual logout here, e.g., redirect to logout URL
    window.location.href = '/logout';
  };

  handleCloseModal = () => {
    this.setState({ showModal: false });
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    let activeLanguage = localStorage.getItem("activeLanguage");

    if (activeLanguage === "en") {
      thisContext.setState({
        isEnglishActivated: true,
        isBanglaActivated: false,
      });
      activeLanguage = thisContext.state.translations.englishLangLabel;
    } else if (activeLanguage === "bn") {
      thisContext.setState({
        isEnglishActivated: false,
        isBanglaActivated: true,
      });
      activeLanguage = thisContext.state.translations.banglaLangLabel;
    } else if (activeLanguage === "ar") {
      activeLanguage = thisContext.state.translations.arabicLangLabel;
    }


    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // dashboardMenuLabel: translationsObj["i18n.menu.dashboardMenuLabel"]
          //   ? translationsObj["i18n.menu.dashboardMenuLabel"]
          //   : thisContext.state.translations.dashboardMenuLabel,
          logoutLabel: translationsObj["i18n.menu.logoutMenuLabel"]
            ? translationsObj["i18n.menu.logoutMenuLabel"]
            : thisContext.state.translations.logoutLabel,

          profileMenuLabel: translationsObj["i18n.menu.profileMenuLabel"]
            ? translationsObj["i18n.menu.profileMenuLabel"]
            : thisContext.state.translations.profileMenuLabel,

          englishLangLabel: translationsObj["i18n.menu.englishLangLabel"]
            ? translationsObj["i18n.menu.englishLangLabel"]
            : thisContext.state.translations.englishLangLabel,

          banglaLangLabel: translationsObj["i18n.menu.banglaLangLabel"]
            ? translationsObj["i18n.menu.banglaLangLabel"]
            : thisContext.state.translations.banglaLangLabel,

          arabicLangLabel: translationsObj["i18n.menu.arabicLangLabel"]
            ? translationsObj["i18n.menu.arabicLangLabel"]
            : thisContext.state.translations.arabicLangLabel,

          cancelLabel: translationsObj["i18n.common.cancelLabel"]
            ? translationsObj["i18n.common.cancelLabel"]
            : thisContext.state.translations.cancelLabel,

          confirmLogoutHeaderLabel: translationsObj["i18n.common.confirmLogoutHeaderLabel"]
            ? translationsObj["i18n.common.confirmLogoutHeaderLabel"]
            : thisContext.state.translations.confirmLogoutHeaderLabel,

          logoutConfirmLabel: translationsObj["i18n.common.logoutConfirmLabel"]
            ? translationsObj["i18n.common.logoutConfirmLabel"]
            : thisContext.state.translations.logoutConfirmLabel,
        },
        currentLanguage: activeLanguage,
      });

      // console.log("AdminPanelNavBar :: loadTranslations");
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    const thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });

    let user = localStorage.getItem("user");
    if (user !== null && user !== undefined) {
      let userObj = JSON.parse(user);
      let profilePictureValue = (userObj.profilePicture && userObj.profilePicture !== "") ? userObj.profilePicture : userDefault;
      thisContext.setState({
        loginUserUsername: userObj.username,
        loginUserEmail: userObj.email,
        loginUserFirstName: userObj.firstName,
        loginUserLastName: userObj.lastName,
        loginUserProfilePicture: profilePictureValue,
        loginUserDesignation: userObj.designation
      });
    }

    await thisContext.loadTranslations();
  }

  reloadCurrentComponent = (lang) => {
    let thisContext = this;
    // Set a timeout to unmount and then mount the component again
    setTimeout(() => {
      //this.forceUpdate(); // Force re-mounting
      loadLanguageTranslations(lang).then(function () {
        const englishActive = lang === 'en';
        const banglaActive = lang === 'bn';

        thisContext.props.reloadSiblingComponents(lang);
        thisContext.setState({
          isLoading: false,
          currentLanguage: lang,
          isEnglishActivated: englishActive,
          isBanglaActivated: banglaActive
        });

        thisContext.loadTranslations();
        // thisContext.props.history.push("/admin/dashboard");
      });
    });
  };




  render() {
    const {
      isOpen,
      isRtl,
    } = this.props;

    const {
      isBanglaActivated, isEnglishActivated,
    } = this.state;


    return (
      <Navbar
        bg="light"
        // className="navbar shadow-sm p-3 mb-3 bg-white rounded"
        className="navbar shadow-sm mb-3 bg-white rounded"
        sticky="top"
        expand="md"
      >
        {!isRtl && (
          <>
            {isOpen && (
              <>
                <Button variant="outline-info" onClick={this.props.toggle}>
                  <FontAwesomeIcon icon={faAngleLeft} />
                </Button>
              </>
            )}
            {!isOpen && (
              <>
                <Button variant="outline-info" onClick={this.props.toggle}>
                  <FontAwesomeIcon icon={faAngleRight} />
                </Button>
              </>
            )}
            {/* <Button variant="outline-info" onClick={this.props.toggleRtl}>
              <FontAwesomeIcon icon={faAlignRight} />
            </Button> */}
          </>
        )}
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />

        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav
            className={classNames(
              { "ml-left": this.props.isRtl },
              { "ml-auto": !this.props.isRtl }
            )}
          >
            {/* <Nav.Link as={NavLink} to="/admin" exact activeClassName="active">
              Home
            </Nav.Link> */}
            <Nav.Link as={NavLink} to="/admin" exact activeClassName="active">
              {this.state.translations.dashboardMenuLabel}
            </Nav.Link>

            <div className="custom-btn-group btn-group-toggle btn-sm mr-6" data-toggle="buttons">
              <label className={classNames("btn btn-sm form-control btn-outline-success", { active: isEnglishActivated })}>
                <input type="radio" name="options" id="option1" checked={isEnglishActivated}
                  onChange={() => {
                    this.reloadCurrentComponent("en");
                    this.setState({
                      currentLanguage: this.state.translations.englishLangLabel,
                    });
                  }}
                /> English
              </label>
              <label className={classNames("btn btn-sm form-control btn-outline-success", { active: isBanglaActivated })}>
                <input type="radio" name="options" id="option2" checked={isBanglaActivated}
                  onChange={() => {
                    this.reloadCurrentComponent("bn");
                    this.setState({
                      currentLanguage: this.state.translations.banglaLangLabel,
                    });
                  }}
                /> 
              </label>
            </div>

            <NavDropdown
              title={
                <div className="nav-item dropdown navbar-profile mt-0">
                  <div className="user-profile d-flex align-items-center">
                    <div className="image-container rounded-circle overflow-hidden me-2">
                      <img src={this.state.loginUserProfilePicture} alt="Profile" height={"32px"} width={"32px"} />
                    </div>
                    <div>

                      <p className="mb-0">
                        {this.state.loginUserFirstName} {this.state.loginUserLastName}
                        {/* - {this.state.loginUserUsername} */}
                      </p>

                      <p className="mb-0">{this.state.loginUserDesignation}</p>
                    </div>
                  </div>
                </div>
              }
              as="div"
              id="collasible-nav-dropdown"
              style={{ cursor: "pointer", display: "inline-block" }}
              className="custom-dropdown-toggle"
            >
              <NavDropdown.Item
                as={NavLink}
                to="/profile"
              >
                {this.state.translations.profileMenuLabel}
              </NavDropdown.Item>

              <NavDropdown.Divider />
              <NavDropdown.Item
                as={NavLink}
                to="/admin/logout"
              >
                <Nav.Link onClick={this.handleLogoutClick} style={{ color: "red" }}>
                  {/* <button className="btn btn-sm btn-outline-danger">{this.state.translations.logoutMenuLabel}</button> */}
                  {this.state.translations.logoutLabel}
                </Nav.Link>
              </NavDropdown.Item>
            </NavDropdown>



            <Modal show={this.state.showModal} onHide={this.handleCloseModal}>
              <Modal.Header closeButton>
                <Modal.Title>{this.state.translations.confirmLogoutHeaderLabel}</Modal.Title>
              </Modal.Header>
              <Modal.Body>{this.state.translations.logoutConfirmLabel}</Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={this.handleCloseModal}>
                  {this.state.translations.cancelLabel}
                </Button>
                <Button variant="primary" onClick={this.handleConfirmLogout}>
                  {this.state.translations.logoutLabel}
                </Button>
              </Modal.Footer>
            </Modal>

          </Nav>
        </Navbar.Collapse>
        {isRtl && (
          <>
            <Button variant="outline-info" onClick={this.props.toggleRtl}>
              <FontAwesomeIcon icon={faAlignLeft} />
            </Button>
            {!isOpen && (
              <>
                <Button variant="outline-info" onClick={this.props.toggle}>
                  <FontAwesomeIcon icon={faAngleLeft} />
                </Button>
              </>
            )}
            {isOpen && (
              <>
                <Button variant="outline-info" onClick={this.props.toggle}>
                  <FontAwesomeIcon icon={faAngleRight} />
                </Button>
              </>
            )}
          </>
        )}
      </Navbar>
    );
  }
}

export default AdminPanelNavBar;
