import React from "react";
import classNames from "classnames";
import { Container } from "react-bootstrap";
import AdminPanelNavBar from "./AdminPanelNavBar";
import { Redirect } from "react-router-dom";
import { checkAuthentication } from "../../components/common/authenticationService";
import AlertModal from "../../components/public/includes/AlertModal";

class AdminPanelContent extends React.Component {
  state = {
    isLoggedIn: "false",
    loginUserEmail: "",
    loginUserFirstName: "",
    loginUserLastName: "",
    isAdmin: false,
    isSupplier: false,
    alertShow: false,
    alertMessage: "",
    alertDetailsMessage: "",
    isCustomer: false,
    redirect: "",
  };

  componentDidMount() {
    // alert('admin panel loaded.');
    const thisContext = this;
    // const { data: customer } = await getAll();
    // thisContext.setState({ customer });
    checkAuthentication()
      .then(function (result) {
        // let authorities = localStorage.getItem("authorities");
        let user = localStorage.getItem("user");
        if (result && user !== null && user !== undefined) {
          let userObj = JSON.parse(user);
          thisContext.setState({
            loginUserEmail: userObj.email,
            loginUserFirstName: userObj.firstName,
            loginUserLastName: userObj.lastName,
          });
          if (
            userObj.membershipType !== null &&
            userObj.membershipType !== undefined
          ) {
            if (
              userObj.membershipType === "SUPERADMIN" ||
              userObj.membershipType === "ADMIN"
            ) {
              thisContext.setState({ isAdmin: true, isUser: true });
            } else if (userObj.membershipType === "USER") {
              thisContext.setState({ isAdmin: true, isUser: true });
            } else {
              thisContext.setState({ redirect: "/" });
            }
          } else {
            thisContext.setState({ redirect: "/" });
          }
          thisContext.setState({
            isLoggedIn: "true",
          });
        } else {
          thisContext.setState({ redirect: "/" });
        }
        // console.log(JSON.parse(authorities));
      })
      .catch((err) => {
        thisContext.setState({
          alertShow: true,
          alertMessage: err,
          alertDetailsMessage: "Please Reload The Page",
        });
      });
  }
  render() {
    const { children } = this.props;
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }
    const stateValues = this.props;

    return (
      <Container
        fluid
        // className={classNames("content", { "is-open": this.props.isOpen })}
        className={classNames("content", { "is-open": this.props.isOpen })}
        isloggedin={this.state.isLoggedIn}
        loginuseremail={this.state.loginUserEmail}
        loginuserfirstname={this.state.loginUserFirstName}
        loginuserlastname={this.state.loginUserLastName}
      >
        <AdminPanelNavBar
          isloggedin={this.state.isLoggedIn}
          loginuseremail={this.state.loginUserEmail}
          loginuserfirstname={this.state.loginUserFirstName}
          loginuserlastname={this.state.loginUserLastName}
          toggle={this.props.toggle}
          toggleRtl={this.props.toggleRtl}
          isOpen={this.props.isOpen}
          isRtl={this.props.isRtl}
          reloadSiblingComponents = {this.props.reloadSiblingComponents}
        />
        {/* {children} */}

        {React.Children.map(children, (child) =>
            React.cloneElement(child, { stateValues })
        )}
       
        <AlertModal
          title={"Error Occured !"}
          message={this.state.alertMessage}
          alertDetailsMessage={this.state.alertDetailsMessage}
          show={this.state.alertShow}
          onClick={(e) => this.reloadPage()}
          onHide={(e) => this.hideAlertModal()}
          onClose={(e) => this.reloadPage()}
        />
      </Container>
    );
  }
}

export default AdminPanelContent;