import http from "../../common/httpService";
import config from "../../../config.json";
// import axios from "axios";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/userRequests";
const options = {
  headers: { Authorization: "Bearer " + localStorage.getItem("token") },
};
function userRequestUrl(id) {
  return `${apiEndpoint}/${id}`;
}

export function getAll(page = 0, size = 5) {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}

export function get(userRequestId) {
  return http.get(userRequestUrl(userRequestId), options);
}

export function save(datObj) {
  return datObj.name !== null ? update(datObj) : create(datObj);
}

export function create(userRequest) {
  if (userRequest.id !== undefined && userRequest.id !== 999) {
    const body = { ...userRequest };
    return http.put(apiEndpoint, body, options);
  }
  userRequest.id = null;
  return http.post(apiEndpoint, userRequest, options);
}

export function update(userRequest) {
  if (userRequest.id !== undefined && userRequest.id !== 999) {
    const body = { ...userRequest };
    return http.put(apiEndpoint, body, options);
  }
  return http.put(apiEndpoint, userRequest, options);
}

export function remove(userRequestId) {
  return http.delete(userRequestUrl(userRequestId), options);
}
