import React from "react";
import UxCrudForm from "../../common/UxCrudForm";
import { save, get } from "./userRequestService";

import { Container, Row, Col, Card } from "react-bootstrap";
// import { NavLink } from "react-router-dom";

class UserRequestForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["USER_REQUEST_CREATE", "USER_REQUEST_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/userRequests/",
    userPermissions: [],
    data: {
      id: "",
      username: "",
      email: "",
      password: "",
      firstName: "",
      lastName: "",
      requestedRole: "",
      groupId: "1",
    },
    commonValidationMessage: "",
    translations: {
      titleLabel: "User Request",
      homeLabel: "Home",
      cancelLabel: "Cancel",
      saveLabel: "Save",
      homeLabel: "Home",
      emailLabel: "Email",
      emailPlaceholder: "Enter Email",
      passwordLabel: "Password",
      passwordPlaceholder: "Enter Password",
      firstNameLabel: "First Name",
      firstNamePlaceholder: "Enter First Name",
      lastNameLabel: "Last Name",
      lastNamePlaceholder: "Enter Last Name",
      requestedRoleLabel: "Requested Role",
      requestedRolePlaceholder: "Enter Requested Role",
    },
    errors: {},
    validationMessage: {
      email: "",
      password: "",
      firstName: "",
      lastName: "",
      requestedRole: "",
    },
    message: "",
  };

  // schema = {
  //   id: Joi.number().allow(null),
  //   username: Joi.string().label("Username"),
  //   email: Joi.string().label("Email"),
  //   password: Joi.string().label("Password"),
  //   firstName: Joi.string().required().label("First Name"),
  //   lastName: Joi.string().required().label("Last Name"),
  //   requestedRole: Joi.string().required().label("Requested Role"),
  //   groupId: Joi.number().required().label("Company Id"),
  // };
  async populateData() {
    try {
      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
        this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        this.setState({
          data: {
            username: "",
            email: "",
            password: "",
            firstName: "",
            lastName: "",
            requestedRole: "",
            groupId: 1,
          },
        });

        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    thisContext.setState({ isLoading: true });
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
          saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
          homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,

          titleLabel: translationsObj["i18n.userRequest.titleLabel"]
            ? translationsObj["i18n.userRequest.titleLabel"]
            : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          emailLabel: translationsObj["i18n.userRequest.email.label"]
            ? translationsObj["i18n.userRequest.email.label"]
            : thisContext.state.translations.emailLabel,
          emailPlaceholder: translationsObj["i18n.userRequest.email.placeholder"]
            ? translationsObj["i18n.userRequest.email.placeholder"]
            : thisContext.state.translations.emailPlaceholder,

          passwordLabel: translationsObj["i18n.userRequest.password.label"]
            ? translationsObj["i18n.userRequest.password.label"]
            : thisContext.state.translations.passwordLabel,
          passwordPlaceholder: translationsObj["i18n.userRequest.password.placeholder"]
            ? translationsObj["i18n.userRequest.password.placeholder"]
            : thisContext.state.translations.passwordPlaceholder,


          firstNameLabel: translationsObj["i18n.userRequest.firstName.label"]
            ? translationsObj["i18n.userRequest.firstName.label"]
            : thisContext.state.translations.firstNameLabel,
          firstNamePlaceholder: translationsObj[
            "i18n.userRequest.firstName.placeholder"
          ]
            ? translationsObj["i18n.userRequest.firstName.placeholder"]
            : thisContext.state.translations.firstNamePlaceholder,

          lastNameLabel: translationsObj["i18n.userRequest.lastName.label"]
            ? translationsObj["i18n.userRequest.lastName.label"]
            : thisContext.state.translations.lastNameLabel,
          lastNamePlaceholder: translationsObj[
            "i18n.userRequest.lastName.placeholder"
          ]
            ? translationsObj["i18n.userRequest.lastName.placeholder"]
            : thisContext.state.translations.lastNamePlaceholder,

          requestedRoleLabel: translationsObj["i18n.userRequest.requestedRole.label"]
            ? translationsObj["i18n.userRequest.requestedRole.label"]
            : thisContext.state.translations.requestedRoleLabel,
          requestedRolePlaceholder: translationsObj[
            "i18n.userRequest.requestedRole.placeholder"
          ]
            ? translationsObj["i18n.userRequest.requestedRole.placeholder"]
            : thisContext.state.translations.requestedRolePlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      email: obj.email,
      password: obj.password,
      firstName: obj.firstName,
      lastName: obj.lastName,
      requestedRole: obj.requestedRole,
      groupId: 1,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status === "FAILED" ||
        response.data.validationMessages !== undefined
      ) {
        if (response.data.validationMessages !== undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message !== undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    const thisContext = this;
    const { data } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="pt-2 pb-2">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black mr-3 mb-3">
                  <Card.Title className="card-form-title-light">
                    <Row>
                      <Col md='1'>
                        {
                          this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                            e.preventDefault();
                            thisContext.props.history.push(thisContext.state.basicUrl);
                          })
                        }
                      </Col>
                      <Col md='11'>
                        <h4 className="text-right">{this.state.translations.titleLabel}</h4>
                      </Col>
                    </Row>
                  </Card.Title>
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderInput(false, false, "id", "Id", "hidden")}
                      {this.renderInput(
                        true,
                        true,
                        "email",
                        this.state.translations.emailLabel,
                        this.state.translations.emailPlaceholder,
                        "email",
                        (data.id !== null),
                        this.state.validationMessage.email
                      )}

                      {this.renderInput(
                        true,
                        true,
                        "password",
                        this.state.translations.passwordLabel,
                        this.state.translations.passwordPlaceholder,
                        "password",
                        (data.id !== null),
                        this.state.validationMessage.pasword
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "firstName",
                        this.state.translations.firstNameLabel,
                        this.state.translations.firstNamePlaceholder,
                        "text",
                        (data.id !== null),
                        this.state.validationMessage.firstName
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "lastName",
                        this.state.translations.lastNameLabel,
                        this.state.translations.lastNamePlaceholder,
                        "text",
                        (data.id !== null),
                        this.state.validationMessage.email
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "requestedRole",
                        this.state.translations.requestedRoleLabel,
                        this.state.translations.requestedRolePlaceholder,
                        "text",
                        (data.id !== null),
                        this.state.validationMessage.requestedRole
                      )}
                    </div>
                  </Card.Body>
                  {thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default UserRequestForm;
