import React, { useState, useEffect } from "react";
import FileBase64 from 'react-file-base64';


const UxCrudImage = ({
  isRequired,
  name,
  label,
  placeholder,
  validationMessage,
  value,
  error,
  updateSelected,
  ...rest
}) => {
  const requiredHtml = isRequired ? (
    <span style={{ color: "red" }}>*</span>
  ) : (
    ""
  );
  const customFontStyle = {
    fontFamily: "Diodrum Arabic Semibold, sans-serif",
    fontWeight: "5",
  };

  const [selectedValue, setSelectedValue] = useState('');
  const [inputValidationMessage, setInputValidationMessage] = useState('');

  useEffect(() => {
    setSelectedValue(value);
  }, [value]);

  useEffect(() => {
    setInputValidationMessage(validationMessage);
  }, [validationMessage]);

  const handleChange = (selectedImageBase64) => {
    setSelectedValue(selectedImageBase64);
    updateSelected(name, selectedImageBase64);
    setInputValidationMessage('');
  };

  const imageInputClass = (isRequired && inputValidationMessage && inputValidationMessage != '')
    ? 'col-md-12 border border-danger' : 'col-md-12';

  return (
    <div className="form-group">
      <b><label htmlFor={name}>{label}</label></b>
      &nbsp;{requiredHtml}
      <div className={imageInputClass}>
        <FileBase64
          multiple={false}
          onDone={(file) => {
            handleChange(file.base64);
          }
          } />
        <img width={'120px'} height={'120px'} src={selectedValue} alt={label} />
      </div>
      {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
    </div>
  );
};

export default UxCrudImage;
