import React from "react";
import UxCrudForm from "../../common/UxCrudForm";
import { save, get } from "./userService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
// import FileBase64 from "react-file-base64";
import { getAll as getAllAuthority } from "../authority/authorityService";

class UserForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["USER_CREATE", "USER_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/users/",
    userPermissions: [],
    authorityOptions: [],
    data: {
      id: null,
      username: "",
      password: "",
      firstName: "",
      lastName: "",
      designation: "",
      email: "",
      mobileNo: "",
      activated: "true",
      authorities: [],
      membershipId: "1",
      membershipType: "USER",
      lastLoginDate: "",
      companyId: "1",
      activeLanguage: this.props.stateValues.activeLanguage,
    },
    errors: {},
    validationMessage: {
      username: "",
      profilePicture: "",
      password: "",
      firstName: "",
      lastName: "",
      designation: "",
      email: "",
      mobileNo: "",
      authorities: "",
      membershipId: "",
      membershipType: "",
      companyId: "",
      activeLanguage: "",
    },
    commonValidationMessage: "",
    translations: {
      // orgNameLabel: "Organization Name",
      titleLabel: "User",
      homeLabel: "Home",
      cancelLabel: "Cancel",
      saveLabel: "Save",
      usernameLabel: "Username",
      usernamePlaceholder: "Enter Username",
      profilePictureLabel: "Profile Picture",
      profilePicturePlaceholder: "Select Profile Picture",
      passwordLabel: "Password",
      passwordPlaceholder: "Enter Password",
      firstNameLabel: "First Name",
      firstNamePlaceholder: "Enter First Name",
      lastNameLabel: "Last Name",
      lastNamePlaceholder: "Enter Last Name",
      designationLabel: "Designation",
      designationPlaceholder: "Enter Designation",
      emailLabel: "Email",
      emailPlaceholder: "Enter Email",
      mobileNoLabel: "Mobile No",
      mobileNoPlaceholder: "Enter Mobile No",
      activatedLabel: "Activated ?",
      activatedPlaceholder: "Enter Activated ?",
      authoritiesLabel: "Authorities",
      authoritiesPlaceholder: "Select Authorities",
      membershipIdLabel: "Membership Id",
      membershipIdPlaceholder: "Enter Membership Id",
      membershipTypeLabel: "Membership Type",
      membershipTypePlaceholder: "Enter Membership Type",
      lastLoginDateLabel: "Last Login Date",
      lastLoginDatePlaceholder: "Enter Last Login Date",
      companyIdLabel: "Company",
      companyIdPlaceholder: "Enter Company",
      activeLanguageLabel: "Active Language",
      activeLanguagePlaceholder: "Enter Active Language",
    },
  };

  /* schema = {
    id: Joi.number().allow(null),
    username: Joi.string().required().label({this.state.translations.usernameLabel}),password: Joi.string().required().label({this.state.translations.passwordLabel}),firstName: Joi.string().required().label({this.state.translations.firstNameLabel}),lastName: Joi.string().required().label({this.state.translations.lastNameLabel}),email: Joi.string().required().label({this.state.translations.emailLabel}),activated: Joi.string().label({this.state.translations.activatedLabel}),authority: Joi.string().label("Authority"),membershipId: Joi.string().required().label({this.state.translations.membershipIdLabel}),membershipType: Joi.string().required().label({this.state.translations.membershipTypeLabel}),lastLoginDate: Joi.string().label({this.state.translations.lastLoginDateLabel}),companyId: Joi.string().required().label({this.state.translations.companyIdLabel}),activeLanguage: Joi.string().required().label({this.state.translations.activeLanguageLabel}),
  };
*/
  async populateData() {
    try {
      const thisContext = this;
      getAllAuthority().then(function (result) {
        let authorityOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          authorityOptions.push({
            value: result.data[i].name,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ authorityOptions: authorityOptions });
      });
      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
        this.props.location.pathname.split("/").length - 1
        ];
      if (!(id === undefined || id === "new")) {
        const { data: obj } = await get(id);
        // // console.log(obj);
        this.setState({ data: this.mapToViewModel(obj) });
      }
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj["i18n.user.titleLabel"]
            ? translationsObj["i18n.user.titleLabel"]
            : thisContext.state.translations.titleLabel,

          homeLabel: translationsObj["i18n.user.homeLabel"]
            ? translationsObj["i18n.user.homeLabel"]
            : thisContext.state.translations.homeLabel,

          saveLabel: translationsObj["i18n.user.saveLabel"]
            ? translationsObj["i18n.user.saveLabel"]
            : thisContext.state.translations.saveLabel,

          cancelLabel: translationsObj["i18n.user.cancelLabel"]
            ? translationsObj["i18n.user.cancelLabel"]
            : thisContext.state.translations.cancelLabel,

            usernameLabel: translationsObj["i18n.user.username.label"]
            ? translationsObj["i18n.user.username.label"]
            : thisContext.state.translations.usernameLabel,
            usernamePlaceholder: translationsObj["i18n.user.username.placeholder"]
              ? translationsObj["i18n.user.username.placeholder"]
              : thisContext.state.translations.usernamePlaceholder,

            profilePictureLabel: translationsObj["i18n.user.profilePicture.label"]
              ? translationsObj["i18n.user.profilePicture.label"]
              : thisContext.state.translations.profilePictureLabel,

          profilePicturePlaceholder: translationsObj["i18n.user.profilePicture.placeholder"]
            ? translationsObj["i18n.user.profilePicture.placeholder"]
            : thisContext.state.translations.profilePicturePlaceholder,

          passwordLabel: translationsObj["i18n.user.password.label"]
            ? translationsObj["i18n.user.password.label"]
            : thisContext.state.translations.passwordLabel,
          passwordPlaceholder: translationsObj["i18n.user.password.placeholder"]
            ? translationsObj["i18n.user.password.placeholder"]
            : thisContext.state.translations.passwordPlaceholder,
          firstNameLabel: translationsObj["i18n.user.firstName.label"]
            ? translationsObj["i18n.user.firstName.label"]
            : thisContext.state.translations.firstNameLabel,
          firstNamePlaceholder: translationsObj[
            "i18n.user.firstName.placeholder"
          ]
            ? translationsObj["i18n.user.firstName.placeholder"]
            : thisContext.state.translations.firstNamePlaceholder,
          lastNameLabel: translationsObj["i18n.user.lastName.label"]
            ? translationsObj["i18n.user.lastName.label"]
            : thisContext.state.translations.lastNameLabel,
          lastNamePlaceholder: translationsObj["i18n.user.lastName.placeholder"]
            ? translationsObj["i18n.user.lastName.placeholder"]
            : thisContext.state.translations.lastNamePlaceholder,

          designationLabel: translationsObj["i18n.user.designation.label"]
            ? translationsObj["i18n.user.designation.label"]
            : thisContext.state.translations.designationLabel,
          designationPlaceholder: translationsObj[
            "i18n.user.designation.placeholder"
          ]
            ? translationsObj["i18n.user.designation.placeholder"]
            : thisContext.state.translations.designationPlaceholder,

          emailLabel: translationsObj["i18n.user.email.label"]
            ? translationsObj["i18n.user.email.label"]
            : thisContext.state.translations.emailLabel,
          emailPlaceholder: translationsObj["i18n.user.email.placeholder"]
            ? translationsObj["i18n.user.email.placeholder"]
            : thisContext.state.translations.emailPlaceholder,

          mobileNoLabel: translationsObj["i18n.user.mobileNo.label"]
            ? translationsObj["i18n.user.mobileNo.label"]
            : thisContext.state.translations.mobileNoLabel,
          mobileNoPlaceholder: translationsObj["i18n.user.mobileNo.placeholder"]
            ? translationsObj["i18n.user.mobileNo.placeholder"]
            : thisContext.state.translations.mobileNoPlaceholder,

          activatedLabel: translationsObj["i18n.user.activated.label"]
            ? translationsObj["i18n.user.activated.label"]
            : thisContext.state.translations.activatedLabel,
          activatedPlaceholder: translationsObj[
            "i18n.user.activated.placeholder"
          ]
            ? translationsObj["i18n.user.activated.placeholder"]
            : thisContext.state.translations.activatedPlaceholder,
          authoritiesLabel: translationsObj["i18n.user.authorities.label"]
            ? translationsObj["i18n.user.authorities.label"]
            : thisContext.state.translations.authoritiesLabel,
          authoritiesPlaceholder: translationsObj[
            "i18n.user.authorities.placeholder"
          ]
            ? translationsObj["i18n.user.authorities.placeholder"]
            : thisContext.state.translations.authoritiesPlaceholder,
          membershipIdLabel: translationsObj["i18n.user.membershipId.label"]
            ? translationsObj["i18n.user.membershipId.label"]
            : thisContext.state.translations.membershipIdLabel,
          membershipIdPlaceholder: translationsObj[
            "i18n.user.membershipId.placeholder"
          ]
            ? translationsObj["i18n.user.membershipId.placeholder"]
            : thisContext.state.translations.membershipIdPlaceholder,
          membershipTypeLabel: translationsObj["i18n.user.membershipType.label"]
            ? translationsObj["i18n.user.membershipType.label"]
            : thisContext.state.translations.membershipTypeLabel,
          membershipTypePlaceholder: translationsObj[
            "i18n.user.membershipType.placeholder"
          ]
            ? translationsObj["i18n.user.membershipType.placeholder"]
            : thisContext.state.translations.membershipTypePlaceholder,
          lastLoginDateLabel: translationsObj["i18n.user.lastLoginDate.label"]
            ? translationsObj["i18n.user.lastLoginDate.label"]
            : thisContext.state.translations.lastLoginDateLabel,
          lastLoginDatePlaceholder: translationsObj[
            "i18n.user.lastLoginDate.placeholder"
          ]
            ? translationsObj["i18n.user.lastLoginDate.placeholder"]
            : thisContext.state.translations.lastLoginDatePlaceholder,
          companyIdLabel: translationsObj["i18n.user.companyId.label"]
            ? translationsObj["i18n.user.companyId.label"]
            : thisContext.state.translations.companyIdLabel,
          companyIdPlaceholder: translationsObj[
            "i18n.user.companyId.placeholder"
          ]
            ? translationsObj["i18n.user.companyId.placeholder"]
            : thisContext.state.translations.companyIdPlaceholder,
          activeLanguageLabel: translationsObj["i18n.user.activeLanguage.label"]
            ? translationsObj["i18n.user.activeLanguage.label"]
            : thisContext.state.translations.activeLanguageLabel,
          activeLanguagePlaceholder: translationsObj[
            "i18n.user.activeLanguage.placeholder"
          ]
            ? translationsObj["i18n.user.activeLanguage.placeholder"]
            : thisContext.state.translations.activeLanguagePlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(
        thisContext.state.userPermissions.includes("USER_CREATE") ||
        thisContext.state.userPermissions.includes("USER_UPDATE")
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      thisContext.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false });
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      username: obj.username,
      password: "",
      profilePicture: obj.profilePicture,
      firstName: obj.firstName,
      lastName: obj.lastName,
      designation: obj.designation,
      email: obj.email,
      mobileNo: obj.mobileNo,
      activated: obj.activated,
      authorities: [],
      requestedAuthorities: obj.requestedAuthorities,
      membershipId: obj.membershipId,
      membershipType: obj.membershipType,
      lastLoginDate: obj.lastLoginDate,
      companyId: obj.companyId,
      activeLanguage: obj.activeLanguage,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status === "FAILED" ||
        response.data.validationMessages !== undefined
      ) {
        if (response.data.validationMessages !== undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message !== undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="pt-2 pb-2">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black mr-3 mb-3">
                  <Card.Title className="card-form-title-light">
                    <Row>
                      <Col md='1'>
                        {
                          this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                            e.preventDefault();
                            thisContext.props.history.push(thisContext.state.basicUrl);
                          })
                        }
                      </Col>
                      <Col md='11'>
                        <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                      </Col>
                    </Row>
                  </Card.Title>
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}

                    {this.renderInput(false, false, "id", "Id", "hidden")}
                    <Row>
                      <Col>
                        {this.renderImageInput(true, "profilePicture", this.state.translations.profilePictureLabel, this.state.translations.profilePicturePlaceholder, this.state.validationMessage.profilePicture)}
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "username",
                          this.state.translations.usernameLabel,
                          this.state.translations.usernamePlaceholder,
                          "text",
                          data.id !== null,
                          this.state.validationMessage.username
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "password",
                          this.state.translations.passwordLabel,
                          this.state.translations.passwordPlaceholder,
                          "password",
                          false,
                          this.state.validationMessage.password
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "password",
                          "Confirm Password",
                          "Confirm Password",
                          "password",
                          false,
                          this.state.validationMessage.password
                        )}
                      </Col>
                    </Row>

                    <Row>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "firstName",
                          this.state.translations.firstNameLabel,
                          this.state.translations.firstNamePlaceholder,
                          "text",
                          false,
                          this.state.validationMessage.firstName
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "lastName",
                          this.state.translations.lastNameLabel,
                          this.state.translations.lastNamePlaceholder,
                          "text",
                          false,
                          this.state.validationMessage.lastName
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "designation",
                          this.state.translations.designationLabel,
                          this.state.translations.designationPlaceholder,
                          "text",
                          false,
                          this.state.validationMessage.designation
                        )}
                      </Col>
                    </Row>

                    <Row>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "email",
                          this.state.translations.emailLabel,
                          this.state.translations.emailPlaceholder,
                          "text",
                          false,
                          this.state.validationMessage.email
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "mobileNo",
                          this.state.translations.mobileNoLabel,
                          this.state.translations.mobileNoPlaceholder,
                          "text",
                          false,
                          this.state.validationMessage.mobileNo
                        )}
                      </Col>
                      <Col>
                        {this.renderSelect(
                          true,
                          true,
                          "requestedAuthorities",
                          this.state.translations.authoritiesLabel,
                          this.state.translations.authoritiesPlaceholder,
                          this.state.authorityOptions,
                          this.state.validationMessage.authorities
                        )}
                      </Col>
                    </Row>


                    {/* {this.renderInput("dueDate", "Due Date")} */}
                  </Card.Body>
                  {thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default UserForm;
