import http from "../../common/httpService";
import config from "../../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/permissionGroups";

function permissionGroupUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}
export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(permissionGroupId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(permissionGroupUrl(permissionGroupId), options);
}


export function save(datObj) {
	return datObj.id !== null ? update(datObj) : create(datObj);
}

export function create(permissionGroup) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, permissionGroup, options);
}

export function update(permissionGroup) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, permissionGroup, options);
}

export function remove(permissionGroupId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(permissionGroupUrl(permissionGroupId), options);
}

