import React, { Component, useState } from "react";
// import { Row, Col } from "react-bootstrap";
// import Container from "react-bootstrap/Container";
import { Route } from "react-router-dom";

// import AdminPanelSideBar from "../components/sidebar/AdminPanelSideBar";
// import AdminPanelContent from "../components/content/AdminPanelContent";
import properties from "../../common_properties.json";
// import PublicFooter from "../components/public/includes/PublicFooter";
import AdminLayout from "./AdminPanelLayout";

const AdminPanelLayoutRoute = ({ component: Component, ...rest }) => {
  const activeLayout = localStorage.getItem("activeLayout") || 'LTR';
  const activeLanguage = localStorage.getItem("activeLanguage") || "en";
  const isRtl = activeLayout != undefined ? activeLayout == "RTL" : false;
  return (
    <Route
      render={(matchProps) => (
        <AdminLayout
          activeLanguage = {activeLanguage}
          isOpen={false}
          isRtl={isRtl}
          labels={properties.labels.languages.en}
        >
          <Component {...matchProps} />
        </AdminLayout>
      )}
    />
  );
};

export default AdminPanelLayoutRoute;
