// src/components/bootstrap-carousel.component.js
import React, { Component } from "react";

import { Modal, Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

class AlertModal extends Component {
  render() {
    return (
      <div>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
        >
          <Modal.Header closeButton>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <h1>{this.props.message}</h1>

            {/* <ul hidden={true}>
              {this.props.errors.map((error) => (
                <li key={error.id}>
                  {error.description}
                </li>
              ))}
            </ul> */}
            <p>
              {this.props.alertDetailsMessage}
            </p>
          </Modal.Body>

          <Modal.Footer>
            {/* <Button
              variant="secondary"
              onClick={() => this.props.onClick({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
            <Button
              variant="primary"
              onClick={() => this.props.onClick({ msg: "Modal Submitted!" })}
            >
              Submit
            </Button> */}
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default AlertModal;
