import React from "react";
import classNames from "classnames";
import { Container, Row } from "react-bootstrap";
import AlertModal from '../../components/public/includes/AlertModal';

class PublicWebsiteContent extends React.Component {
  state = {
    isLoggedIn: true,
    loginUserEmail: 'Hello',
    loginUserFirstName: 'World',
    loginUserLastName: '',
    isSupplier: false,
    isCustomer: false,
    alertShow: false,
    alertMessage: '',
    alertDetailsMessage: '',
    isCustomer: false,
  };
  hideAlertModal() {
    return;
  }
  render() {
    const { children, ...rest } = this.props;
    return (
      <Container
        fluid
        className={classNames("content", { "is-open": this.props.isOpen })}
      >
     
        {children}
        <AlertModal
          title={"Error Occured !"}
          message={this.state.alertMessage}
          alertDetailsMessage={this.state.alertDetailsMessage}
          show={this.state.alertShow}
          onClick={e => this.reloadPage()}
          onHide={e => this.hideAlertModal()}
          onClose={e => this.reloadPage()} />
      </Container>

    );
  }
}

export default PublicWebsiteContent;
