import http from "../../common/httpService";
import config from "../../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/translationss";

function translationsUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}

export function getAllPaginatedAndSearchToken(page = 0, size = 5, searchToken) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size + "&searchToken=" + searchToken, headerOptions);
}

export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(translationsId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(translationsUrl(translationsId), options);
}



export function save(datObj) {
	return datObj.name !== null ? update(datObj) : create(datObj);
}

export function create(translations) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, translations, options);
}

export function update(translations) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, translations, options);
}

export function remove(translationsId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(translationsUrl(translationsId), options);
}

