import React, { Component } from "react";
// import Table from "../common/table";
import { Button, Form, Modal } from 'react-bootstrap';
// import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faPencilAlt,
  faCheck
} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../../common/UxCrudTable";

class TranslationsViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" size="sm" onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form>
              <Form.Group controlId="languageCode">
                <Form.Label>Language Code</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.languageCode} placeholder="Language Code" />
              </Form.Group>
              <Form.Group controlId="groupName">
                <Form.Label>Group Name</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.groupName} placeholder="Group Name" />
              </Form.Group>
              <Form.Group controlId="keyName">
                <Form.Label>Key Name</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.keyName} placeholder="Key Name" />
              </Form.Group>
              <Form.Group controlId="valueText">
                <Form.Label>Value Text</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.valueText} placeholder="Value Text" />
              </Form.Group>
              <Form.Group controlId="description">
                <Form.Label>Description</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.description} placeholder="Description" />
              </Form.Group>


              {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class TranslationsTable extends UxCrudTable {
  state = {
    DATA_CREATE: "TRANSLATIONS_CREATE",
    DATA_READ: "TRANSLATIONS_READ",
    DATA_UPDATE: "TRANSLATIONS_UPDATE",
    DATA_DELETE: "TRANSLATIONS_DELETE",
    DATA_APPROVE: "TRANSLATIONS_APPROVE",
    userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    translations: {
      actionsLabel: "Actions",
      idLabel: "Id", viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      languageCodeLabel: "Language Code",
      groupNameLabel: "Group Name",
      keyNameLabel: "Key Name",
      valueTextLabel: "Value Text",
      englishTextValueLabel: "English Label",
      banglaTextValueLabel: "Bangla Label",
      descriptionLabel: "Description",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          idLabel: translationsObj["i18n.common.idLabel"]
            ? translationsObj["i18n.common.idLabel"]
            : thisContext.state.translations.idLabel,

          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          languageCodeLabel: translationsObj["i18n.translations.languageCode.label"]
            ? translationsObj["i18n.translations.languageCode.label"]
            : thisContext.state.translations.languageCodeLabel,
          groupNameLabel: translationsObj["i18n.translations.groupName.label"]
            ? translationsObj["i18n.translations.groupName.label"]
            : thisContext.state.translations.groupNameLabel,

          keyNameLabel: translationsObj["i18n.translations.keyName.label"]
            ? translationsObj["i18n.translations.keyName.label"]
            : thisContext.state.translations.keyNameLabel,

          valueTextLabel: translationsObj["i18n.translations.valueText.label"]
            ? translationsObj["i18n.translations.valueText.label"]
            : thisContext.state.translations.valueTextLabel,

          englishTextValueLabel: translationsObj["i18n.translations.englishValueText.label"]
            ? translationsObj["i18n.translations.englishValueText.label"]
            : thisContext.state.translations.englishTextValueLabel,

          banglaTextValueLabel: translationsObj["i18n.translations.banglaValueText.label"]
            ? translationsObj["i18n.translations.banglaValueText.label"]
            : thisContext.state.translations.banglaTextValueLabel,

          descriptionLabel: translationsObj["i18n.translations.description.label"]
            ? translationsObj["i18n.translations.description.label"]
            : thisContext.state.translations.descriptionLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  async loadColumns() {
    this.columns = [
      { path: "id", label: this.state.translations.idLabel },
      {
        path: "groupName",
        label: this.state.translations.groupNameLabel,
      }, {
        path: "keyName",
        label: this.state.translations.keyNameLabel,
      }, {
        path: "englishTextValue",
        label: this.state.translations.englishTextValueLabel,
      }, {
        path: "banglaTextValue",
        label: this.state.translations.banglaTextValueLabel,
      },
    ];
    this.loadActionColumns();
    this.setState({ columns: this.columns });
  }

}

export default TranslationsTable;
