import React from "react";
import UxCrudForm from "../../common/UxCrudForm";
import { save, get } from "./authorityService";
import { getAll as getAllPermissionGroup } from "../permissionGroup/permissionGroupService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
// import Breadcrumb from "react-bootstrap/Breadcrumb";
// import { NavLink } from "react-router-dom";
// import FileBase64 from "react-file-base64";

class AuthorityForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["AUTHORITY_CREATE", "AUTHORITY_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/authoritys/",
    userPermissions: [],
    data: { name: "", description: "", isActive: "true", permissionGroups: [] },
    errors: {},
    validationMessage: {
      name: "",
      description: "",
      isActive: "",
      permissionGroups: ""
    },
    commonValidationMessage: "",
    translations: {
      // orgNameLabel: "Organization Name",
      titleLabel: "Authority",
      homeLabel: "Home",
      cancelLabel: "Cancel",
      saveLabel: "Save",
      nameLabel: "Authority Name",
      namePlaceholder: "Enter Authority Name",
      descriptionLabel: "Description",
      descriptionPlaceholder: "Enter Description",
      isActiveLabel: "Is Active",
      isActivePlaceholder: "Enter Is Active",
      permissionGroupLabel: "Permission Group",
      permissionGroupPlaceholder: "Select Permission Groups",
    },
  };
  permissionGroupOptions = [];
  /* schema = {
    id: Joi.number().allow(null),
    name: Joi.string().required().label({this.state.translations.nameLabel}),description: Joi.string().required().label({this.state.translations.descriptionLabel}),isActive: Joi.string().required().label({this.state.translations.isActiveLabel}),
  };
*/
  async populateData() {
    try {
      const thisContext = this;
      await getAllPermissionGroup().then(function (result) {
        for (var i = 0; i < result.data.length; i++) {
          thisContext.permissionGroupOptions.push({
            value: result.data[i].id,
            label: result.data[i].description,
          });
        }
      });

      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
        this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj["i18n.authority.titleLabel"]
            ? translationsObj["i18n.authority.titleLabel"]
            : thisContext.state.translations.titleLabel,

          homeLabel: translationsObj["i18n.user.homeLabel"]
            ? translationsObj["i18n.user.homeLabel"]
            : thisContext.state.translations.homeLabel,

          saveLabel: translationsObj["i18n.user.saveLabel"]
            ? translationsObj["i18n.user.saveLabel"]
            : thisContext.state.translations.saveLabel,

          cancelLabel: translationsObj["i18n.user.cancelLabel"]
            ? translationsObj["i18n.user.cancelLabel"]
            : thisContext.state.translations.cancelLabel,

          nameLabel: translationsObj["i18n.authority.name.label"]
            ? translationsObj["i18n.authority.name.label"]
            : thisContext.state.translations.nameLabel,
          namePlaceholder: translationsObj["i18n.authority.name.placeholder"]
            ? translationsObj["i18n.authority.name.placeholder"]
            : thisContext.state.translations.namePlaceholder,
          descriptionLabel: translationsObj["i18n.authority.description.label"]
            ? translationsObj["i18n.authority.description.label"]
            : thisContext.state.translations.descriptionLabel,
          descriptionPlaceholder: translationsObj[
            "i18n.authority.description.placeholder"
          ]
            ? translationsObj["i18n.authority.description.placeholder"]
            : thisContext.state.translations.descriptionPlaceholder,
          isActiveLabel: translationsObj["i18n.authority.isActive.label"]
            ? translationsObj["i18n.authority.isActive.label"]
            : thisContext.state.translations.isActiveLabel,
          isActivePlaceholder: translationsObj[
            "i18n.authority.isActive.placeholder"
          ]
            ? translationsObj["i18n.authority.isActive.placeholder"]
            : thisContext.state.translations.isActivePlaceholder,


          permissionGroupLabel: translationsObj[
            "i18n.authority.permissionGroup.label"
          ]
            ? translationsObj["i18n.authority.permissionGroup.label"]
            : thisContext.state.translations.permissionGroupLabel,

          permissionGroupPlaceholder: translationsObj[
            "i18n.authority.permissionGroup.placeholder"
          ]
            ? translationsObj["i18n.authority.permissionGroup.placeholder"]
            : thisContext.state.translations.permissionGroupPlaceholder,
        },
      });
    } else {
      // console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(
        thisContext.state.userPermissions.includes("AUTHORITY_CREATE") ||
        thisContext.state.userPermissions.includes("AUTHORITY_UPDATE")
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      name: obj.name,
      description: obj.description,
      permissionGroups: obj.permissionGroups,
      isActive: obj.isActive,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status === "FAILED" ||
        response.data.validationMessages !== undefined
      ) {
        if (response.data.validationMessages !== undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message !== undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    // const { data, errors } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="pt-2 pb-2">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black mr-3 mb-3">
                  <Card.Title className="card-form-title-light">
                    <Row>
                      <Col md='1'>
                        {
                          this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                            e.preventDefault();
                            thisContext.props.history.push(thisContext.state.basicUrl);
                          })
                        }
                      </Col>
                      <Col md='11'>
                        <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                      </Col>
                    </Row>
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello Authority please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderInput(
                        true,
                        true,
                        "name",
                        this.state.translations.nameLabel,
                        this.state.translations.namePlaceholder,
                        "text",
                        false,
                        this.state.validationMessage.name
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "description",
                        this.state.translations.descriptionLabel,
                        this.state.translations.descriptionPlaceholder,
                        "textarea",
                        false,
                        this.state.validationMessage.description
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "isActive",
                        this.state.translations.isActiveLabel,
                        this.state.translations.isActivePlaceholder,
                        "checkbox",
                        false,
                        this.state.validationMessage.isActive
                      )}
                      {this.renderSelect(
                        true,
                        true,
                        "permissionGroups",
                        this.state.translations.permissionGroupLabel,
                        this.state.translations.permissionGroupPlaceholder,
                        thisContext.permissionGroupOptions,
                        this.state.validationMessage.permissionGroups
                      )}

                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </div>
                  </Card.Body>
                  {thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default AuthorityForm;
