import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./variantService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';


class VariantForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["VARIANT_CREATE","VARIANT_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/variants/",
	userPermissions: [],
    data: { id: null , name: "", description: "", variantValuess: [{value:"",}] },
    errors: {},
    validationMessage: {
      name: '',
description: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Variant",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	nameLabel: "Variant Name",
namePlaceholder: "Enter Variant Name",
descriptionLabel: "Description",
descriptionPlaceholder: "Enter Description",
variantValuesLabel: "Variant Values",
variantValuesPlaceholder: "Create Variant Values",

    },
	  variantValuesOptions : [],
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    name: Joi.string().required().label({this.state.translations.nameLabel}),description: Joi.string().required().label({this.state.translations.descriptionLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      
      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.variant.titleLabel"] ? translationsObj["i18n.variant.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          nameLabel: translationsObj["i18n.variant.name.label"] ? translationsObj["i18n.variant.name.label"] : thisContext.state.translations.nameLabel,
namePlaceholder: translationsObj["i18n.variant.name.placeholder"] ? translationsObj["i18n.variant.name.placeholder"] : thisContext.state.translations.namePlaceholder,
descriptionLabel: translationsObj["i18n.variant.description.label"] ? translationsObj["i18n.variant.description.label"] : thisContext.state.translations.descriptionLabel,
descriptionPlaceholder: translationsObj["i18n.variant.description.placeholder"] ? translationsObj["i18n.variant.description.placeholder"] : thisContext.state.translations.descriptionPlaceholder,
variantValuesLabel: translationsObj["i18n.variant.variantValues.label"] ? translationsObj["i18n.variant.variantValues.label"] : thisContext.state.translations.variantValuesLabel,
variantValuesPlaceholder: translationsObj["i18n.variant.variantValues.placeholder"] ? translationsObj["i18n.variant.variantValues.placeholder"] : thisContext.state.translations.variantValuesPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("VARIANT_CREATE") ||
      thisContext.state.userPermissions.includes("VARIANT_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      name: obj.name,description: obj.description,variantValuess: obj.variantValuess,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

    addVariantValuesRow = (e) => {
	   e.preventDefault();    let updatedVariantValuess = [...this.state.data.variantValuess, { value:"", }];
    this.setState({
      data: {
        ...this.state.data, variantValuess: updatedVariantValuess
      }
    });
    return;
  };

  handleVariantValuesDelete = (e,index) => {
  e.preventDefault();
    const updatedVariantValuess = [...this.state.data.variantValuess];
    updatedVariantValuess.splice(index, 1);
    // this.state.data.variantValuess = updatedVariantValuess;
this.setState({
      data: {
        ...this.state.data, variantValuess: updatedVariantValuess
      }
    });
return;
  };

  handleVariantValuesMultiRowChange = (index, e) => {
    const { name, value } = e.target;
    const updatedVariantValuess = [...this.state.data.variantValuess];
    updatedVariantValuess[index][name] = value;
    //this.state.data.variantValuess = updatedVariantValuess;
    this.setState({
      variantValuess: updatedVariantValuess
    });
    return;
  };
  handleVariantValuesMultiRowSelectChange = (index, name, value) => {
    const updatedVariantValuess = [...this.state.data.variantValuess];
    updatedVariantValuess[index][name] = value;
    this.setState({
      productVariants: updatedVariantValuess
    });
    return;
  };
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-light">
                <Row>
                  <Col md='1'>
                    {
                      this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                        e.preventDefault();
                        thisContext.props.history.push(thisContext.state.basicUrl);
                      })
                    }
                  </Col>
                  <Col md='11'>
                    <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                  </Col>
                </Row>
              </Card.Title>
			{/*<Card.Header>
				<p>Hello Variant please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}

                {this.renderInput(false,false,"id", "Id", "hidden")}
                <Row><Col>{this.renderInput(true,true,"name", this.state.translations.nameLabel,this.state.translations.namePlaceholder  ,"text",false,this.state.validationMessage.name)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"description", this.state.translations.descriptionLabel,this.state.translations.descriptionPlaceholder  ,"textarea",false,this.state.validationMessage.description)}</Col></Row>
<table className="table table-striped">
                      <thead>
                        <tr>
 <th>Value</th>
<th className="text-right"><button className="btn btn-sm btn-outline-success" onClick={this.addVariantValuesRow}><FontAwesomeIcon icon={faPlus}/> Variant Values</button></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.data.variantValuess.map((variantValues, index) => (
                          <tr key={index}>
             <td>
                              <input
                                type="text" class="form-control" placeholder = "Enter Value"
                                name="value"
                                value={variantValues.value}
                                onChange={(e) => this.handleVariantValuesMultiRowChange(index, e)}
                              />
                            </td>
                            <td className="text-right">
                              <button className="btn btn-sm btn-outline-danger" onClick={(e) => this.handleVariantValuesDelete(e,index)}><FontAwesomeIcon icon={faTrash}/></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>

                {/* {this.renderInput("dueDate", "Due Date")} */}

                
            </Card.Body>

				{thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
          		</Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default VariantForm;
