import React, { Component } from "react";
import _ from "lodash";
import PropTypes from "prop-types";
import { Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";

const Pagination = (props) => {
  const {
    currentPage,
    itemsCount,
    pageSize,
    onPageChange,
    firstPage,
    lastPage,
  } = props;

  const pagesCount = Math.ceil(itemsCount / pageSize);

  const startIndex = 1;
  const endIndex = pagesCount + 1;

  let extraPages = (pagesCount % 20 == 1) ? pagesCount : 20;
  let totalPages = pagesCount / 20;
  let extra = totalPages % 20;
//  alert(extra);
  
  // const startIndex = currentPage > 20 ? currentPage : 1;
  // const endIndex = pagesCount > 20 ? (startIndex + (extraPages*20)) : pagesCount + 1;

  const pages = _.range(startIndex, endIndex);

  return (
    <React.Fragment>
      <nav aria-label="Page navigation example">
        <ul className="pagination">
          <li>
            <Button
              disabled={firstPage}
              className="page-link"
              onClick={() => onPageChange(currentPage - 1)}
            >
              <FontAwesomeIcon icon={faArrowLeft} />
            </Button>
          </li>
          {pages.map((page) => (
            <li
              key={page}
              className={
                page === currentPage + 1 ? "page-item active" : "page-item"
              }
            >
              <Button
                  className="page-link"
                  onClick={() => onPageChange(page - 1)}
                >
                  {page}
                </Button>
            </li>
          ))}

          <li>
            <Button
              disabled={lastPage}
              className="page-link"
              onClick={() => onPageChange(currentPage + 1)}
            >
              <FontAwesomeIcon icon={faArrowRight} />
            </Button>
          </li>
        </ul>
      </nav>
    </React.Fragment>
  );
};
Pagination.propTypes = {
  itemsCount: PropTypes.number.isRequired,
  pageSize: PropTypes.number.isRequired,
  currentPage: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
};
export default Pagination;
