import React, { Component } from "react";
import { Navbar, Row, Col, Container, Jumbotron } from "react-bootstrap";
// import DashboardData from "./DashboardData";
import properties from '../../common_properties.json';

class AdminDashboard extends Component {
  state = {
    isLoggedIn: false,
    integrationUsingWebserver: true,
    userPermissions: [],
    loginUserEmail: "",
    loginUserFirstName: "",
    loginUserLastName: "",
    loginUserMobileNo: "",
    loginUserDesignation: "",
    translations: {
      welcomeMessageLabel: properties.labels.languages.en.welcome_message,
      mobileNoLabel: "Mobile No",
      emailLabel: "Email",
    }
  };

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidUpdate(prevProps) {
    if (prevProps.stateValues.activeLanguage !== this.props.stateValues.activeLanguage) {
      await this.loadTranslations();
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          welcomeMessageLabel: translationsObj["i18n.common.dashboardWelcomeMessage"]
            ? translationsObj["i18n.common.dashboardWelcomeMessage"]
            : thisContext.state.translations.welcomeMessageLabel,
          mobileNoLabel: translationsObj["i18n.common.mobileNoLabel"]
            ? translationsObj["i18n.common.mobileNoLabel"]
            : thisContext.state.translations.mobileNoLabel,
          emailLabel: translationsObj["i18n.common.emailLabel"]
            ? translationsObj["i18n.common.emailLabel"]
            : thisContext.state.translations.emailLabel,
        }
      });
      // console.log("AdminPanelNavBar :: loadTranslations");
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }


  async componentDidMount() {
    const thisContext = this;
    console.log("event: " + this.props.eventFired);
    const userPermissions = await thisContext.loadPermissions();
    if (userPermissions !== undefined) {
      thisContext.setState({ userPermissions: userPermissions });
    }

    let user = localStorage.getItem("user");
    if (user !== null && user !== undefined) {
      let userObj = JSON.parse(user);
      thisContext.setState({
        loginUserEmail: userObj.email,
        loginUserFirstName: userObj.firstName,
        loginUserLastName: userObj.lastName,
        loginUserMobileNo: userObj.mobileNo,
        loginUserDesignation: userObj.designation,
      });
      await thisContext.loadTranslations();
    } else {
      this.props.history.push("/access-denied");
    }
  }

  render() {
    return (

      <div className="app-dashboard">

        <Navbar
          bg="light"
          className="navbar shadow-sm bg-white rounded ml-1 mr-2"
          expand="md"
        >
          <Container>
            <Col sm='6'>
              <h4 className="text-left">{this.state.translations.welcomeMessageLabel} </h4>
            </Col>
            <Col sm='6'>
              <p className="text-right">
                {this.state.loginUserFirstName} {this.state.loginUserLastName} <br />
                {this.state.loginUserDesignation} <br />
                {this.state.translations.mobileNoLabel}: {this.state.loginUserMobileNo}<br/>
                {this.state.translations.emailLabel}: {this.state.loginUserEmail}
                
              </p>
            </Col>
          </Container>
        </Navbar>

        <Row>
          <Col>
            {/* <DashboardData userPermissions={this.state.userPermissions} integrationUsingWebserver={this.state.integrationUsingWebserver} /> */}
            <Jumbotron className="mr-2 ml-2 card-form-title-light">Dashboard will be updated soon...</Jumbotron>
          </Col>
        </Row>
      </div>
    );
  }
}

export default AdminDashboard;
