import "bootstrap/dist/css/bootstrap.min.css";
import React from "react";
import "./App.css";
import './css/fonts.css';

import Contact from "./components/public/Contact";
import Login from "./components/public/login";
import Logout from "./components/public/logout";

import TranslationsImportExportComponent from "./components/reserved/i18n/TranslationsImportExportComponent";

import NotFoundPage from "./components/public/NotFoundPage";

import AuthorityForm from "./components/reserved/authority/authorityForm";
import AuthorityManager from "./components/reserved/authority/authorityManager";

import Profile from "./components/reserved/user/profile";
import UserForm from "./components/reserved/user/userForm";
import UserManager from "./components/reserved/user/userManager";

import UserRequestForm from "./components/reserved/userRequest/userRequestForm";
import UserRequestManager from "./components/reserved/userRequest/userRequestManager";

import AppPermissionManager from "./components/reserved/appPermission/appPermissionManager";
import AppPermissionForm from "./components/reserved/appPermission/appPermissionForm";

import PermissionGroupManager from "./components/reserved/permissionGroup/permissionGroupManager";
import PermissionGroupForm from "./components/reserved/permissionGroup/permissionGroupForm";

import TranslationsManager from "./components/reserved/translations/translationsManager";
import TranslationsForm from "./components/reserved/translations/translationsForm";

import CategoryManager from "./components/category/categoryManager";
import CategoryForm from "./components/category/categoryForm";
import SubCategoryManager from "./components/subCategory/subCategoryManager";
import SubCategoryForm from "./components/subCategory/subCategoryForm";
import SupplierManager from "./components/supplier/supplierManager";
import SupplierForm from "./components/supplier/supplierForm";
import VariantManager from "./components/variant/variantManager";
import VariantForm from "./components/variant/variantForm";
import ProductRatingManager from "./components/productRating/productRatingManager";
import ProductRatingForm from "./components/productRating/productRatingForm";

import ProductManager from "./components/product/productManager";
import ProductForm from "./components/product/productForm";
import RelatedProductManager from "./components/relatedProduct/relatedProductManager";
import RelatedProductForm from "./components/relatedProduct/relatedProductForm";
import BrandManager from "./components/brand/brandManager";
import BrandForm from "./components/brand/brandForm";
import WebsiteFeatureManager from "./components/websiteFeature/websiteFeatureManager";
import WebsiteFeatureForm from "./components/websiteFeature/websiteFeatureForm";
import CustomerReviewManager from "./components/customerReview/customerReviewManager";
import CustomerReviewForm from "./components/customerReview/customerReviewForm";

import WebsiteInfoManager from "./components/websiteInfo/websiteInfoManager";
import WebsiteInfoForm from "./components/websiteInfo/websiteInfoForm";
import PurchaseOrderManager from "./components/purchaseOrder/purchaseOrderManager";
import PurchaseOrderForm from "./components/purchaseOrder/purchaseOrderForm";
import CustomerOrderManager from "./components/customerOrder/customerOrderManager";
import CustomerOrderForm from "./components/customerOrder/customerOrderForm";
import CustomerManager from "./components/customer/customerManager";
import CustomerForm from "./components/customer/customerForm";
import PaymentMethodManager from "./components/paymentMethod/paymentMethodManager";
import PaymentMethodForm from "./components/paymentMethod/paymentMethodForm";
import SalesOrderManager from "./components/salesOrder/salesOrderManager";
import SalesOrderForm from "./components/salesOrder/salesOrderForm";
// entityComponentsImport
import AdminPanelLayoutRoute from "./layouts/admin/AdminPanelLayoutRoute";
import PublicWebsiteLayoutRoute from "./layouts/PublicWebsiteLayout";
// import BusinessChart from "./components/adminPanel/BusinessChart";



import {
    BrowserRouter as Router,
    Switch
} from "react-router-dom";
// import ECommercePage from "./components/public/eCommerce";
// import WelcomePage from "./components/public/WelcomePage";

import AdminDashboard from "./components/adminPanel/AdminDashboard";
import SetLanguage from "./components/adminPanel/SetLanguage";
import AppStatus from "./components/adminPanel/AppStatus";
import AppsDashboardLayoutRoute from "./layouts/dashboard/AppsDashboardLayoutRoute";
import AppsDashboard from "./layouts/dashboard/AppsDashboard";


class App extends React.Component {
    constructor(props) {
        super(props);

        // Moblie first
        this.state = {
            isDark: true,
            isOpen: true,
            isMobile: true
        };

        this.previousWidth = -1;
    }

    updateWidth() {
        const width = window.innerWidth;
        const widthLimit = 576;
        const isMobile = width <= widthLimit;
        const wasMobile = this.previousWidth <= widthLimit;

        if (isMobile !== wasMobile) {
            this.setState({
                isOpen: !isMobile
            });
        }

        this.previousWidth = width;
    }

    /**
     * Add event listener
     */
    componentDidMount() {
        this.updateWidth();
        window.addEventListener("resize", this.updateWidth.bind(this));
    }

    /**
     * Remove event listener
     */
    componentWillUnmount() {
        window.removeEventListener("resize", this.updateWidth.bind(this));
    }

    toggle = () => {
        alert('from App.js');
        this.setState({ isOpen: !this.state.isOpen });
    };


    render() {
        return (
            <Router basename="/adminpanel">
                <Switch>
                    {/* <PublicWebsiteLayoutRoute exact path="/" component={WelcomePage}>
            <Redirect to="/login" />  
          </PublicWebsiteLayoutRoute> */}
                    <PublicWebsiteLayoutRoute exact path="/" component={Login} />
                    <AppsDashboardLayoutRoute exact path="/apps" component={AppsDashboard} />
                    <PublicWebsiteLayoutRoute path="/login" component={Login} />
                    <PublicWebsiteLayoutRoute path="/contact" component={Contact} />
                    <PublicWebsiteLayoutRoute path="/logout" component={Logout} />


                    <AdminPanelLayoutRoute path="/admin" exact component={AdminDashboard} />
                    <AdminPanelLayoutRoute path="/admin/dashboard" exact component={AdminDashboard} />
                    <AdminPanelLayoutRoute path="/admin/appStatus" exact component={AppStatus} />
                    <AdminPanelLayoutRoute path="/admin/userRequests" exact component={UserRequestManager} />
                    <AdminPanelLayoutRoute path="/admin/userRequests/new" exact component={UserRequestForm} />
                    <AdminPanelLayoutRoute path="/admin/userRequests/:id" exact component={UserRequestForm} />

                    <AdminPanelLayoutRoute path="/admin/users" exact component={UserManager} />
                    <AdminPanelLayoutRoute path="/admin/i18n/setLanguage/:lang" component={SetLanguage} />

                    <AdminPanelLayoutRoute path="/admin/users/new" exact component={UserForm} />
                    <AdminPanelLayoutRoute path="/profile" exact component={Profile} />
                    <AdminPanelLayoutRoute path="/admin/users/:id" exact component={UserForm} />

                    <AdminPanelLayoutRoute path="/admin/authoritys" exact component={AuthorityManager} />
                    <AdminPanelLayoutRoute path="/admin/authoritys/new" exact component={AuthorityForm} />
                    <AdminPanelLayoutRoute path="/admin/authoritys/:id" exact component={AuthorityForm} />

                    <AdminPanelLayoutRoute path="/admin/appPermissions" exact component={AppPermissionManager} />
                    <AdminPanelLayoutRoute path="/admin/appPermissions/new" exact component={AppPermissionForm} />
                    <AdminPanelLayoutRoute path="/admin/appPermissions/:id" exact component={AppPermissionForm} />

                    <AdminPanelLayoutRoute path="/admin/permissionGroups" exact component={PermissionGroupManager} />
                    <AdminPanelLayoutRoute path="/admin/permissionGroups/new" exact component={PermissionGroupForm} />
                    <AdminPanelLayoutRoute path="/admin/permissionGroups/:id" exact component={PermissionGroupForm} />

                    <AdminPanelLayoutRoute path="/admin/translationss" exact component={TranslationsManager} />
                    <AdminPanelLayoutRoute path="/admin/translationss/new" exact component={TranslationsForm} />
                    <AdminPanelLayoutRoute path="/admin/translationss/:id" exact component={TranslationsForm} />


                    <AdminPanelLayoutRoute path="/admin/categorys" exact component={CategoryManager} />
                    <AdminPanelLayoutRoute path="/admin/categorys/new" exact component={CategoryForm} />
                    <AdminPanelLayoutRoute path="/admin/categorys/:id" exact component={CategoryForm} />

                    <AdminPanelLayoutRoute path="/admin/subCategorys" exact component={SubCategoryManager} />
                    <AdminPanelLayoutRoute path="/admin/subCategorys/new" exact component={SubCategoryForm} />
                    <AdminPanelLayoutRoute path="/admin/subCategorys/:id" exact component={SubCategoryForm} />

                    <AdminPanelLayoutRoute path="/admin/suppliers" exact component={SupplierManager} />
                    <AdminPanelLayoutRoute path="/admin/suppliers/new" exact component={SupplierForm} />
                    <AdminPanelLayoutRoute path="/admin/suppliers/:id" exact component={SupplierForm} />

                    <AdminPanelLayoutRoute path="/admin/variants" exact component={VariantManager} />
                    <AdminPanelLayoutRoute path="/admin/variants/new" exact component={VariantForm} />
                    <AdminPanelLayoutRoute path="/admin/variants/:id" exact component={VariantForm} />

                    <AdminPanelLayoutRoute path="/admin/productRatings" exact component={ProductRatingManager} />
                    <AdminPanelLayoutRoute path="/admin/productRatings/new" exact component={ProductRatingForm} />
                    <AdminPanelLayoutRoute path="/admin/productRatings/:id" exact component={ProductRatingForm} />

                    <AdminPanelLayoutRoute path="/admin/products" exact component={ProductManager} />
                    <AdminPanelLayoutRoute path="/admin/products/new" exact component={ProductForm} />
                    <AdminPanelLayoutRoute path="/admin/products/:id" exact component={ProductForm} />

                    <AdminPanelLayoutRoute path="/admin/relatedProducts" exact component={RelatedProductManager} />
                    <AdminPanelLayoutRoute path="/admin/relatedProducts/new" exact component={RelatedProductForm} />
                    <AdminPanelLayoutRoute path="/admin/relatedProducts/:id" exact component={RelatedProductForm} />

                    <AdminPanelLayoutRoute path="/admin/brands" exact component={BrandManager} />
                    <AdminPanelLayoutRoute path="/admin/brands/new" exact component={BrandForm} />
                    <AdminPanelLayoutRoute path="/admin/brands/:id" exact component={BrandForm} />

                    <AdminPanelLayoutRoute path="/admin/websiteFeatures" exact component={WebsiteFeatureManager} />
                    <AdminPanelLayoutRoute path="/admin/websiteFeatures/new" exact component={WebsiteFeatureForm} />
                    <AdminPanelLayoutRoute path="/admin/websiteFeatures/:id" exact component={WebsiteFeatureForm} />

                    <AdminPanelLayoutRoute path="/admin/websiteInfos" exact component={WebsiteInfoManager} />
                    <AdminPanelLayoutRoute path="/admin/websiteInfos/new" exact component={WebsiteInfoForm} />
                    <AdminPanelLayoutRoute path="/admin/websiteInfos/:id" exact component={WebsiteInfoForm} />

                    <AdminPanelLayoutRoute path="/admin/customerReviews" exact component={CustomerReviewManager} />
                    <AdminPanelLayoutRoute path="/admin/customerReviews/new" exact component={CustomerReviewForm} />
                    <AdminPanelLayoutRoute path="/admin/customerReviews/:id" exact component={CustomerReviewForm} />

                    <AdminPanelLayoutRoute path="/admin/purchaseOrders" exact component={PurchaseOrderManager} />

                    <AdminPanelLayoutRoute path="/admin/purchaseOrders/new" exact component={PurchaseOrderForm} />
                    <AdminPanelLayoutRoute path="/admin/purchaseOrders/:id" exact component={PurchaseOrderForm} />

                    <AdminPanelLayoutRoute path="/admin/customerOrders" exact component={CustomerOrderManager} />

                    <AdminPanelLayoutRoute path="/admin/customerOrders/new" exact component={CustomerOrderForm} />
                    <AdminPanelLayoutRoute path="/admin/customerOrders/:id" exact component={CustomerOrderForm} />
                      
<AdminPanelLayoutRoute path="/admin/customers" exact component={CustomerManager} />   

<AdminPanelLayoutRoute path="/admin/customers/new" exact component={CustomerForm} />
<AdminPanelLayoutRoute path="/admin/customers/:id" exact component={CustomerForm} />
  
<AdminPanelLayoutRoute path="/admin/paymentMethods" exact component={PaymentMethodManager} />   

<AdminPanelLayoutRoute path="/admin/paymentMethods/new" exact component={PaymentMethodForm} />
<AdminPanelLayoutRoute path="/admin/paymentMethods/:id" exact component={PaymentMethodForm} />
  
<AdminPanelLayoutRoute path="/admin/salesOrders" exact component={SalesOrderManager} />   

<AdminPanelLayoutRoute path="/admin/salesOrders/new" exact component={SalesOrderForm} />
<AdminPanelLayoutRoute path="/admin/salesOrders/:id" exact component={SalesOrderForm} />
{/*entityComponentsRoutes*/}
                    <AdminPanelLayoutRoute exact path="/admin/i18n/importTranslations" component={TranslationsImportExportComponent} />

                    <PublicWebsiteLayoutRoute path="*" component={NotFoundPage} />
                </Switch>
            </Router>

        );
    }
}


export default App;