import React from "react";
import { Nav } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faCaretUp, faCopy, faImage } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";

class SubMenuSettings extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeMenu: null // Stores the active menu item
    };
    this.toggleNavbar = this.toggleNavbar.bind(this);
  }

  componentDidMount() {
    const currentPath = window.location.pathname;

    // Check the current URL path to determine which menu should be active
    if (currentPath.startsWith("/adminpanel/admin/categorys")) {
      this.setState({ activeMenu: "categorys" });
    } else if (currentPath.startsWith("/adminpanel/admin/suppliers")) {
      this.setState({ activeMenu: "suppliers" });
    } else if (currentPath.startsWith("/adminpanel/admin/subCategorys")) {
      this.setState({ activeMenu: "subCategorys" });
    } else if (currentPath.startsWith("/adminpanel/admin/variants")) {
      this.setState({ activeMenu: "variants" });
    } else if (currentPath.startsWith("/adminpanel/admin/products")) {
      this.setState({ activeMenu: "products" });
    }
    if (currentPath.startsWith("/adminpanel/admin/subCategorys")
      || currentPath.startsWith("/adminpanel/admin/suppliers")
      || currentPath.startsWith("/adminpanel/admin/categorys")
      || currentPath.startsWith("/adminpanel/admin/variants")
      || currentPath.startsWith("/adminpanel/admin/products")) {
      this.toggleNavbar("settings");
    }

  }

  toggleNavbar(menu) {
    // Toggle the specific menu passed
    this.setState((prevState) => ({
      activeMenu: prevState.activeMenu === menu ? null : menu
    }));
  }

  renderMenuItem(to, label, icon, menuKey) {
    const { activeMenu } = this.state;
    return (
      <Nav.Item>
        <Nav.Link as="div">
          <NavLink
            to={to}
            className={classNames("nav-link", "nav-item", { active: activeMenu === menuKey })}
          >
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {label}
          </NavLink>
        </Nav.Link>
      </Nav.Item>
    );
  }

  render() {
    const { icon, title, translations, userPermissions } = this.props;
    const { activeMenu } = this.state;

    return (
      <Nav.Item className={classNames({ open: activeMenu })}>
        <Nav.Link
          variant="link"
          className={classNames("d-flex justify-content-between align-items-center nav-link nav-item", { active: activeMenu })}
          onClick={() => this.toggleNavbar("settings")}
        >
          <div>
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {title}
          </div>
          <FontAwesomeIcon icon={activeMenu === "settings" ? faCaretUp : faCaretDown} />
        </Nav.Link>

        {activeMenu === "settings" && (
          <nav className="nav flex-column">
            {userPermissions.includes("CATEGORY_READ") &&
              this.renderMenuItem("/admin/categorys", translations.categoryMenuLabel, faImage, "categorys")}
            {userPermissions.includes("SUPPLIER_READ") &&
              this.renderMenuItem("/admin/suppliers", translations.supplierMenuLabel, faImage, "suppliers")}
            {userPermissions.includes("SUB_CATEGORY_READ") &&
              this.renderMenuItem("/admin/subCategorys", translations.subCategoryMenuLabel, faImage, "subCategorys")}
            {userPermissions.includes("VARIANT_READ") &&
              this.renderMenuItem("/admin/variants", translations.variantMenuLabel, faImage, "variants")}

            {userPermissions.includes("PRODUCT_READ") &&
              this.renderMenuItem("/admin/products", translations.productMenuLabel, faImage, "products")}

          </nav>
        )}
      </Nav.Item>
    );
  }
}

export default SubMenuSettings;
