import React, { Component } from "react";
import logo from "../../images/companyLogo.png";

import {
  loginAction,
  loadUser,
  loadTranslationsAuthless
} from "../common/authenticationService";
import {
  Row,
  Form,
  Button,
  Container,
  Col,
  Jumbotron,
} from "react-bootstrap";
import properties from "../../common_properties.json";
// import LoginFooter from "./includes/LoginFooter";
import PasswordInputWithToggle from "./PasswordInputWithToggle";
// const activeLanguage = localStorage.getItem("activeLanguage") || "en";

class Login extends Component {
  state = {
    data: { username: "", password: "" },
    adminLoginMessage: "",
    userLoginMessage: "",
    errors: {},
    translations: {
      loginTitleLabel: "Login",
      loginDescriptionLabel: "Sign In To Your Account",
      appNameDetails: properties.labels.languages.en.app_name_details,
      usernameLabel: "Username",
      usernamePlaceholderLabel: "Enter Username",
      passwordLabel: "Password",
      passwordPlaceholderLabel: "Enter Password",
      forgotPasswordLabel: "Forgot Password",
      loginButtonLabel: "Submit",
      rememberMeLabel: "Remember Me",
    },
    language: "en" // Initialize with default language
  };

  async loadTranslations(language) {
    try {
      const success = await loadTranslationsAuthless(language);
      if (!success) {
        console.log("Failed to load translations from API");
        return;
      }

      const translations = localStorage.getItem("translations");
      if (translations) {
        const translationsObj = JSON.parse(translations);
        // alert('reset language labels'+JSON.stringify(translationsObj));
        this.setState({
          translations: {
            loginTitleLabel: translationsObj["i18n.common.loginTitleLabel"] || "Login",
            loginDescriptionLabel: translationsObj["i18n.common.loginDescriptionLabel"] || "Sign In To Your Account",
            appNameDetails: translationsObj["i18n.common.appNameDetails"] || properties.labels.languages.en.app_name_details,
            usernameLabel: translationsObj["i18n.common.usernameLabel"] || "Username",
            usernamePlaceholderLabel: translationsObj["i18n.common.usernamePlaceholderLabel"] || "Enter Username",
            passwordLabel: translationsObj["i18n.common.passwordLabel"] || "Password",
            passwordPlaceholderLabel: translationsObj["i18n.common.passwordPlaceholderLabel"] || "Enter Password",
            forgotPasswordLabel: translationsObj["i18n.common.forgotPasswordLabel"] || "Forgot Password",
            loginButtonLabel: translationsObj["i18n.common.loginButtonLabel"] || "Submit",
            rememberMeLabel: translationsObj["i18n.common.rememberMeLabel"] || "Remember Me",
          }
        });
        this.reloadSiblingComponents(language);

      } else {
        console.log("Alert!!!, Translations not found!");
      }
    } catch (error) {
      console.error("Error loading translations from API:", error);
    }
  }

  componentDidMount() {
    localStorage.removeItem("user");
    localStorage.removeItem("token");
    localStorage.removeItem("authorities");
    localStorage.removeItem("translations");

    const initialLanguage = this.state.language || 'en';
    this.loadTranslations(initialLanguage).then(() => {
      console.log("Initial translations loaded.");
    }).catch((error) => {
      console.error("Error loading initial translations:", error);
    });
  }

  updateRedirectState(url) {
    this.props.history.push(url);
  }

  sleep = async (msec) => {
    return new Promise((resolve) => setTimeout(resolve, msec));
  };

  doSubmit = async (event) => {
    event.preventDefault();
    this.login(event);
    return;
  };

  login = (event) => {
    const thisContext = this;
    const formData = new FormData(event.target),
      formDataObj = Object.fromEntries(formData.entries());
    try {
      loginAction(formDataObj.username, formDataObj.password).then(function (result) {
        if (result == true) {
          thisContext.loadUserDetails(result);
        } else {
          thisContext.setState({
            userLoginMessage: result,
          });
        }
        return;
      });
    } catch (error) {
      thisContext.setState({
        userLoginMessage: error,
      });
    }
  };

  loadUserDetails = (result) => {
    const thisContext = this;
    loadUser().then(function (userIsLoaded) {
      if (userIsLoaded) {
        let user = localStorage.getItem("user");
        if (result == true && user != null && user != undefined) {
          let userObj = JSON.parse(user);
          loadTranslationsAuthless(thisContext.state.language).then(function () {
            console.log("translations loaded!");
            thisContext.loadTranslations(thisContext.state.language); // Reload translations after API call
            if (
              userObj.membershipType != undefined &&
              userObj.membershipType != null
            ) {
              if (
                userObj.membershipType == "SUPERADMIN" ||
                userObj.membershipType == "ADMIN"
              ) {
                thisContext.updateRedirectState("/admin/dashboard");
              } else if (userObj.membershipType == "USER") {
                thisContext.updateRedirectState("/admin/dashboard");
              } else {
                thisContext.updateRedirectState("/admin/dashboard");
              }
            } else {
              thisContext.setState({ userLoginMessage: "Not Authorized!!!" });
            }
          });
        } else {
          thisContext.setState({ userLoginMessage: "User not logged in." });
        }
      }
      return;
    });
  };

  handleLanguageToggle = () => {
    const newLanguage = this.state.language === "en" ? "bn" : "en";
    this.setState({ language: newLanguage }, () => {
      this.loadTranslations(newLanguage).then(() => {
        console.log("Translations updated.");
      }).catch((error) => {
        console.error("Error updating translations:", error);
      });
    });
  };

  reloadSiblingComponents = (updatedLanguage) => {
    // this.sideBarComponent.reloadTranslations(updatedLanguage);
    // this.footerComponent.reloadTranslations(updatedLanguage);
  };

  render() {
    const { data, errors } = this.state;

    return (
      <React.Fragment>
        <Container className="flexbox-container content-wrapper">
          <Button variant="secondary" size="sm" onClick={this.handleLanguageToggle}
            style={{ position: "fixed", top: "0px", right: "0px", justifyContent: "auto" }}
          >
            {this.state.language === "en" ? "" : "English"}
          </Button>
          <div className="login-form row">
            <Col sm={"6"} align="center">
              <Row>
                <Col><img style={{ paddingTop: "1rem" }} src={logo} width={"300px"} height={"200px"} /></Col>
              </Row>
              <Row><hr /></Row>
              <Row>
                <Col>
                  <h4 className="app-name">
                    {this.state.translations.appNameDetails}
                    </h4>
                </Col>
              </Row>
            </Col>
            <Col>
              <Form onSubmit={this.doSubmit} align="left">
                <Form.Group className="row">
                  <h2>{this.state.translations.loginTitleLabel}</h2>
                </Form.Group>
                <Form.Group className="row">
                  <h6>{this.state.translations.loginDescriptionLabel}</h6>
                </Form.Group>
                <Form.Group className="row" controlId="formBasicUsername">
                  <Form.Label>{this.state.translations.usernameLabel}</Form.Label>
                  <Form.Control
                    onFocus={() => {
                      this.setState({ userLoginMessage: "" });
                    }}
                    name="username"
                    type="text"
                    placeholder={this.state.translations.usernamePlaceholderLabel}
                  />
                </Form.Group>
                {/* <Form.Group className="row" controlId="formBasicPassword">
                      <Form.Label>{this.state.translations.passwordLabel}</Form.Label>
                      <Form.Control
                        onFocus={() => {
                          this.setState({ userLoginMessage: "" });
                        }}
                        type="password"
                        name="password"
                        placeholder={this.state.translations.passwordPlaceholderLabel}
                      />
                    </Form.Group> */}
                <PasswordInputWithToggle passwordLabel={this.state.translations.passwordLabel}
                  passwordPlaceholderLabel={this.state.translations.passwordPlaceholderLabel}
                />

                <Form.Group className="row">
                  <Button className="form-control" variant="success" type="submit">
                    {this.state.translations.loginButtonLabel}
                  </Button>
                </Form.Group>
                <Form.Group className="row">
                  <p style={{ color: "red" }}>
                    {" "}
                    {this.state.userLoginMessage}{" "}
                  </p>
                </Form.Group>

              </Form>
              {/* <LoginFooter ref={(child) => { this.footerComponent = child; }} /> */}
            </Col>
          </div>
        </Container>
      </React.Fragment>
    );
  }
}

export default Login;
