import React, { Component } from "react";
import Table from "../common/table";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faPencilAlt } from "@fortawesome/free-solid-svg-icons";
import DeleteConfirmModal from "./DeleteConfirmModal";
import { Button } from "react-bootstrap";

class UxCrudTable extends Component {
    state = {
        userPermissions: [],
        columns: [],
        itemId: null,
        showView: false,
        showDeleteConfirm: false,
        title: "",
        body: "",
        data: [],
        translations: {
            viewLabel: "View",
            editLabel: "Edit",
            approveLabel: "Approve",
            deleteLabel: "Delete",
        },
    };

    columns = [];

    handleViewShow = (item) => {
        const filteredDatas = this.props.data.filter(function (obj) {
            return obj.id === item.id;
        });

        const preparedObj = filteredDatas.length > 0 ? filteredDatas[0] : { id: null };
        this.setState({
            item: preparedObj,
            showView: true,
            showDeleteConfirm: false,
            data: preparedObj,
        });
    };

    handleViewClose = (fromModal) => {
        this.setState({
            item: {},
            showView: false,
            showDeleteConfirm: false,
        });
    };

    handleDeleteConfirmClose = (fromModal) => {
        this.setState({
            item: {},
            showView: false,
            showDeleteConfirm: false,
        });
    };

    handleDeleteFromModal = (item) => {
        this.props.onDelete(item);
        this.setState({
            item: {},
            showView: false,
            showDeleteConfirm: false,
        });
    };

    loadPermissions = () => {
        return new Promise((resolve) => {
            setTimeout(() => {
                let permissions = localStorage.getItem("permissions");
                const userPermissions = permissions.split(",");
                resolve(userPermissions);
            }, 100);
        });
    }

    handleDeleteModalShow = (item) => {
        const filteredDatas = this.props.data.filter(function (obj) {
            return obj.id === item.id;
        });

        const preparedObj =
            filteredDatas.length > 0 ? filteredDatas[0] : { id: null };

        this.setState({
            item: preparedObj,
            showView: false,
            showDeleteConfirm: true,
        });
    };

    handleViewClose = (fromModal) => {
        this.setState({
            item: {},
            showView: false,
            showDeleteConfirm: false,
        });
    };

    handleDeleteConfirmClose = (fromModal) => {
        this.setState({
            item: {},
            showView: false,
            showDeleteConfirm: false,
        });
    };

    handleDeleteFromModal = (item) => {
        this.props.onDelete(item);
        this.setState({
            item: {},
            showView: false,
            showDeleteConfirm: false,
        });
    };


    async componentDidUpdate(prevProps) {
        if (prevProps.activeLanguage !== this.props.activeLanguage) {
          await this.loadTranslations();
          await this.loadColumns();
        }
    }

    async componentDidMount() {
        const thisContext = this;
        try {
            const permissions = await thisContext.loadPermissions();
            thisContext.setState({ userPermissions: permissions });
            await thisContext.loadTranslations();
            await thisContext.loadColumns();
        } catch (error) {
            if (error.response && error.response.status === 404) {
                this.props.history.replace("/not-found");
            }
        }
    }

    loadActionColumns() {
        let thisContext = this;
        if (thisContext.state.userPermissions.includes(this.state.DATA_READ)) {
            thisContext.columns.push({
                key: "edit",
                label: thisContext.state.translations.actionsLabel,
                content: (item) => (
                    <>
                        {/* {thisContext.state.userPermissions.includes(this.state.DATA_READ) &&
                            <Button variant="light" size="sm" onClick={this.props.onClick}>
                                <FontAwesomeIcon icon={faEye} />
                            </Button>
                        } */}
                        {thisContext.state.userPermissions.includes(this.state.DATA_UPDATE) &&
                            <button
                                onClick={() => this.props.onEdit(item)}
                                className="btn btn-sm btn-light"
                            >
                                <FontAwesomeIcon icon={faPencilAlt} />
                            </button>
                        }


                        {thisContext.state.userPermissions.includes(this.state.DATA_DELETE) && <DeleteConfirmModal
                            disabled={item.status == "APPROVED"}
                            show={this.state.showDeleteConfirm}
                            item={this.state.item}
                            onClick={() => this.handleDeleteModalShow(item)}
                            onHide={this.handleDeleteConfirmClose}
                            onConfirm={(item) => this.handleDeleteFromModal(item)}
                        />}
                    </>
                ),
            });
        }
    }

    render() {
        const { onCreate, data, onSort, sortColumn } = this.props;
        return (
            <React.Fragment>
                <Table
                    columns={this.state.columns}
                    sortColumn={sortColumn}
                    onCreate={onCreate}
                    onSort={onSort}
                    data={data}
                />
            </React.Fragment>
        );
    }
}
export default UxCrudTable;