import React, { Component, useState } from "react";
// import { Row, Col } from "react-bootstrap";
// import Container from "react-bootstrap/Container";
import { Route } from "react-router-dom";

// import AdminPanelSideBar from "../components/sidebar/AdminPanelSideBar";
// import AdminPanelContent from "../components/content/AdminPanelContent";
import properties from "../../common_properties.json";
// import PublicFooter from "../components/public/includes/PublicFooter";
import AppsDashboardLayout from "./AppsDashboardLayout";

const AppsDashboardLayoutRoute = ({ component: Component, ...rest }) => {
  const activeLayout = localStorage.getItem("activeLayout");
  const isRtl = activeLayout != undefined ? activeLayout == "RTL" : false;
  return (
    <Route
      render={(matchProps) => (
        <AppsDashboardLayout
          activeLanguage="bn"
          isOpen={false}
          isRtl={isRtl}
          labels={properties.labels.languages.en}
        >
          <Component {...matchProps} />
        </AppsDashboardLayout>
      )}
    />
  );
};

export default AppsDashboardLayoutRoute;
