import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/dashboard";

function applicationItemStatusUrl(id) {
  return `${apiEndpoint}/${id}`;
}

export function getAllApplicationItemStatuss() {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(apiEndpoint + "/applicationItemStatuss", headerOptions);
}
export function getAllDailyApplicationItems() {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(apiEndpoint + "/dailyApplicationItems", headerOptions);
}

export function getConfiguredCsraPortals() {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(apiEndpoint + "/configuredCsraPortals", headerOptions);
}

export function getMNOCsraIntegrationUrl(mnoId) {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(apiEndpoint + "/getMNOCsraIntegrationUrl/" + mnoId, headerOptions);
}
