import React, { Component } from "react";
import { Spinner, Container, Row, Col } from "react-bootstrap";
import UxCrudInput from "./form_attributes/UxCrudInput";
import CustomBreadcrumb from "./CustomBreadcrumb";
import UxCrudSelect from "./form_attributes/UxCrudSelect";
import UxCrudImage from "./form_attributes/UxCrudImage";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faLongArrowAltLeft
} from "@fortawesome/free-solid-svg-icons";

class UxCrudForm extends Component {
  state = {
    isLoading: true,
    data: {},
    errors: {},
  };

  validate = () => {
    // const data = { ...this.state.data };
    // data[input.name] = input.value;
    // console.log(input.name + "->" + input.value);
    // this.setState({ data, errors });
    // console.log("validating....");
    // console.log(this.state.data);
    // const options = { abortEarly: false };
    return null;
    /* ignore joi validation, need to work for different validations, 
    if ignored then server side validation will do all responsibilities*/

    // const { error } = Joi.validate(this.state.data, this.schema, options);
    // if (!error) return null;

    // const errors = {};
    // // for (let item of error.details) errors[item.path[0]] = item.message;
    // return errors;
  };

  async componentDidUpdate(prevProps) {
    if (prevProps.stateValues.activeLanguage !== this.props.stateValues.activeLanguage) {
      await this.loadTranslations();
    }
  }

  validateProperty = ({ name, value }) => {
    // const obj = { [name]: value };
    // const schema = { [name]: this.schema[name] };
    const { error } = {}; //Joi.validate(obj, schema);
    return error ? error.details[0].message : null;
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const errors = this.validate();
    this.setState({ errors: errors || {} });
    // console.log(errors);
    if (errors) return;
    this.doSubmit();
  };

  handleChange = ({ currentTarget: input }) => {
    // console.log(input.type);
    const errors = { ...this.state.errors };
    const errorMessage = null; //this.validateProperty(input);
    if (errorMessage) errors[input.name] = errorMessage;
    else delete errors[input.name];

    const data = { ...this.state.data };

    if (input.type === "checkbox") {
      data[input.name] = input.checked;
    } else if (input.type === "select-multiple") {
      const selectedOptions = Array.from(
        input.selectedOptions,
        (option) => option.value
      );
      data[input.name] = selectedOptions;
    } else {
      if (input.value === '') {
        errors[input.name] = 'data required';
      }
      data[input.name] = input.value;
    }

    // console.log(JSON.stringify(data));
    this.setState({ data, errors });
  };

  handleOptionChange = (name, selectedOption) => {
    // this.state.data
    // console.log(selectedOption);
    const data = { ...this.state.data };
    data[name] = selectedOption;
    this.setState({ data });
  };

  renderButton(label, onClickEvent) {
    return (
      <button className="btn btn-success form-control" onClick={onClickEvent}>
        {label}
      </button>
    );
  }

  renderCancelButton(label, onClickEvent) {
    return (
      <button className="btn btn-light form-control" onClick={onClickEvent}>
        {/* {label} */}
        <FontAwesomeIcon icon={faLongArrowAltLeft} />
      </button>
    );
  }
  // (true, false, "orgTypeId", "Organization Type", thisContext.organizationTypeOptions, this.state.validationMessage.orgTypeId)}
  renderSelect(
    isRequired,
    isMultiple,
    name,
    label,
    placeholder,
    options,
    validationMessage
  ) {
    const { data, errors } = this.state;
    // let selectedLabel = data[name];
    // { isRequired, isMultiple, name, label, options,validationMessage,selectedOption, error, ...rest }
    return (
      <UxCrudSelect
        isRequired={isRequired}
        isMultiple={isMultiple}
        name={name}
        label={label}
        placeholder={placeholder}
        options={options}
        validationMessage={validationMessage}
        value={data[name]}
        // value={selectedOption}
        error={errors[name]}
        updateSelected={this.handleOptionChange}
      />
    );
  }


  renderImageInput(
    isRequired,
    name,
    label,
    placeholder,
    validationMessage
  ) {
    const { data, errors } = this.state;
    // let selectedLabel = data[name];
    // { isRequired, isMultiple, name, label, options,validationMessage,selectedOption, error, ...rest }
    return (
      <UxCrudImage
        isRequired={isRequired}
        name={name}
        label={label}
        // placeholder={placeholder}
        validationMessage={validationMessage}
        value={data[name]}
        error={errors[name]}
        updateSelected={this.handleOptionChange}
      />
    );
  }

  renderInput(
    isRequired,
    isVisible,
    name,
    label,
    placeholder,
    type,
    readonly,
    validationMessage,
    inputFieldDefaultVal
  ) {
    const { data, errors } = this.state;
    let defaultValue =
      data[name] === undefined ? '' : data[name];
    return (
      <>
        <UxCrudInput
          isRequired={isRequired}
          isVisible={isVisible}
          name={name}
          label={label}
          placeholder={placeholder}
          type={type}
          value={defaultValue}
          checked={defaultValue}
          readonly={readonly}
          validationMessage={validationMessage}
          error={errors[name]}
          onChange={this.handleChange}
        />
      </>
    );
  }
  noPermissionMessage() {
    return "You are not permitted to view this page!";
  }

  render() {
    let { isLoading } = this.state;
    let isPermitted = false;
    this.state.requiredPermissions.forEach((requiredPermission) => {
      if (
        !isPermitted &&
        this.state.userPermissions.includes(requiredPermission)
      ) {
        isPermitted = true;
      }
    });

    if (isLoading) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden"></span>
        </Spinner>
      );
    } else if (!isPermitted) {
      return (
        <>
          <Container className="bg-light text-black">
            <Row>
              <Col>
                <h2>{this.noPermissionMessage()}</h2>
              </Col>
            </Row>
          </Container>
        </>
      );
    }
    return (
      <>
        {/* <CustomBreadcrumb
          moduleUrl={this.state.moduleUrl}
          basicUrl={this.state.basicUrl}
          titleLabel={this.state.translations.titleLabel}
          homeLabel={this.state.translations.homeLabel}
        /> */}

        {this.htmlContent()}
      </>
    );
  }
}

export default UxCrudForm;
