import React from "react";
import Pagination from "../../common/pagination";
import { getAllPaginatedAndSearchToken, remove, save } from "./translationsService";
import { checkAuthentication } from "../../common/authenticationService";
// import Container from "react-bootstrap/Container";
import { Col, Row, Card, Button, Alert } from "react-bootstrap";
import TranslationssTable from "./translationsTable";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faSearch } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../../common/UxCrudManager";

class TranslationsManager extends UxCrudManager {
  state = {
    isLoading: true,
    errorDismissed: true,
    successDismissed: true,
    commonValidationMessage: "",
    DATA_CREATE: "TRANSLATIONS_CREATE",
    requiredPermission: "TRANSLATIONS_READ",
    basicUrl: "/admin/translationss/",
    catagorySelectionLabel: "All Translations",
    categories: [
      "All Translationss",
      "DRAFT",
      "APPROVED",
      "REJECTED",
      "DELETED",
    ],
    userPermissions: [],
    data: [],
    currentPage: 0,
    translations: {
      homeLabel: "Home",
      titleLabel: "Translations",
      addNewDataLabel: "Add New Translation",
    },
    pageSize: 10,
    searchToken: "",
    firstPage: true,
    lastPage: false,
    count: 0,
    currentItem: "All Translationss",
    sortColumn: { path: "status", order: "asc" },
    totalElements: 0,
  };

  loadData = async () => {
    let thisContext = this;
    thisContext.setState({ isLoading: true });
    await checkAuthentication().then(function (result) {
      if (result) {
        // console.log("current page: " + thisContext.state.currentPage);
        // console.log("page size: " + thisContext.state.pageSize);
        getAllPaginatedAndSearchToken(
          thisContext.state.currentPage,
          thisContext.state.pageSize,
          thisContext.state.searchToken
        )
          .then((response) => {
            const { data: responseObj } = response;
            thisContext.setState({
              data: responseObj.content,
              pageSize: responseObj.pageable.pageSize,
              currentPage: responseObj.pageable.pageNumber,
              totalElements: responseObj.totalElements,
              firstPage: responseObj.first,
              lastPage: responseObj.last,
              isLoading: false,
            });
            thisContext.getPagedData();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        thisContext.props.history.push("/login");
      }
    });
  };

  handleTextChange = ({ currentTarget: input }) => {
    this.setState({ searchToken: input.value });
  }


  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          "homeLabel": translationsObj["i18n.home.label"] ? translationsObj["i18n.home.label"] : thisContext.state.translations.homeLabel,
          "titleLabel": translationsObj["i18n.translations.titleLabel"]
            ? translationsObj["i18n.translations.titleLabel"]
            : thisContext.state.translations.titleLabel,
          "addNewDataLabel": translationsObj["i18n.translations.addNewDataLabel"]
            ? translationsObj["i18n.translations.addNewDataLabel"]
            : thisContext.state.translations.addNewDataLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  saveItem = async (item) => {
    await save(item);
  };

  deleteItem = async (item) => {
    await remove(item);
  };

  htmlContent() {
    let thisContext = this;
    const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } =
      this.state;
    const { totalCount, data } = this.getPagedData();
    return (
      <React.Fragment>
        {/* <DeleteConfirmModal /> */}
        <Row>
          {/*
             * <Col md="2" xs="2"> <ListGroup
             * currentItem={this.state.currentItem} textProperty="name"
             * valueProperty="_id" items={this.state.categories}
             * onItemSelect={this.handleCategorySelect} /> </Col>
             */}
          <Col md="12" xs="12" className="ml-2">
            <Card className="bg-light text-black mr-3 mb-3">
              <Card.Title className="card-form-title-light">
                <Row>
                  <Col>
                    {this.state.translations.titleLabel}
                  </Col>
                  <Col style={{ display: 'flex', justifyContent: 'right' }}>
                    {this.state.userPermissions.includes(this.state.DATA_CREATE) && (
                      <button
                        onClick={this.loadCreateForm}
                        className="btn btn-sm btn-success"
                      >
                        <FontAwesomeIcon icon={faPlus} />
                        &nbsp; {this.state.translations.addNewDataLabel}
                      </button>
                    )}
                  </Col>
                  <Col style={{ display: 'none', justifyContent: 'right' }}>
                    <select
                      className="form-control"
                      value={this.state.currentItem}
                      onChange={function (event) {
                        thisContext.handleCategorySelect(event.target.value);
                      }}
                    >
                      <option>-Select Status-</option>
                      {this.state.categories.map((category) => (
                        <option value={category} key={category}>
                          {category}
                        </option>
                      ))}
                    </select>{" "}
                  </Col>
                  <Col>
                    <div className="input-group">
                      <input type="text" onChange={this.handleTextChange} value={this.state.searchToken} className="form-control" placeholder="Enter key name" aria-label="Search Token" aria-describedby="searchToken" />

                      <div className="input-group-append">
                        <div className="input-group-text form-control">
                          <Button onClick={()=>{
                            this.loadData();
                          }} variant="none" size="sm" className="mr-2">
                            <FontAwesomeIcon icon={faSearch} />
                          </Button>
                        </div>
                      </div>
                    </div>
                  </Col>
                </Row>
              </Card.Title>

              <Card.Body>
                {/* <Card.Title>User Request</Card.Title> */}
                {/* form>(div.form-group>label+input.form-control)*2 */}
                <TranslationssTable
                  activeLanguage={this.props.stateValues.activeLanguage} columns={columns}
                  sortColumn={sortColumn}
                  onSort={this.handleSort}
                  onLikeClick={this.handleChangeChk}
                  data={data}
                  onView={this.handleView}
                  onEdit={this.handleEdit}
                  onApprove={this.handleApprove}
                  onDelete={this.handleDelete}
                  onCreate={this.loadCreateForm}
                  stateValues={this.props.stateValues}
                />
                <p>Total {this.state.totalElements} records found!</p>
                {!this.state.errorDismissed && (
                  <div style={{
                    cursor: 'pointer',
                    margin: '5px',
                    backgroundColor: 'white',
                    borderRadius: '.5rem'
                  }}>
                    <Alert variant="danger" onClose={() => { this.setState({ errorDismissed: true }) }} dismissible>
                      <Alert.Heading>Oops Failed to delete!</Alert.Heading>
                      <p>
                        {this.state.commonValidationMessage}
                      </p>
                    </Alert>
                  </div>
                )}

                {!this.state.successDismissed && (
                  <div style={{
                    cursor: 'pointer',
                    margin: '5px',
                    backgroundColor: 'white',
                    borderRadius: '.5rem'
                  }}>
                    <Alert variant="success" onClose={() => { this.setState({ successDismissed: true }) }} dismissible>
                      <Alert.Heading>Success</Alert.Heading>
                      <p>
                        {this.state.commonValidationMessage}
                      </p>
                    </Alert>
                  </div>
                )}
              </Card.Body>
              <Card.Footer>
                <Pagination
                  currentPage={currentPage}
                  itemsCount={totalCount}
                  pageSize={pageSize}
                  firstPage={firstPage}
                  lastPage={lastPage}
                  onPageChange={this.handlePageChange}
                />
              </Card.Footer>
            </Card>
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}

export default TranslationsManager;
