import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/brands";

function brandUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5, fromDate, toDate, status, searchToken) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size 
	+ "&fromDate=" + fromDate + "&toDate=" + toDate + "&status=" + status + "&searchToken=" + searchToken, headerOptions);
}

export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(brandId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(brandUrl(brandId), options);
}


export function save(datObj) {
	return datObj.name != null ? update(datObj) : create(datObj);
}


export function create(brand) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, brand, options);
}

export function update(brand) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, brand, options);
}

export function remove(brandId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(brandUrl(brandId), options);
}

