import React, { useState } from "react";
import { Form, InputGroup } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faEyeSlash } from "@fortawesome/free-solid-svg-icons";

function PasswordInputWithToggle({passwordLabel, passwordPlaceholderLabel}) {
  const [passwordVisible, setPasswordVisible] = useState(false);

  const togglePasswordVisibility = () => {
    setPasswordVisible(!passwordVisible);
  };

  return (
    <Form.Group className="row" controlId="formBasicPassword">
      <Form.Label>{passwordLabel}</Form.Label>
      <InputGroup>
        <Form.Control
          onFocus={() => {
            // this.setState({ userLoginMessage: "" });
          }}
          type={passwordVisible ? "text" : "password"}
          name="password"
          placeholder={passwordPlaceholderLabel}
        />
        <InputGroup.Text onClick={togglePasswordVisibility} style={{ cursor: "pointer" }}>
          <FontAwesomeIcon icon={passwordVisible ? faEyeSlash : faEye} />
        </InputGroup.Text>
      </InputGroup>
    </Form.Group>
  );
}

export default PasswordInputWithToggle;
