import React, { Component } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";

class Contact extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <Container id="contact" classname="container-fluid bg-grey">
          <h2 classname="text-center">CONTACT</h2>
          <Row classname="row">
            <Col classname="col-sm-5">
              <p>Contact us and we'll get back to you within 24 hours.</p>
              <p>
                <span classname="glyphicon glyphicon-map-marker"></span>{" "}
                Chicago, US
              </p>
              <p>
                <span classname="glyphicon glyphicon-phone"></span> +00
                1515151515
              </p>
              <p>
                <span classname="glyphicon glyphicon-envelope"></span>{" "}
                myemail@something.com
              </p>
            </Col>
            <Col classname="col-sm-7 slideanim">
              <Row classname="row">
                <Col classname="col-sm-6 form-group">
                <Form.Group controlId="name">
                    <Form.Label>Name</Form.Label>
                    <Form.Control type="email" placeholder="Enter name" />
                    <Form.Text className="text-muted">
                      Full name
                    </Form.Text>
                  </Form.Group>
                  
                </Col>
                <Col classname="col-sm-6 form-group">
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" placeholder="Enter email" />
                    <Form.Text className="text-muted">
                      We'll never share your email with anyone else.
                    </Form.Text>
                  </Form.Group>
                </Col>
              </Row>
              <Form.Group controlId="comments">
                    <Form.Label>Comment</Form.Label>
                    <Form.Control type="textarea" placeholder="Enter comments" />
                    <Form.Text className="text-muted">
                      Enter details
                    </Form.Text>
                  </Form.Group>
              <br />
              <Row classname="row">
                <Col classname="col-sm-12 form-group">
                  <button classname="btn btn-default pull-right" type="submit">
                    Send
                  </button>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default Contact;
