import React from "react";
import UxCrudForm from "../../common/UxCrudForm";
import { save, get } from "./translationsService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
// import FileBase64 from "react-file-base64";

class TranslationsForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["TRANSLATIONS_CREATE", "TRANSLATIONS_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/translationss/",
    userPermissions: [],
    data: {
      id: null,
      groupName: "i18n",
      keyName: "",
      languageCode: "",
      englishTextValue: "",
      banglaTextValue: "",
      description: "",
    },
    errors: {},
    translations: {
      "titleLabel": "Translations",
      "homeLabel": "Home",
      "cancelLabel": "Cancel",
      "saveLabel": "Save",
      "languageCodeLabel": "Language Code",
      "languageCodePlaceholder": "Enter Language Code",
      "groupNameLabel": "Group Name",
      "groupNamePlaceholder": "Enter Group Name",
      "keyNameLabel": "Key Name",
      "keyNamePlaceholder": "Enter Key Name",
      "valueTextLabel": "Value Text",
      "englishTextValueLabel": "English Label",
      "banglaTextValueLabel": "Bangla Label",
      "valueTextPlaceholder": "Enter Value Text",
      "descriptionLabel": "Description",
      "descriptionPlaceholder": "Enter Description",
    },
    validationMessage: {
      groupName: "",
      keyName: "",
      englishTextValue: "",
      banglaTextValue: "",
    },
    commonValidationMessage: "",
  };

  /* schema = {
    id: Joi.number().allow(null),
    languageCode: Joi.string().required().label("Language"),groupName: Joi.string().required().label("Group Name"),keyName: Joi.string().required().label("Key"),valueText: Joi.string().label("Value Text"),description: Joi.string().label("Description"),
  };
*/
  async populateData() {
    try {

      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
        this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          titleLabel: translationsObj["i18n.translations.titleLabel"]
            ? translationsObj["i18n.translations.titleLabel"]
            : thisContext.state.translations.titleLabel,

            viewLabel: translationsObj["i18n.common.viewLabel"]
              ? translationsObj["i18n.common.viewLabel"]
              : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

 deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          languageCodeLabel: translationsObj["i18n.translations.languageCode.label"]
            ? translationsObj["i18n.translations.languageCode.label"]
            : thisContext.state.translations.languageCodeLabel,
          groupNameLabel: translationsObj["i18n.translations.groupName.label"]
            ? translationsObj["i18n.translations.groupName.label"]
            : thisContext.state.translations.groupNameLabel,

          keyNameLabel: translationsObj["i18n.translations.keyName.label"]
            ? translationsObj["i18n.translations.keyName.label"]
            : thisContext.state.translations.keyNameLabel,


          valueTextLabel: translationsObj["i18n.translations.valueText.label"]
            ? translationsObj["i18n.translations.valueText.label"]
            : thisContext.state.translations.valueTextLabel,

          englishTextValueLabel: translationsObj["i18n.translations.englishValueText.label"]
            ? translationsObj["i18n.translations.englishValueText.label"]
            : thisContext.state.translations.englishTextValueLabel,

          banglaTextValueLabel: translationsObj["i18n.translations.banglaValueText.label"]
            ? translationsObj["i18n.translations.banglaValueText.label"]
            : thisContext.state.translations.banglaTextValueLabel,

          descriptionLabel: translationsObj["i18n.translations.description.label"]
            ? translationsObj["i18n.translations.description.label"]
            : thisContext.state.translations.descriptionLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    try {
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      groupName: obj.groupName,
      keyName: obj.keyName,
      valueText: obj.valueText,
      description: obj.description,
      banglaTextValue: obj.banglaTextValue,
      englishTextValue: obj.englishTextValue
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status === "FAILED" ||
        response.data.validationMessages !== undefined
      ) {
        if (response.data.validationMessages !== undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message !== undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="pt-2 pb-2">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black mr-3 mb-3">
                  <Card.Title className="card-form-title-light">
                    <Row>
                      <Col md='1'>
                        {
                          this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                            e.preventDefault();
                            thisContext.props.history.push(thisContext.state.basicUrl);
                          })
                        }
                      </Col>
                      <Col md='11'>
                        <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                      </Col>
                    </Row>
                  </Card.Title>
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <Row>
                      {this.renderInput(false, false, "id", "Id", "hidden")}
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "groupName",
                          this.state.translations.groupNameLabel,
                          this.state.translations.groupNameLabel,
                          "text",
                          (data.id !== null),
                          this.state.validationMessage.groupName
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "keyName",
                          this.state.translations.keyNameLabel,
                          this.state.translations.keyNameLabel,
                          "text",
                          (data.id !== null),
                          this.state.validationMessage.keyName
                        )}
                      </Col>
                      </Row>
                      <Row>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "englishTextValue",
                          this.state.translations.englishTextValueLabel,
                          this.state.translations.englishTextValueLabel,
                          "text",
                          false,
                          this.state.validationMessage.englishTextValue
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "banglaTextValue",
                          this.state.translations.banglaTextValueLabel,
                          this.state.translations.banglaTextValueLabel,
                          "text",
                          false,
                          this.state.validationMessage.banglaTextValue
                        )}
                      </Col>

                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </Row>
                  </Card.Body>
                  {thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default TranslationsForm;
