import React from "react";
import styled from "styled-components";
import companyLogo from "../../../images/companyLogo.png";
import vendorLogo from "../../../images/vendorLogo.png";
import { Col, Row } from "react-bootstrap";
import properties from '../../../common_properties.json';

const Styles = styled.div`
  .navbar {
    background-color: skyblue;
    color: black;
  }
  a,
  .navbar-brand,
  .navbar-nav .nav-link {
    color: black;
    &:hover {
      color: gray;
      background-color: white;
    }
  }
  body {
    padding-top: 0px;
  }

footer {
  flex-shrink: 0;
  background-color: #f8f9fa;
  padding: 1rem;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1);
  font-size: 10px;
}
  .footer {
    background-color: #E9ECEF;
    padding: 3px 0;
  }
  .footer-content {
    text-align: center;
    line-height: 1.5;
  }
  .footer-content img {
    vertical-align: left;
    text-align: left;
    margin-left: 0px;
  }
    

`;

class PublicFooter extends React.Component {
  state = {
    companyName: properties.labels.languages.en.company_name,
    companyWebsiteUrl: properties.labels.languages.en.company_website_url,
    vendorName: properties.labels.languages.en.vendor_name,
    vendorWebsiteUrl: properties.labels.languages.en.vendor_website_url,
    translations: {
      footerMessageCopyright: "Copyright",
      footerMessagePoweredByLabel: "Powered by",
      footerMessageAllRightsReservedLabel: "All rights reserved"
    }
  };

  async componentDidMount() {
    await this.loadTranslations();
  }

  reloadTranslations = (updatedLanguage) => {
    this.loadTranslations();
  };

  hideAlertModal() {
    this.setState({
      alertShow: false,
      alertMessage: "",
      alertDetailsMessage: "",
    });
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          footerMessageCopyright: translationsObj["i18n.common.footerMessageCopyright"]
            ? translationsObj["i18n.common.footerMessageCopyright"]
            : thisContext.state.translations.footerMessageCopyright,
          footerMessagePoweredByLabel: translationsObj["i18n.common.footerMessagePoweredByLabel"]
            ? translationsObj["i18n.common.footerMessagePoweredByLabel"]
            : thisContext.state.translations.footerMessagePoweredByLabel,
          footerMessageAllRightsReservedLabel: translationsObj["i18n.common.footerMessageAllRightsReservedLabel"]
            ? translationsObj["i18n.common.footerMessageAllRightsReservedLabel"]
            : thisContext.state.translations.footerMessageAllRightsReservedLabel,
        }
      });
    } else {
      // console.log("Alert!!!, Translations not found!");
    }
  }

  render() {
    // const { isLoggedIn } = this.props;
    return (
      <Styles>
        <footer className="footer">
          <div className="container">
            <div className="footer-content">
              <Row>
                <Col md='6' className="text-left">
                  <b>
                    {this.state.translations.footerMessageCopyright} &copy; {new Date().getFullYear()}{" "}
                    <a target="_blank" href={this.state.companyWebsiteUrl}>
                      <img style={{ height: "15px" }} src={companyLogo} alt={this.state.companyName} />
                      &nbsp;{this.state.companyName}
                    </a>, {this.state.translations.footerMessageAllRightsReservedLabel}
                  </b>
                </Col>
                <Col>
                  <b>{this.state.translations.footerMessagePoweredByLabel} &nbsp;
                    <a target="_blank" href={this.state.vendorWebsiteUrl}>
                      <img style={{ height: "15px" }} src={vendorLogo} alt={this.state.vendorName} />
                    </a>
                  </b>
                </Col>
              </Row>
            </div>
          </div>
        </footer>
      </Styles>
    );
  }
}

export default PublicFooter;
