import React, { Component } from "react";
// import Table from "../common/table";
import { Button, Form, Modal } from 'react-bootstrap';
// import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../../common/UxCrudTable";

class AuthorityViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" size="sm" onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form>
              <div className="form-group row">
                <div className="col-md-6">
                  <b><label>Name</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.name}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Description</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.description}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Is Active</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.isActive}
                </div>
              </div>

              {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class AuthorityTable extends UxCrudTable {
  state = {
    DATA_CREATE: "AUTHORITY_CREATE",
    DATA_READ: "AUTHORITY_READ",
    DATA_UPDATE: "AUTHORITY_UPDATE",
    DATA_DELETE: "AUTHORITY_DELETE",
    DATA_APPROVE: "AUTHORITY_APPROVE",
    userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    title: "Authority Details",
    translations: {
      actionsLabel: "Actions",
      idLabel: "Id", viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
     deleteLabel: "Delete",
      nameLabel: "Name",
      descriptionLabel: "Description",
      isActiveLabel: "Is Active?",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          idLabel: translationsObj["i18n.common.idLabel"]
            ? translationsObj["i18n.common.idLabel"]
            : thisContext.state.translations.idLabel,

          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

 deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          nameLabel: translationsObj[
            "i18n.authority.name.label"
          ]
            ? translationsObj["i18n.authority.name.label"]
            : thisContext.state.translations.nameLabel,

          descriptionLabel: translationsObj[
            "i18n.authority.description.label"
          ]
            ? translationsObj["i18n.authority.description.label"]
            : thisContext.state.translations.descriptionLabel,
          isActiveLabel: translationsObj[
            "i18n.authority.isActive.label"
          ]
            ? translationsObj["i18n.authority.isActive.label"]
            : thisContext.state.translations.isActiveLabel,
        },
      });
    } else {
      // console.log("Alert!!!, Translations not found!");
    }
  }

  handleDeleteModalShow = (item) => {
    const filteredDatas = this.props.data.filter(function (obj) {
      return obj.name === item.name;
    });

    let preparedObj = {};
    if (filteredDatas && filteredDatas.length > 0) {
      preparedObj = filteredDatas[0];
      this.setState({
        item: preparedObj,
        showView: false,
        showDeleteConfirm: true,
      });

    } else {
      alert('item not found!');
    }
  };

  async loadColumns() {
    this.columns = [
      {
        path: "name",
        label: this.state.translations.nameLabel,
      }, {
        path: "description",
        label: this.state.translations.descriptionLabel,
      }, {
        path: "isActive",
        label: this.state.translations.isActiveLabel,
      },
    ];

    this.loadActionColumns();
    this.setState({ columns: this.columns });
  }

}

export default AuthorityTable;
