import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./websiteInfoService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';


class WebsiteInfoForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["WEBSITE_INFO_CREATE","WEBSITE_INFO_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/websiteInfos/",
	userPermissions: [],
    data: { id: null , companyName: "", email: "", contactNumber: "", streetAddress: "", websiteUrl: "", vendorName: "", vendorWebsiteUrl: "", linkedinUrl: "", facebookUrl: "", twitterUrl: "", pinterestUrl: "" },
    errors: {},
    validationMessage: {
      companyName: '',
email: '',
contactNumber: '',
streetAddress: '',
vendorName: '',
vendorWebsiteUrl: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Website Info",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	companyNameLabel: "Company Name",
companyNamePlaceholder: "Enter Company Name",
emailLabel: "Email",
emailPlaceholder: "Enter Email",
contactNumberLabel: "Contact Number",
contactNumberPlaceholder: "Enter Contact Number",
streetAddressLabel: "Street Address",
streetAddressPlaceholder: "Enter Street Address",
websiteUrlLabel: "Website URL",
websiteUrlPlaceholder: "Enter Website URL",
vendorNameLabel: "Vendor Name",
vendorNamePlaceholder: "Enter Vendor Name",
vendorWebsiteUrlLabel: "Vendor Website URL",
vendorWebsiteUrlPlaceholder: "Enter Vendor Website URL",
linkedinUrlLabel: "Linkedin URL",
linkedinUrlPlaceholder: "Enter Linkedin URL",
facebookUrlLabel: "Facebook URL",
facebookUrlPlaceholder: "Enter Facebook URL",
twitterUrlLabel: "Twitter URL",
twitterUrlPlaceholder: "Enter Twitter URL",
pinterestUrlLabel: "Pinterest URL",
pinterestUrlPlaceholder: "Enter Pinterest URL",

    },
	  
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    companyName: Joi.string().required().label({this.state.translations.companyNameLabel}),email: Joi.string().required().label({this.state.translations.emailLabel}),contactNumber: Joi.string().required().label({this.state.translations.contactNumberLabel}),streetAddress: Joi.string().required().label({this.state.translations.streetAddressLabel}),websiteUrl: Joi.string().label({this.state.translations.websiteUrlLabel}),vendorName: Joi.string().required().label({this.state.translations.vendorNameLabel}),vendorWebsiteUrl: Joi.string().required().label({this.state.translations.vendorWebsiteUrlLabel}),linkedinUrl: Joi.string().label({this.state.translations.linkedinUrlLabel}),facebookUrl: Joi.string().label({this.state.translations.facebookUrlLabel}),twitterUrl: Joi.string().label({this.state.translations.twitterUrlLabel}),pinterestUrl: Joi.string().label({this.state.translations.pinterestUrlLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      
      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.websiteInfo.titleLabel"] ? translationsObj["i18n.websiteInfo.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          companyNameLabel: translationsObj["i18n.websiteInfo.companyName.label"] ? translationsObj["i18n.websiteInfo.companyName.label"] : thisContext.state.translations.companyNameLabel,
companyNamePlaceholder: translationsObj["i18n.websiteInfo.companyName.placeholder"] ? translationsObj["i18n.websiteInfo.companyName.placeholder"] : thisContext.state.translations.companyNamePlaceholder,
emailLabel: translationsObj["i18n.websiteInfo.email.label"] ? translationsObj["i18n.websiteInfo.email.label"] : thisContext.state.translations.emailLabel,
emailPlaceholder: translationsObj["i18n.websiteInfo.email.placeholder"] ? translationsObj["i18n.websiteInfo.email.placeholder"] : thisContext.state.translations.emailPlaceholder,
contactNumberLabel: translationsObj["i18n.websiteInfo.contactNumber.label"] ? translationsObj["i18n.websiteInfo.contactNumber.label"] : thisContext.state.translations.contactNumberLabel,
contactNumberPlaceholder: translationsObj["i18n.websiteInfo.contactNumber.placeholder"] ? translationsObj["i18n.websiteInfo.contactNumber.placeholder"] : thisContext.state.translations.contactNumberPlaceholder,
streetAddressLabel: translationsObj["i18n.websiteInfo.streetAddress.label"] ? translationsObj["i18n.websiteInfo.streetAddress.label"] : thisContext.state.translations.streetAddressLabel,
streetAddressPlaceholder: translationsObj["i18n.websiteInfo.streetAddress.placeholder"] ? translationsObj["i18n.websiteInfo.streetAddress.placeholder"] : thisContext.state.translations.streetAddressPlaceholder,
websiteUrlLabel: translationsObj["i18n.websiteInfo.websiteUrl.label"] ? translationsObj["i18n.websiteInfo.websiteUrl.label"] : thisContext.state.translations.websiteUrlLabel,
websiteUrlPlaceholder: translationsObj["i18n.websiteInfo.websiteUrl.placeholder"] ? translationsObj["i18n.websiteInfo.websiteUrl.placeholder"] : thisContext.state.translations.websiteUrlPlaceholder,
vendorNameLabel: translationsObj["i18n.websiteInfo.vendorName.label"] ? translationsObj["i18n.websiteInfo.vendorName.label"] : thisContext.state.translations.vendorNameLabel,
vendorNamePlaceholder: translationsObj["i18n.websiteInfo.vendorName.placeholder"] ? translationsObj["i18n.websiteInfo.vendorName.placeholder"] : thisContext.state.translations.vendorNamePlaceholder,
vendorWebsiteUrlLabel: translationsObj["i18n.websiteInfo.vendorWebsiteUrl.label"] ? translationsObj["i18n.websiteInfo.vendorWebsiteUrl.label"] : thisContext.state.translations.vendorWebsiteUrlLabel,
vendorWebsiteUrlPlaceholder: translationsObj["i18n.websiteInfo.vendorWebsiteUrl.placeholder"] ? translationsObj["i18n.websiteInfo.vendorWebsiteUrl.placeholder"] : thisContext.state.translations.vendorWebsiteUrlPlaceholder,
linkedinUrlLabel: translationsObj["i18n.websiteInfo.linkedinUrl.label"] ? translationsObj["i18n.websiteInfo.linkedinUrl.label"] : thisContext.state.translations.linkedinUrlLabel,
linkedinUrlPlaceholder: translationsObj["i18n.websiteInfo.linkedinUrl.placeholder"] ? translationsObj["i18n.websiteInfo.linkedinUrl.placeholder"] : thisContext.state.translations.linkedinUrlPlaceholder,
facebookUrlLabel: translationsObj["i18n.websiteInfo.facebookUrl.label"] ? translationsObj["i18n.websiteInfo.facebookUrl.label"] : thisContext.state.translations.facebookUrlLabel,
facebookUrlPlaceholder: translationsObj["i18n.websiteInfo.facebookUrl.placeholder"] ? translationsObj["i18n.websiteInfo.facebookUrl.placeholder"] : thisContext.state.translations.facebookUrlPlaceholder,
twitterUrlLabel: translationsObj["i18n.websiteInfo.twitterUrl.label"] ? translationsObj["i18n.websiteInfo.twitterUrl.label"] : thisContext.state.translations.twitterUrlLabel,
twitterUrlPlaceholder: translationsObj["i18n.websiteInfo.twitterUrl.placeholder"] ? translationsObj["i18n.websiteInfo.twitterUrl.placeholder"] : thisContext.state.translations.twitterUrlPlaceholder,
pinterestUrlLabel: translationsObj["i18n.websiteInfo.pinterestUrl.label"] ? translationsObj["i18n.websiteInfo.pinterestUrl.label"] : thisContext.state.translations.pinterestUrlLabel,
pinterestUrlPlaceholder: translationsObj["i18n.websiteInfo.pinterestUrl.placeholder"] ? translationsObj["i18n.websiteInfo.pinterestUrl.placeholder"] : thisContext.state.translations.pinterestUrlPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("WEBSITE_INFO_CREATE") ||
      thisContext.state.userPermissions.includes("WEBSITE_INFO_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      companyName: obj.companyName,email: obj.email,contactNumber: obj.contactNumber,streetAddress: obj.streetAddress,websiteUrl: obj.websiteUrl,vendorName: obj.vendorName,vendorWebsiteUrl: obj.vendorWebsiteUrl,linkedinUrl: obj.linkedinUrl,facebookUrl: obj.facebookUrl,twitterUrl: obj.twitterUrl,pinterestUrl: obj.pinterestUrl,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-light">
                <Row>
                  <Col md='1'>
                    {
                      this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                        e.preventDefault();
                        thisContext.props.history.push(thisContext.state.basicUrl);
                      })
                    }
                  </Col>
                  <Col md='11'>
                    <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                  </Col>
                </Row>
              </Card.Title>
			{/*<Card.Header>
				<p>Hello Website Info please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}

                {this.renderInput(false,false,"id", "Id", "hidden")}
                <Row><Col>{this.renderInput(true,true,"companyName", this.state.translations.companyNameLabel,this.state.translations.companyNamePlaceholder  ,"text",false,this.state.validationMessage.companyName)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"email", this.state.translations.emailLabel,this.state.translations.emailPlaceholder  ,"text",false,this.state.validationMessage.email)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"contactNumber", this.state.translations.contactNumberLabel,this.state.translations.contactNumberPlaceholder  ,"text",false,this.state.validationMessage.contactNumber)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"streetAddress", this.state.translations.streetAddressLabel,this.state.translations.streetAddressPlaceholder  ,"textarea",false,this.state.validationMessage.streetAddress)}</Col></Row>
<Row><Col>{this.renderInput(false,true,"websiteUrl", this.state.translations.websiteUrlLabel,this.state.translations.websiteUrlPlaceholder  ,"text",false,this.state.validationMessage.websiteUrl)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"vendorName", this.state.translations.vendorNameLabel,this.state.translations.vendorNamePlaceholder  ,"text",false,this.state.validationMessage.vendorName)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"vendorWebsiteUrl", this.state.translations.vendorWebsiteUrlLabel,this.state.translations.vendorWebsiteUrlPlaceholder  ,"text",false,this.state.validationMessage.vendorWebsiteUrl)}</Col></Row>
<Row><Col>{this.renderInput(false,false,"linkedinUrl", this.state.translations.linkedinUrlLabel,this.state.translations.linkedinUrlPlaceholder  ,"text",false,this.state.validationMessage.linkedinUrl)}</Col></Row>
<Row><Col>{this.renderInput(false,false,"facebookUrl", this.state.translations.facebookUrlLabel,this.state.translations.facebookUrlPlaceholder  ,"text",false,this.state.validationMessage.facebookUrl)}</Col></Row>
<Row><Col>{this.renderInput(false,false,"twitterUrl", this.state.translations.twitterUrlLabel,this.state.translations.twitterUrlPlaceholder  ,"text",false,this.state.validationMessage.twitterUrl)}</Col></Row>
<Row><Col>{this.renderInput(false,false,"pinterestUrl", this.state.translations.pinterestUrlLabel,this.state.translations.pinterestUrlPlaceholder  ,"text",false,this.state.validationMessage.pinterestUrl)}</Col></Row>

                {/* {this.renderInput("dueDate", "Due Date")} */}

                
            </Card.Body>

				{thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
          		</Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default WebsiteInfoForm;
