import React, { useState, useEffect } from "react";
import Select from "react-select";

const UxSelect = ({
  isRequired,
  isMultiple,
  name,
  value,
  label,
  placeholder,
  options,
  validationMessage,
  selectedOption,
  error,
  updateSelected,
  ...rest
}) => {
  const requiredHtml = isRequired ? (
    <span style={{ color: "red" }}>*</span>
  ) : (
    ""
  );
  const customFontStyle = {
    fontFamily: "Diodrum Arabic Semibold, sans-serif",
    fontWeight: "5",
  };

  const [selectedValue, setSelectedValue] = useState({});
  const [selectedValues, setSelectedValues] = useState([]);
  const [inputValidationMessage, setInputValidationMessage] = useState('');

  const getOptionById = (value) => {
    return options.filter(option => option.value === value);
  }

  const inputClass = (isRequired && inputValidationMessage && inputValidationMessage != '')
    ? 'react-select-container border border-danger' : 'react-select-container';

  useEffect(() => {
    if (Array.isArray(value)) {
      let selectedOptions = [];
      value.forEach(selecectedOptionValue => {
        // console.log(selecectedOptionValue);
        let option = getOptionById(selecectedOptionValue);
        if (option.length === 1) {
          selectedOptions.push(option[0]);
        }
      });
      setSelectedValues(selectedOptions);
      setInputValidationMessage(validationMessage);
    } else {
      let selectedLabel = "";
      options.forEach(option => {
        if (option.value === value) {
          selectedLabel = option.label;
        }
      });
      setSelectedValue({ label: selectedLabel, value: value });
      setInputValidationMessage(validationMessage);
    }

  }, []);

  useEffect(() => {
    setInputValidationMessage(validationMessage);
  }, [validationMessage]);

  const handleMultiOptionChange = (selectedOptions) => {
    setSelectedValues(selectedOptions);
    let values = [];
    selectedOptions.forEach(selectedOption => {
      values.push(selectedOption.value);
    });
    updateSelected(name, values);
    if (values.length > 0) {
      setInputValidationMessage('');
    }

  };

  const handleSingleOptionChange = (selectedOption) => {
    setSelectedValue(selectedOption);
    updateSelected(name, selectedOption.value);
    setInputValidationMessage('');
  };


  return (
    <>
        {isMultiple === true && (<Select
          id={name}
          name={name}
          isMulti
          options={options}
          value={selectedValues}
          placeholder={"Please Select"}
          className={inputClass}
          classNamePrefix="react-select"
          onChange={handleMultiOptionChange}
          {...rest}
        />
        )}

        {isMultiple === false && (<Select
          id={name}
          name={name}
          options={options}
          value={selectedValue}
          placeholder={"Please Select"}
          className={inputClass}
          classNamePrefix="react-select"
          onChange={handleSingleOptionChange}
          {...rest}
        />
        )}
    </>
  );
};

export default UxSelect;