import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./productService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';
import { getAll as getAllSupplier } from "../supplier/supplierService";import { getAll as getAllCategory } from "../category/categoryService";import { getAll as getAllSubCategory } from "../subCategory/subCategoryService";
import { getAll as getAllVariant } from "../variant/variantService";

class ProductForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["PRODUCT_CREATE","PRODUCT_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/products/",
	userPermissions: [],
    data: { id: null , supplierId: "", categoryId: "", subCategoryId: "", title: "", defaultImage: "", productImages: [{productImage:"",name:"",}], productPrices: [{fromDate:"",toDate:"",price:"",}], productVariants: [{variantId:"",}], currentPrice: "", previousPrice: "", description: "", specification: "", remarks: "" },
    errors: {},
    validationMessage: {
      supplier: '',
category: '',
subCategory: '',
title: '',
defaultImage: '',
description: '',
specification: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Product",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	supplierIdLabel: "Supplier",
supplierIdPlaceholder: "Select Supplier",
categoryIdLabel: "Category",
categoryIdPlaceholder: "Select Category",
subCategoryIdLabel: "Sub Category ",
subCategoryIdPlaceholder: "Select Sub Category ",
titleLabel: "Product Title",
titlePlaceholder: "Enter Product Title",
defaultImageLabel: "Default Image",
defaultImagePlaceholder: "Enter Default Image",
productImageLabel: "Product Image",
productImagePlaceholder: "Create Product Image",
productPriceLabel: "Product Image",
productPricePlaceholder: "Create Product Image",
productVariantLabel: "Product Variant",
productVariantPlaceholder: "Create Product Variant",
currentPriceLabel: "Current Price",
currentPricePlaceholder: "Enter Current Price",
previousPriceLabel: "Previous Price",
previousPricePlaceholder: "Enter Previous Price",
descriptionLabel: "Product Description",
descriptionPlaceholder: "Enter Product Description",
specificationLabel: "Product Specification",
specificationPlaceholder: "Enter Product Specification",
remarksLabel: "Remarks",
remarksPlaceholder: "Enter Remarks",

    },
	  supplierOptions : [],categoryOptions : [],subCategoryOptions : [],productImageOptions : [],productPriceOptions : [],variantOptions : [],productVariantOptions : [],
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    supplier: Joi.number().label("Supplier"),category: Joi.number().label("Category"),subCategory: Joi.number().label("Sub Category"),title: Joi.string().required().label({this.state.translations.titleLabel}),defaultImage: Joi.string().required().label({this.state.translations.defaultImageLabel}),currentPrice: Joi.string().label({this.state.translations.currentPriceLabel}),previousPrice: Joi.string().label({this.state.translations.previousPriceLabel}),description: Joi.string().required().label({this.state.translations.descriptionLabel}),specification: Joi.string().required().label({this.state.translations.specificationLabel}),remarks: Joi.string().label({this.state.translations.remarksLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      await getAllSupplier().then(function (result) {
        let supplierOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          supplierOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ supplierOptions: supplierOptions });
      });
await getAllCategory().then(function (result) {
        let categoryOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          categoryOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ categoryOptions: categoryOptions });
      });
await getAllSubCategory().then(function (result) {
        let subCategoryOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          subCategoryOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ subCategoryOptions: subCategoryOptions });
      });
await getAllVariant().then(function (result) {
        let variantOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          variantOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ variantOptions: variantOptions });
      });

      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.product.titleLabel"] ? translationsObj["i18n.product.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          supplierIdLabel: translationsObj["i18n.product.supplierId.label"] ? translationsObj["i18n.product.supplierId.label"] : thisContext.state.translations.supplierIdLabel,
supplierIdPlaceholder: translationsObj["i18n.product.supplierId.placeholder"] ? translationsObj["i18n.product.supplierId.placeholder"] : thisContext.state.translations.supplierIdPlaceholder,
categoryIdLabel: translationsObj["i18n.product.categoryId.label"] ? translationsObj["i18n.product.categoryId.label"] : thisContext.state.translations.categoryIdLabel,
categoryIdPlaceholder: translationsObj["i18n.product.categoryId.placeholder"] ? translationsObj["i18n.product.categoryId.placeholder"] : thisContext.state.translations.categoryIdPlaceholder,
subCategoryIdLabel: translationsObj["i18n.product.subCategoryId.label"] ? translationsObj["i18n.product.subCategoryId.label"] : thisContext.state.translations.subCategoryIdLabel,
subCategoryIdPlaceholder: translationsObj["i18n.product.subCategoryId.placeholder"] ? translationsObj["i18n.product.subCategoryId.placeholder"] : thisContext.state.translations.subCategoryIdPlaceholder,
titleLabel: translationsObj["i18n.product.title.label"] ? translationsObj["i18n.product.title.label"] : thisContext.state.translations.titleLabel,
titlePlaceholder: translationsObj["i18n.product.title.placeholder"] ? translationsObj["i18n.product.title.placeholder"] : thisContext.state.translations.titlePlaceholder,
defaultImageLabel: translationsObj["i18n.product.defaultImage.label"] ? translationsObj["i18n.product.defaultImage.label"] : thisContext.state.translations.defaultImageLabel,
defaultImagePlaceholder: translationsObj["i18n.product.defaultImage.placeholder"] ? translationsObj["i18n.product.defaultImage.placeholder"] : thisContext.state.translations.defaultImagePlaceholder,
productImageLabel: translationsObj["i18n.product.productImage.label"] ? translationsObj["i18n.product.productImage.label"] : thisContext.state.translations.productImageLabel,
productImagePlaceholder: translationsObj["i18n.product.productImage.placeholder"] ? translationsObj["i18n.product.productImage.placeholder"] : thisContext.state.translations.productImagePlaceholder,
productPriceLabel: translationsObj["i18n.product.productPrice.label"] ? translationsObj["i18n.product.productPrice.label"] : thisContext.state.translations.productPriceLabel,
productPricePlaceholder: translationsObj["i18n.product.productPrice.placeholder"] ? translationsObj["i18n.product.productPrice.placeholder"] : thisContext.state.translations.productPricePlaceholder,
productVariantLabel: translationsObj["i18n.product.productVariant.label"] ? translationsObj["i18n.product.productVariant.label"] : thisContext.state.translations.productVariantLabel,
productVariantPlaceholder: translationsObj["i18n.product.productVariant.placeholder"] ? translationsObj["i18n.product.productVariant.placeholder"] : thisContext.state.translations.productVariantPlaceholder,
currentPriceLabel: translationsObj["i18n.product.currentPrice.label"] ? translationsObj["i18n.product.currentPrice.label"] : thisContext.state.translations.currentPriceLabel,
currentPricePlaceholder: translationsObj["i18n.product.currentPrice.placeholder"] ? translationsObj["i18n.product.currentPrice.placeholder"] : thisContext.state.translations.currentPricePlaceholder,
previousPriceLabel: translationsObj["i18n.product.previousPrice.label"] ? translationsObj["i18n.product.previousPrice.label"] : thisContext.state.translations.previousPriceLabel,
previousPricePlaceholder: translationsObj["i18n.product.previousPrice.placeholder"] ? translationsObj["i18n.product.previousPrice.placeholder"] : thisContext.state.translations.previousPricePlaceholder,
descriptionLabel: translationsObj["i18n.product.description.label"] ? translationsObj["i18n.product.description.label"] : thisContext.state.translations.descriptionLabel,
descriptionPlaceholder: translationsObj["i18n.product.description.placeholder"] ? translationsObj["i18n.product.description.placeholder"] : thisContext.state.translations.descriptionPlaceholder,
specificationLabel: translationsObj["i18n.product.specification.label"] ? translationsObj["i18n.product.specification.label"] : thisContext.state.translations.specificationLabel,
specificationPlaceholder: translationsObj["i18n.product.specification.placeholder"] ? translationsObj["i18n.product.specification.placeholder"] : thisContext.state.translations.specificationPlaceholder,
remarksLabel: translationsObj["i18n.product.remarks.label"] ? translationsObj["i18n.product.remarks.label"] : thisContext.state.translations.remarksLabel,
remarksPlaceholder: translationsObj["i18n.product.remarks.placeholder"] ? translationsObj["i18n.product.remarks.placeholder"] : thisContext.state.translations.remarksPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("PRODUCT_CREATE") ||
      thisContext.state.userPermissions.includes("PRODUCT_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      supplierId: obj.supplierId,categoryId: obj.categoryId,subCategoryId: obj.subCategoryId,title: obj.title,defaultImage: obj.defaultImage,productImages: obj.productImages,productPrices: obj.productPrices,productVariants: obj.productVariants,currentPrice: obj.currentPrice,previousPrice: obj.previousPrice,description: obj.description,specification: obj.specification,remarks: obj.remarks,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

    addProductImageRow = (e) => {
	   e.preventDefault();    let updatedProductImages = [...this.state.data.productImages, { productImage:"",name:"", }];
    this.setState({
      data: {
        ...this.state.data, productImages: updatedProductImages
      }
    });
    return;
  };

  handleProductImageDelete = (e,index) => {
  e.preventDefault();
    const updatedProductImages = [...this.state.data.productImages];
    updatedProductImages.splice(index, 1);
    // this.state.data.productImages = updatedProductImages;
this.setState({
      data: {
        ...this.state.data, productImages: updatedProductImages
      }
    });
return;
  };

  handleProductImageMultiRowChange = (index, e) => {
    const { name, value } = e.target;
    const updatedProductImages = [...this.state.data.productImages];
    updatedProductImages[index][name] = value;
    //this.state.data.productImages = updatedProductImages;
    this.setState({
      productImages: updatedProductImages
    });
    return;
  };
  handleProductImageMultiRowSelectChange = (index, name, value) => {
    const updatedProductImages = [...this.state.data.productImages];
    updatedProductImages[index][name] = value;
    this.setState({
      productVariants: updatedProductImages
    });
    return;
  };  addProductPriceRow = (e) => {
	   e.preventDefault();    let updatedProductPrices = [...this.state.data.productPrices, { fromDate:"",toDate:"",price:"", }];
    this.setState({
      data: {
        ...this.state.data, productPrices: updatedProductPrices
      }
    });
    return;
  };

  handleProductPriceDelete = (e,index) => {
  e.preventDefault();
    const updatedProductPrices = [...this.state.data.productPrices];
    updatedProductPrices.splice(index, 1);
    // this.state.data.productPrices = updatedProductPrices;
this.setState({
      data: {
        ...this.state.data, productPrices: updatedProductPrices
      }
    });
return;
  };

  handleProductPriceMultiRowChange = (index, e) => {
    const { name, value } = e.target;
    const updatedProductPrices = [...this.state.data.productPrices];
    updatedProductPrices[index][name] = value;
    //this.state.data.productPrices = updatedProductPrices;
    this.setState({
      productPrices: updatedProductPrices
    });
    return;
  };
  handleProductPriceMultiRowSelectChange = (index, name, value) => {
    const updatedProductPrices = [...this.state.data.productPrices];
    updatedProductPrices[index][name] = value;
    this.setState({
      productVariants: updatedProductPrices
    });
    return;
  };  addProductVariantRow = (e) => {
	   e.preventDefault();    let updatedProductVariants = [...this.state.data.productVariants, { variantId:"", }];
    this.setState({
      data: {
        ...this.state.data, productVariants: updatedProductVariants
      }
    });
    return;
  };

  handleProductVariantDelete = (e,index) => {
  e.preventDefault();
    const updatedProductVariants = [...this.state.data.productVariants];
    updatedProductVariants.splice(index, 1);
    // this.state.data.productVariants = updatedProductVariants;
this.setState({
      data: {
        ...this.state.data, productVariants: updatedProductVariants
      }
    });
return;
  };

  handleProductVariantMultiRowChange = (index, e) => {
    const { name, value } = e.target;
    const updatedProductVariants = [...this.state.data.productVariants];
    updatedProductVariants[index][name] = value;
    //this.state.data.productVariants = updatedProductVariants;
    this.setState({
      productVariants: updatedProductVariants
    });
    return;
  };
  handleProductVariantMultiRowSelectChange = (index, name, value) => {
    const updatedProductVariants = [...this.state.data.productVariants];
    updatedProductVariants[index][name] = value;
    this.setState({
      productVariants: updatedProductVariants
    });
    return;
  };
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-light">
                <Row>
                  <Col md='1'>
                    {
                      this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                        e.preventDefault();
                        thisContext.props.history.push(thisContext.state.basicUrl);
                      })
                    }
                  </Col>
                  <Col md='11'>
                    <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                  </Col>
                </Row>
              </Card.Title>
			{/*<Card.Header>
				<p>Hello Product please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}

                {this.renderInput(false,false,"id", "Id", "hidden")}
                <Row><Col>{this.renderSelect(true,false,"supplierId", this.state.translations.supplierIdLabel, this.state.translations.supplierIdPlaceholder, thisContext.state.supplierOptions, this.state.validationMessage.supplierId)}</Col></Row>
<Row><Col>{this.renderSelect(true,false,"categoryId", this.state.translations.categoryIdLabel, this.state.translations.categoryIdPlaceholder, thisContext.state.categoryOptions, this.state.validationMessage.categoryId)}</Col></Row>
<Row><Col>{this.renderSelect(true,false,"subCategoryId", this.state.translations.subCategoryIdLabel, this.state.translations.subCategoryIdPlaceholder, thisContext.state.subCategoryOptions, this.state.validationMessage.subCategoryId)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"title", this.state.translations.titleLabel,this.state.translations.titlePlaceholder  ,"text",false,this.state.validationMessage.title)}</Col></Row>
<Row><Col>{this.renderImageInput(true,"defaultImage",this.state.translations.defaultImageLabel, this.state.translations.defaultImagePlaceholder, this.state.validationMessage.defaultImage)}</Col></Row><table className="table table-striped">
                      <thead>
                        <tr>
 <th>Product Image</th>
 <th>Name</th>
<th className="text-right"><button className="btn btn-sm btn-outline-success" onClick={this.addProductImageRow}><FontAwesomeIcon icon={faPlus}/> Product Image</button></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.data.productImages.map((productImage, index) => (
                          <tr key={index}>
<td className="row">
                        <div className="col-md-6">
                          <FileBase64
 multiple={false}
                            onDone={(file) => {
                              let data = thisContext.state.data;
                              data.productImages[index].productImage = file.base64;
                              thisContext.setState({ data: data });
                            }
                            } />

                        </div>
    <div className="col-md-6">
                          <img width={'120px'} height={'100px'} src={this.state.data.productImages[index].productImage} alt="Product Image" />
                          <span style={{ color: 'red' }}>*</span>
                        </div>
</td>
             <td>
                              <input
                                type="text" class="form-control" placeholder = "Enter Name"
                                name="name"
                                value={productImage.name}
                                onChange={(e) => this.handleProductImageMultiRowChange(index, e)}
                              />
                            </td>
                            <td className="text-right">
                              <button className="btn btn-sm btn-outline-danger" onClick={(e) => this.handleProductImageDelete(e,index)}><FontAwesomeIcon icon={faTrash}/></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
<table className="table table-striped">
                      <thead>
                        <tr>
 <th>From Date</th>
 <th>To Date</th>
 <th>Price</th>
<th className="text-right"><button className="btn btn-sm btn-outline-success" onClick={this.addProductPriceRow}><FontAwesomeIcon icon={faPlus}/> Product Price</button></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.data.productPrices.map((productPrice, index) => (
                          <tr key={index}>
             <td>
                              <input
                                type="date" class="form-control" placeholder = "Enter From Date"
                                name="fromDate"
                                value={productPrice.fromDate}
                                onChange={(e) => this.handleProductPriceMultiRowChange(index, e)}
                              />
                            </td>
             <td>
                              <input
                                type="date" class="form-control" placeholder = "Enter To Date"
                                name="toDate"
                                value={productPrice.toDate}
                                onChange={(e) => this.handleProductPriceMultiRowChange(index, e)}
                              />
                            </td>
             <td>
                              <input
                                type="number" class="form-control" placeholder = "Enter Price"
                                name="price"
                                value={productPrice.price}
                                onChange={(e) => this.handleProductPriceMultiRowChange(index, e)}
                              />
                            </td>
                            <td className="text-right">
                              <button className="btn btn-sm btn-outline-danger" onClick={(e) => this.handleProductPriceDelete(e,index)}><FontAwesomeIcon icon={faTrash}/></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
<table className="table table-striped">
                      <thead>
                        <tr>
 <th>Variant Id</th>
<th className="text-right"><button className="btn btn-sm btn-outline-success" onClick={this.addProductVariantRow}><FontAwesomeIcon icon={faPlus}/> Product Variant</button></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.data.productVariants.map((productVariant, index) => (
                          <tr key={index}>
<td><UxSelect
                                isRequired={false}
                                isMultiple={false}
                                name={'variantId'}
                                label={""}
                                placeholder={"Please Select"}
                                options={thisContext.state.variantOptions}
                                validationMessage={""}
                                value={productVariant.variantId}
                                updateSelected={(name, value) => this.handleProductVariantMultiRowSelectChange(index, name, value)}
                              /></td>
                            <td className="text-right">
                              <button className="btn btn-sm btn-outline-danger" onClick={(e) => this.handleProductVariantDelete(e,index)}><FontAwesomeIcon icon={faTrash}/></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
<Row><Col>{this.renderInput(false,true,"currentPrice", this.state.translations.currentPriceLabel,this.state.translations.currentPricePlaceholder  ,"number",false,this.state.validationMessage.currentPrice)}</Col></Row>
<Row><Col>{this.renderInput(false,true,"previousPrice", this.state.translations.previousPriceLabel,this.state.translations.previousPricePlaceholder  ,"number",false,this.state.validationMessage.previousPrice)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"description", this.state.translations.descriptionLabel,this.state.translations.descriptionPlaceholder  ,"textarea",false,this.state.validationMessage.description)}</Col></Row>
<Row><Col>{this.renderInput(true,true,"specification", this.state.translations.specificationLabel,this.state.translations.specificationPlaceholder  ,"textarea",false,this.state.validationMessage.specification)}</Col></Row>
<Row><Col>{this.renderInput(false,true,"remarks", this.state.translations.remarksLabel,this.state.translations.remarksPlaceholder  ,"textarea",false,this.state.validationMessage.remarks)}</Col></Row>

                {/* {this.renderInput("dueDate", "Due Date")} */}

                
            </Card.Body>

				{thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
          		</Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default ProductForm;
