import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Button, Form, Modal, Row, Col, Card } from 'react-bootstrap';
import config from "../../../config.json";
import UxCrudImage from '../../common/form_attributes/UxCrudImage';

const userUpdateApiEndPoint = config.apiUrl;

const Profile = (props) => {
  const [profile, setProfile] = useState({
    username: '',
    firstName: '',
    lastName: '',
    email: '',
    mobileNo: '',
    profilePicture: '',
    oldPassword: '',
    password: '',
    designation: '',
    newPassword: '',
    confirmPassword: '',
  });

  const [translations, setTranslations] = useState({
    titleLabel: 'Update Profile',
    homeLabel: 'Home',
    saveLabel: 'Update Profile',
    cancelLabel: 'Cancel',
    usernameLabel: 'Username',
    usernamePlaceholder: 'Enter Username',
    profilePictureLabel: 'Profile Picture',
    profilePicturePlaceholder: 'Select Profile Picture',
    passwordLabel: 'Password',
    passwordPlaceholder: 'Enter Password',
    firstNameLabel: 'First Name',
    firstNamePlaceholder: 'Enter First Name',
    lastNameLabel: 'Last Name',
    lastNamePlaceholder: 'Enter Last Name',
    designationLabel: 'Designation',
    designationPlaceholder: 'Enter Designation',
    emailLabel: 'Email',
    mobileNoLabel: 'Mobile No',
    emailPlaceholder: 'Enter Email',
    newPasswordLabel: 'New Password',
    confirmPasswordLabel: 'Confirm New Password',
    oldPasswordLabel: 'Old Password',
  });

  // const [response, setResponse] = useState(null);
  const [error, setError] = useState(null);
  const [showModal, setShowModal] = useState(false); // State to handle success modal visibility
  const [showErrorModal, setShowErrorModal] = useState(false); // State to handle error modal visibility


  const loadTranslations = () => {
    let storedTranslations = localStorage.getItem("translations");
    if (storedTranslations) {
      let translationsObj = JSON.parse(storedTranslations);
      setTranslations({
        // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
        titleLabel: translationsObj["i18n.user.titleLabel"]
          ? translationsObj["i18n.user.titleLabel"]
          : translations.titleLabel,

        homeLabel: translationsObj["i18n.user.homeLabel"]
          ? translationsObj["i18n.user.homeLabel"]
          : translations.homeLabel,

        saveLabel: translationsObj["i18n.user.saveLabel"]
          ? translationsObj["i18n.user.saveLabel"]
          : translations.saveLabel,

        cancelLabel: translationsObj["i18n.user.cancelLabel"]
          ? translationsObj["i18n.user.cancelLabel"]
          : translations.cancelLabel,

        usernameLabel: translationsObj["i18n.user.username.label"]
          ? translationsObj["i18n.user.username.label"]
          : translations.usernameLabel,
        usernamePlaceholder: translationsObj["i18n.user.username.placeholder"]
          ? translationsObj["i18n.user.username.placeholder"]
          : translations.usernamePlaceholder,

        profilePictureLabel: translationsObj["i18n.user.profilePicture.label"]
          ? translationsObj["i18n.user.profilePicture.label"]
          : translations.profilePictureLabel,

        profilePicturePlaceholder: translationsObj["i18n.user.profilePicture.placeholder"]
          ? translationsObj["i18n.user.profilePicture.placeholder"]
          : translations.profilePicturePlaceholder,

        passwordLabel: translationsObj["i18n.user.password.label"]
          ? translationsObj["i18n.user.password.label"]
          : translations.passwordLabel,
        passwordPlaceholder: translationsObj["i18n.user.password.placeholder"]
          ? translationsObj["i18n.user.password.placeholder"]
          : translations.passwordPlaceholder,
        firstNameLabel: translationsObj["i18n.user.firstName.label"]
          ? translationsObj["i18n.user.firstName.label"]
          : translations.firstNameLabel,
        firstNamePlaceholder: translationsObj[
          "i18n.user.firstName.placeholder"
        ]
          ? translationsObj["i18n.user.firstName.placeholder"]
          : translations.firstNamePlaceholder,
        lastNameLabel: translationsObj["i18n.user.lastName.label"]
          ? translationsObj["i18n.user.lastName.label"]
          : translations.lastNameLabel,
        lastNamePlaceholder: translationsObj["i18n.user.lastName.placeholder"]
          ? translationsObj["i18n.user.lastName.placeholder"]
          : translations.lastNamePlaceholder,

        designationLabel: translationsObj["i18n.user.designation.label"]
          ? translationsObj["i18n.user.designation.label"]
          : translations.designationLabel,
        designationPlaceholder: translationsObj[
          "i18n.user.designation.placeholder"
        ]
          ? translationsObj["i18n.user.designation.placeholder"]
          : translations.designationPlaceholder,

        emailLabel: translationsObj["i18n.user.email.label"]
          ? translationsObj["i18n.user.email.label"]
          : translations.emailLabel,


        mobileNoLabel: translationsObj["i18n.user.mobileNo.label"]
          ? translationsObj["i18n.user.mobileNo.label"]
          : translations.mobileNoLabel,

        emailPlaceholder: translationsObj["i18n.user.email.placeholder"]
          ? translationsObj["i18n.user.email.placeholder"]
          : translations.emailPlaceholder,

        mobileNoLabel: translationsObj["i18n.user.mobileNo.label"]
          ? translationsObj["i18n.user.mobileNo.label"]
          : translations.mobileNoLabel,
        mobileNoPlaceholder: translationsObj["i18n.user.mobileNo.placeholder"]
          ? translationsObj["i18n.user.mobileNo.placeholder"]
          : translations.mobileNoPlaceholder,

        newPasswordLabel: translationsObj["i18n.user.newPassword.label"]
          ? translationsObj["i18n.user.newPassword.label"]
          : translations.newPasswordLabel,

        confirmPasswordLabel: translationsObj[
          "i18n.user.confirmPassword.label"
        ]
          ? translationsObj["i18n.user.confirmPassword.label"]
          : translations.confirmPasswordLabel,

        oldPasswordLabel: translationsObj[
          "i18n.user.oldPassword.label"
        ]
          ? translationsObj["i18n.user.oldPassword.label"]
          : translations.oldPasswordLabel,
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  useEffect(() => {
    const fetchUserDetails = async () => {
      let token = localStorage.getItem("token");
      if (!token) {
        return;
      }

      try {
        const config = {
          headers: { Authorization: "Bearer " + token },
        };
        await axios.get(userUpdateApiEndPoint + "/user", config).then((response) => {
          const userData = response.data;
          setProfile({
            username: userData.username || '',
            firstName: userData.firstName || '',
            lastName: userData.lastName || '',
            email: userData.email || '',
            mobileNo: userData.mobileNo || '',
            profilePicture: userData.profilePicture || '',
            oldPassword: '',
            designation: userData.designation,
            password: '',
            confirmPassword: '',
          });
        });

      } catch (err) {
        console.error("Error fetching user details:", err);
      }
    };

    fetchUserDetails();
    loadTranslations();
  }, []);

  useEffect(() => {
    loadTranslations();
  }, [props.stateValues.activeLanguage]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProfile({
      ...profile,
      [name]: value,
    });
  };

  const handleOptionChange = (name, value) => {
    setProfile({
      ...profile,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const newPassword = profile.password ? profile.password.trim() : '';
    const confirmNewPassword = profile.confirmPassword ? profile.confirmPassword.trim() : '';

    if (newPassword !== confirmNewPassword) {
      setError("New password and confirm password do not match");
      setShowErrorModal(true); // Show error modal
      return;
    }

    if (!newPassword) {
      profile.password = profile.oldPassword;
    }

    try {
      let token = localStorage.getItem("token");

      await axios.post(userUpdateApiEndPoint + '/profiles', profile, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: "Bearer " + token,
        },
      });
      // setResponse(res.data);
      setError(null);
      setShowModal(true); // Show success modal
    } catch (err) {
      setError('Confirm Your Password');
      // setResponse(null);
      setShowErrorModal(true); // Show error modal
    }
  };

  const handleCloseModal = () => setShowModal(false); // Function to close the success modal
  const handleCloseErrorModal = () => setShowErrorModal(false); // Function to close the error modal


  return (
    <div className="container mt-5">
      {/* Card Component to Wrap the Form */}
      <Card>
        <Card.Title className="card-form-title-light">
          <Row>
            <Col md='11'>
              <h3>{translations.titleLabel}</h3>
            </Col>
          </Row>
        </Card.Title>
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <Row className="mb-3">
              <Col md={6}>
                <Form.Group controlId="username">
                  <Form.Label>{translations.usernameLabel}:</Form.Label>
                  <Form.Control
                    type="text"
                    name="username"
                    value={profile.username}
                    onChange={handleChange}
                    readOnly
                    className="align-input"
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <UxCrudImage
                  isRequired={true}
                  name={"profilePicture"}
                  label={translations.profilePictureLabel}
                  validationMessage={""}
                  value={profile.profilePicture}
                  error={""}
                  updateSelected={handleOptionChange}
                />
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Group controlId="firstName">
                  <Form.Label>{translations.firstNameLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="text"
                    name="firstName"
                    value={profile.firstName}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group controlId="lastName">
                  <Form.Label>{translations.lastNameLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="text"
                    name="lastName"
                    value={profile.lastName}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Group controlId="email">
                  <Form.Label>{translations.emailLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="email"
                    name="email"
                    value={profile.email}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group controlId="mobileNo">
                  <Form.Label>{translations.mobileNoLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="text"
                    name="mobileNo"
                    value={profile.mobileNo}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>

            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Group controlId="oldPassword">
                  <Form.Label>{translations.oldPasswordLabel}: <span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="password"
                    name="oldPassword"
                    value={profile.oldPassword}
                    onChange={handleChange}
                    required
                    className="align-input"
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group controlId="designation">
                  <Form.Label>{translations.designationLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="text"
                    name="designation"
                    value={profile.designation}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>

            </Row>
            <Row className="mb-3">

              <Col md={6}>
                <Form.Group controlId="password">
                  <Form.Label>{translations.newPasswordLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="password"
                    name="password"
                    value={profile.password}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group controlId="confirmPassword">
                  <Form.Label>{translations.confirmPasswordLabel}:<span style={{ color: "red" }}>*</span></Form.Label>
                  <Form.Control
                    type="password"
                    name="confirmPassword"
                    value={profile.confirmPassword}
                    onChange={handleChange}
                    className="align-input"
                  />
                </Form.Group>
              </Col>
            </Row>

            <Button type="submit" className="btn btn-primary">{translations.saveLabel}</Button>
          </Form>
        </Card.Body>
      </Card>

      {/* Modal for Error Message */}
      <Modal show={showErrorModal} onHide={handleCloseErrorModal}>
        <Modal.Header closeButton>
          <Modal.Title>Error</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p style={{ color: "red" }}>{error}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseErrorModal}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Modal for Success Message */}
      <Modal show={showModal} onHide={handleCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>Success</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Profile Updated Successfully!</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default Profile;
