import React, { Component } from "react";
import { Route } from "react-router-dom";
// import PublicNavBar from "../components/public/includes/PublicNavbar";

import PublicWebsiteContent from "./content/PublicWebsiteContent";
import { Col, Container, Row } from "react-bootstrap";
import properties from '../common_properties.json';
import { checkAuthentication } from "../components/common/authenticationService";
class PublicWebsiteLayout extends Component {
  state = {
    isOpen: false,
  };
  toggle = function () {
    return !this.state.isOpen;
  };


  render() {
    const { children, ...rest } = this.props;
    return (
      <div className="App wrapper" >
        {/* <PublicWebsiteContent isLoggedIn={children.props.isLoggedIn} labels={properties.labels.languages.en} toggle={true} isOpen={true}> */}
        <PublicWebsiteContent labels={properties.labels.languages.en} toggle={true} isOpen={true}>
          {/* <PublicNavBar
            toggle={true}
            isLoggedIn={children.props.isLoggedIn}
            loginUserEmail={children.props.loginUserEmail}
            loginUserFirstName={children.props.loginUserFirstName}
            loginUserLastName={children.props.loginUserLastName}
            isAdmin={children.props.isAdmin}
            isSupplier={children.props.isSupplier}
            labels={properties.labels.languages.en}
          /> */}
          <Container>
            <Row>
              {children}
            </Row>
          </Container>
        </PublicWebsiteContent>
      </div>
    );
  } 7
}

class PublicWebsiteLayoutRoute extends Component {
  state = {
    onemanStr: "one man army!",
    isOpen: false,
    data: { username: "", password: "" },
    errors: {},
    products: [],
    cartProducts: [],
    isHover: false,
    totalCartItemPrice: 0,
    currentPage: 0,
    products: [],
    pageSize: 8,
    firstPage: true,
    lastPage: false,
    count: 0,
    currentItem: "All Products",
    sortColumn: { path: "status", order: "asc" },
    totalElements: 0,
    selectedDistrictId: '',
    selectedLocationId: 1,
    selectedCategoryId: '',
    selectedDistanceInMinute: '',
    searchQuery: '',
    isLoggedIn: false,
    loginUserEmail: '',
    loginUserFirstName: '',
    loginUserLastName: '',
    isSupplier: false,
    isCustomer: false,
    alertShow: false,
    alertMessage: '',
    alertDetailsMessage: '',
    isCustomer: false,
    categories: [],
    districts: [],
    locations: [],
    distances: [{
      id: 1,
      description: "20 Minutes"
    }, {
      id: 2,
      description: "30 Minutes"
    }, {
      id: 3,
      description: "40 Minutes"
    }, {
      id: 3,
      description: "1 Hour"
    }]
  };
  updateOneMan = (value) => {
    this.setState({ onemanStr: value });
  }
  async componentDidMount() {
    const thisContext = this;
    // alert('public website layout');
    checkAuthentication().then(function (result) {
      // let authorities = localStorage.getItem("authorities");
      let user = localStorage.getItem("user");
      if (result && (user != null && user != undefined)) {
        let userObj = JSON.parse(user);
        thisContext.setState({
          loginUserEmail: userObj.email,
          loginUserFirstName: userObj.firstName,
          loginUserLastName: userObj.lastName
        });
        if (userObj.membershipType != null && userObj.membershipType != undefined) {
          if ((userObj.membershipType == 'SUPERADMIN')
            || (userObj.membershipType == 'ADMIN')) {
            thisContext.setState({ isAdmin: true });
          } else if (userObj.membershipType == 'VENDOR'
            || userObj.membershipType == 'SUPPLIER') {
            thisContext.setState({ isSupplier: true });
          } else if (userObj.membershipType == 'CUSTOMER') {
            thisContext.setState({ isCustomer: true });
          }
        }
      }
      // console.log(JSON.parse(authorities));
      thisContext.setState({
        isLoggedIn: result
      });
    }).catch((err) => {
      thisContext.setState({ alertShow: true, alertMessage: err, alertDetailsMessage: 'Please Reload The Page' });
    });
  }


  render() {
    const { component: Component, ...rest } = this.props;
    return (
      <Route
        {...rest}
        render={(matchProps) => (
          <PublicWebsiteLayout>
            <Component
              isLoggedIn={this.state.isLoggedIn}
              updateOneMan={this.updateOneMan}
              oneManMsg={this.state.onemanStr}
              toggle={true}
              loginUserEmail={this.state.loginUserEmail}
              loginUserFirstName={this.state.loginUserFirstName}
              loginUserLastName={this.state.loginUserLastName}
              isAdmin={this.state.isAdmin}
              isSupplier={this.state.isSupplier}
              isCustomer={this.state.isCustomer}
              labels={properties.labels.languages.en}
              districts={this.state.districts}
              locations={this.state.locations}
              distances={this.state.distances}
              categories={this.state.categories}
              updateSelectedLocationId={this.updateSelectedLocationId}
              updateSelectedCategoryId={this.updateSelectedCategoryId}
              selectedLocationId={this.state.selectedLocationId}
              selectedDistanceInMinute={this.state.selectedDistanceInMinute}
              updateSelectedDistanceInMinute={this.updateSelectedDistanceInMinute}

              {...matchProps}
            />
          </PublicWebsiteLayout>
        )}
      />
    );
  }
};

