import React, { Component } from "react";
import _ from "lodash";
import { paginate } from "./paginate";
import { Spinner, Container, Col, Row } from "react-bootstrap";
// import CustomBreadcrumb from "./CustomBreadcrumb";

class UxCrudManager extends Component {
  state = {
    errorDismissed: true,
    successDismissed: true,
    commonValidationMessage: "",
    moduleUrl: "/admin",
    basicUrl: "",
    titleLabel: "",
    catagorySelectionLabel: "",
    data: [],
    isLoading: true,
    userPermissions: [],
    categories: [
      "All AppPermissions",
      "DRAFT",
      "APPROVED",
      "REJECTED",
      "DELETED",
    ],
    translations: {
      homeLabel: "Home",
    },
		fromDate: "",
		toDate: "",
		searchToken: "",
    currentPage: 0,
    pageSize: 10,
    firstPage: true,
    lastPage: false,
    count: 0,
    currentItem: "",
    sortColumn: { path: "status", order: "asc" },
    totalElements: 0,
  };

  noPermissionMessage() {
    return "You are not permitted to view this page!";
  }

  async componentDidUpdate(prevProps) {
    if (prevProps.stateValues.activeLanguage !== this.props.stateValues.activeLanguage) {
      await this.loadTranslations();
    }
  }

  async componentDidMount() {
    let thisContext = this;
    try {
      this.loadData().then(function (response) {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        thisContext.setState({
          userPermissions: userPermissions,
        });
        // console.log("component loaded with data");
      });
      await this.loadTranslations();
    } catch (error) {
      if (error.response && error.response.status === 404) {
        this.props.history.replace("/not-found");
      }
    }
  }

  handleView = async (item) => {
    // console.log("View...");
    this.props.history.push(this.state.basicUrl + item.id);
    // console.log("View Loaded..");
  };
  handleEdit = async (item) => {
    // console.log("Edit...");
    this.props.history.push(this.state.basicUrl + item.id);
    // console.log("Edit Loaded.");
  };

  loadCreateForm = () => {
    this.props.history.push(this.state.basicUrl + "new");
  };

  loadPreviousPage = () => {
    const current = this.state.currentPage;
    this.setState({
      currentPage: current - 1,
    });
    this.loadData();
  };

  loadNextPage = () => {
    const current = this.state.currentPage;
    this.setState({
      currentPage: current + 1,
    });
    this.loadData();
  };

  handleSizeChange = (option) => {
    // console.log(option);
    this.setState({
      pageSize: option.value,
      currentPage: 0,
    });
    this.loadData();
  };

  handlePageChange = (page) => {
    this.setState({
      currentPage: page,
    });
    this.loadData();
  };

  handleCategorySelect = (item) => {
    this.setState({
      currentItem: item,
      currentPage: 0,
    });
    this.loadData();
  };

  handleChangeChk = (item) => {
    let dataClone = [...this.state.data];
    const filtered = dataClone.filter((data) => data.id === item.id);
    const index = dataClone.indexOf(item);
    filtered[0].liked = !filtered[0].liked;
    dataClone[index] = filtered[0];
    this.setState({
      data: dataClone,
    });
  };

  handleApprove = async (item) => {
    item.status = "APPROVED";
    await this.saveItem(item);
    this.props.history.push(this.state.basicUrl);
  };

  handleDelete = async (item) => {
    const objData = this.state.data;
    try {
      await this.deleteItem(item.id);
      const data = this.state.data.filter((m) => m.id !== item.id);
      this.setState({ data });
    } catch (error) {
      this.setState({ data: objData });
    }
  };

  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };

  getPagedData = () => {
    const {
      pageSize,
      // currentItem,
      data,
      sortColumn,
      totalElements,
    } = this.state;

    // const filtered =
    //   currentItem && currentItem !== this.state.catagorySelectionLabel
    //     ? data.filter((dataObj) => dataObj.status === currentItem)
    //     : data;

    const sortedItems = _.orderBy(data, [sortColumn.path], [sortColumn.order]);
    const paginatedObjData = paginate(sortedItems, 0, pageSize);
    // console.log("paginatedObjData: "+JSON.stringify(paginatedObjData));

    return { totalCount: totalElements, data: paginatedObjData };
  };

	handleCategoryChange = (selectedCategory) => {
		this.setState({ currentItem: selectedCategory });
	};

	handleSearchTokenChange = (e) => {
		this.setState({ searchToken: e.currentTarget.value });
	};

	handleFromDateChange = (e) => {
		this.setState({ fromDate: e.currentTarget.value });
	};

	handleToDateChange = (e) => {
		this.setState({ toDate: e.currentTarget.value });
	};

	handleSearch = () => {
		this.loadData();
	};

  render() {
    let { isLoading } = this.state;
    if (isLoading) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden"></span>
        </Spinner>
      );
    } else if (
      !this.state.userPermissions.includes(this.state.requiredPermission)
    ) {
      return (
        <>
          <Container className="bg-light text-black">
            <Row>
              <Col>
                <h2>{this.noPermissionMessage()}</h2>
              </Col>
            </Row>
          </Container>
        </>
      );
    }
    return (
      <>
        {/* <CustomBreadcrumb
          moduleUrl={this.state.moduleUrl}
          basicUrl={this.state.basicUrl}
          titleLabel={this.state.translations.titleLabel}
          homeLabel={this.state.translations.homeLabel}
        /> */}

        {this.htmlContent()}
      </>
    );
  }
}

export default UxCrudManager;