import React from "react";
import TableContent from "./tableContent";

const Table = ({
  columns,
  sortColumn,
  onSort,
  onCreate,
  data,
  isRtl,
}) => {
  return (
    <table className="table table-bordered table-sm">
      <TableContent
        onCreate={onCreate}
        columns={columns}
        sortColumn={sortColumn}
        onSort={onSort}
        data={data}
        isRtl={isRtl}
      />
    </table>
  );
};

export default Table;
