import React from "react";
import { Link, NavLink } from "react-router-dom";
import { Nav, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import logo from "../../images/companyLogo.png";
import SubMenuSystemAdmin from "./SubMenuSystemAdmin";
import properties from "../../common_properties.json";

import {
  // fas fa-users
  faHome,
  faHockeyPuck,
  faBriefcase,
  faCloudUploadAlt,
  faAngleLeft,
  faImage,
} from "@fortawesome/free-solid-svg-icons";

import SubMenuSettings from "./SubMenuSettings";
import classNames from "classnames";
import SubMenuUserManager from "./SubMenuUserManager";

class AdminPanelSideBar extends React.Component {
  state = {
    activeLanguage: this.props.activeLanguage,
    userPermissions: [],
    isLoggedIn: false,
    isLoading: true,
    loginUserUsername: "",
    loginUserEmail: "",
    loginUserFirstName: "",
    loginUserLastName: "",
    translations: {
      loginUserHeaderLabel: "Login User : ",
      dashboardMenuLabel: "Dashboard",
      dataStatusMenuLabel: "Data Status",
      securityStatusMenuLabel: "Security Status",
      userManagerMenuLabel: "User Manager",
      settingsMenuLabel: "Settings",
      systemAdminMenuLabel: "System Admin",
      adminPanelMenuLabel: "Admin Panel",

      userRequestMenuLabel: "User Request",
      translationsManagerMenuLabel: "Translation Manager",
      userMenuLabel: "Users",
      translationsMenuLabel: "Translations",
      authorityMenuLabel: "Authority",
      permissionGroupMenuLabel: "Permission Group",
      appPermissionMenuLabel: "App Permission",
      userMnoCsraPortalMenuLabel: "User MNO CSRA Portal",
      mnoCsraPortalMenuLabel: "MNO CSRA Portal",
      mnoCsraAuthorizationKeyMenuLabel: "MNO CSRA Authorization Key",
      mnoMenuLabel: "MNO",
      categoryMenuLabel: "Category",
      relatedProductMenuLabel: "Related Product",
      supplierMenuLabel: "Supplier",
      productRatingMenuLabel: "Product Rating",
      subCategoryMenuLabel: "Sub Category",
      variantMenuLabel: "Variant",
      productMenuLabel: "Product",
      brandMenuLabel: "Brand",
      websiteInfoMenuLabel: "Website Info",
      websiteFeatureMenuLabel: "Website Feature",
      purchaseOrderMenuLabel: "Purchase Order",
      customerOrderMenuLabel: "Customer Order",
      customerMenuLabel: "Customer",
      salesOrderMenuLabel: "Sales Order",
// newMenuLabel
      appNameDetailLabel: properties.labels.languages.en.app_name_details,
    },
  };

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          loginUserHeaderLabel: translationsObj[
            "i18n.menu.loginUserHeaderLabel"
          ]
            ? translationsObj["i18n.menu.loginUserHeaderLabel"]
            : thisContext.state.translations.loginUserHeaderLabel,

          dashboardMenuLabel: translationsObj["i18n.menu.dashboardMenuLabel"]
            ? translationsObj["i18n.menu.dashboardMenuLabel"]
            : thisContext.state.translations.dashboardMenuLabel,

          dataStatusMenuLabel: translationsObj[
            "i18n.menu.dataStatusMenuLabel"
          ]
            ? translationsObj["i18n.menu.dataStatusMenuLabel"]
            : thisContext.state.translations.dataStatusMenuLabel,

          securityStatusMenuLabel: translationsObj[
            "i18n.menu.securityStatusLabel"
          ]
            ? translationsObj["i18n.menu.securityStatusLabel"]
            : thisContext.state.translations.securityStatusMenuLabel,

          settingsMenuLabel: translationsObj["i18n.menu.settingsMenuLabel"]
            ? translationsObj["i18n.menu.settingsMenuLabel"]
            : thisContext.state.translations.settingsMenuLabel,

          systemAdminMenuLabel: translationsObj["i18n.menu.systemAdminMenuLabel"]
            ? translationsObj["i18n.menu.systemAdminMenuLabel"]
            : thisContext.state.translations.systemAdminMenuLabel,

          adminPanelMenuLabel: translationsObj["i18n.menu.adminPanelLabel"]
            ? translationsObj["i18n.menu.adminPanelLabel"]
            : thisContext.state.translations.adminPanelMenuLabel,

          userManagerMenuLabel: translationsObj["i18n.menu.userManagerMenuLabel"]
            ? translationsObj["i18n.menu.userManagerMenuLabel"]
            : thisContext.state.translations.userManagerMenuLabel,


          translationsManagerMenuLabel: translationsObj["i18n.menu.translationsManagerMenuLabel"]
            ? translationsObj["i18n.menu.translationsManagerMenuLabel"]
            : thisContext.state.translations.translationsManagerMenuLabel,

          userRequestMenuLabel: translationsObj[
            "i18n.menu.userRequestMenuLabel"
          ]
            ? translationsObj["i18n.menu.userRequestMenuLabel"]
            : thisContext.state.translations.userRequestMenuLabel,


          userMenuLabel: translationsObj["i18n.menu.userMenuLabel"]
            ? translationsObj["i18n.menu.userMenuLabel"]
            : thisContext.state.translations.userMenuLabel,

          translationsMenuLabel: translationsObj[
            "i18n.menu.translationsMenuLabel"
          ]
            ? translationsObj["i18n.menu.translationsMenuLabel"]
            : thisContext.state.translations.translationsMenuLabel,

          authorityMenuLabel: translationsObj["i18n.menu.authorityMenuLabel"]
            ? translationsObj["i18n.menu.authorityMenuLabel"]
            : thisContext.state.translations.authorityMenuLabel,

          permissionGroupMenuLabel: translationsObj[
            "i18n.menu.permissionGroupMenuLabel"
          ]
            ? translationsObj["i18n.menu.permissionGroupMenuLabel"]
            : thisContext.state.translations.permissionGroupMenuLabel,

          appPermissionMenuLabel: translationsObj[
            "i18n.menu.appPermissionMenuLabel"
          ]
            ? translationsObj["i18n.menu.appPermissionMenuLabel"]
            : thisContext.state.translations.appPermissionMenuLabel,

          userMnoCsraPortalMenuLabel: translationsObj[
            "i18n.menu.userMnoCsraPortalMenuLabel"
          ]
            ? translationsObj["i18n.menu.userMnoCsraPortalMenuLabel"]
            : thisContext.state.translations.userMnoCsraPortalMenuLabel,

          mnoCsraPortalMenuLabel: translationsObj[
            "i18n.menu.mnoCsraPortalMenuLabel"
          ]
            ? translationsObj["i18n.menu.mnoCsraPortalMenuLabel"]
            : thisContext.state.translations.mnoCsraPortalMenuLabel,

          mnoMenuLabel: translationsObj["i18n.menu.mnoMenuLabel"]
            ? translationsObj["i18n.menu.mnoMenuLabel"]
            : thisContext.state.translations.mnoMenuLabel,

          mnoCsraAuthorizationKeyMenuLabel: translationsObj[
            "i18n.menu.mnoCsraAuthorizationKeyMenuLabel"
          ]
            ? translationsObj["i18n.menu.mnoCsraAuthorizationKeyMenuLabel"]
            : thisContext.state.translations.mnoCsraAuthorizationKeyMenuLabel,
          categoryMenuLabel: translationsObj["i18n.menu.categoryMenuLabel"]
            ? translationsObj["i18n.menu.categoryMenuLabel"]
            : thisContext.state.translations.categoryMenuLabel,
          relatedProductMenuLabel: translationsObj["i18n.menu.relatedProductMenuLabel"]
            ? translationsObj["i18n.menu.relatedProductMenuLabel"]
            : thisContext.state.translations.relatedProductMenuLabel,
          supplierMenuLabel: translationsObj["i18n.menu.supplierMenuLabel"]
            ? translationsObj["i18n.menu.supplierMenuLabel"]
            : thisContext.state.translations.supplierMenuLabel,
          productRatingMenuLabel: translationsObj["i18n.menu.productRatingMenuLabel"]
            ? translationsObj["i18n.menu.productRatingMenuLabel"]
            : thisContext.state.translations.productRatingMenuLabel,
          subCategoryMenuLabel: translationsObj["i18n.menu.subCategoryMenuLabel"]
            ? translationsObj["i18n.menu.subCategoryMenuLabel"]
            : thisContext.state.translations.subCategoryMenuLabel,
          variantMenuLabel: translationsObj["i18n.menu.variantMenuLabel"]
            ? translationsObj["i18n.menu.variantMenuLabel"]
            : thisContext.state.translations.variantMenuLabel,
          productMenuLabel: translationsObj["i18n.menu.productMenuLabel"]
            ? translationsObj["i18n.menu.productMenuLabel"]
            : thisContext.state.translations.productMenuLabel,
          brandMenuLabel: translationsObj["i18n.menu.brandMenuLabel"]
            ? translationsObj["i18n.menu.brandMenuLabel"]
            : thisContext.state.translations.brandMenuLabel,
          websiteInfoMenuLabel: translationsObj["i18n.menu.websiteInfoMenuLabel"]
            ? translationsObj["i18n.menu.websiteInfoMenuLabel"]
            : thisContext.state.translations.websiteInfoMenuLabel,
          websiteFeatureMenuLabel: translationsObj["i18n.menu.websiteFeatureMenuLabel"]
            ? translationsObj["i18n.menu.websiteFeatureMenuLabel"]
            : thisContext.state.translations.websiteFeatureMenuLabel,
          purchaseOrderMenuLabel: translationsObj["i18n.menu.purchaseOrderMenuLabel"]
            ? translationsObj["i18n.menu.purchaseOrderMenuLabel"]
            : thisContext.state.translations.purchaseOrderMenuLabel,
          customerOrderMenuLabel: translationsObj["i18n.menu.customerOrderMenuLabel"]
            ? translationsObj["i18n.menu.customerOrderMenuLabel"]
            : thisContext.state.translations.customerOrderMenuLabel,
          customerMenuLabel: translationsObj["i18n.menu.customerMenuLabel"]
            ? translationsObj["i18n.menu.customerMenuLabel"]
            : thisContext.state.translations.customerMenuLabel,
          salesOrderMenuLabel: translationsObj["i18n.menu.salesOrderMenuLabel"]
            ? translationsObj["i18n.menu.salesOrderMenuLabel"]
            : thisContext.state.translations.salesOrderMenuLabel,
// newMenuTranslationsAssignment
        },
      });

      // console.log("AdminPanelSideBar :: loadTranslations");
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  reloadTranslations = (updatedLanguage) => {
    // console.log("+++ AdminPanelSideBar :: reloadTranslations +++");
    // console.log("updatedLanguage: " + updatedLanguage);

    try {
      this.loadTranslations();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }

    // console.log("--- AdminPanelSideBar :: reloadTranslations ---");
  };

  async componentDidMount() {
    const thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    try {
      await this.loadTranslations();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
    let user = localStorage.getItem("user");
    if (user != null && user != undefined) {
      let userObj = JSON.parse(user);
      thisContext.setState({
        loginUserEmail: userObj.email,
        loginUserFirstName: userObj.firstName,
        loginUserLastName: userObj.lastName,
      });
    }
  }
  render() {
    let thisContext = this;
    const customFontStyle = {
      fontFamily: "Diodrum Arabic Semibold, sans-serif",
      fontWeight: "5",
    };
    return (
      <div
        className={classNames(
          { "sidebar-rtl": this.props.isRtl },
          { sidebar: !this.props.isRtl },
          { "is-open": this.props.isOpen }
        )}
      >
        <div className="sidebar-header" align="center">
          <Button
            variant="outline-info"
            onClick={this.props.toggle}
            className="mt-1"
          >
            {/* <FontAwesomeIcon icon={faBriefcase} pull="right" size="xs" /> */}
            <FontAwesomeIcon icon={faAngleLeft} pull="right" size="xs" />
          </Button>
          <h3>
            <img src={logo} alt="Company Logo" width={"200px"} height={"100px"} />
          </h3>
          <p className="ml-3" style={{ color: "green" }}>
            {this.state.translations.appNameDetailLabel}
          </p>
        </div>

        <Nav className="flex-column pt-2">
          <Nav.Item as={"li"}>
            <NavLink to="/admin/dashboard" className="nav-link nav-item"
              isActive={(match, location) => {
                if (!match) return false;
                return location.pathname.startsWith("/admin/dashboard");
              }}
            >
              <FontAwesomeIcon icon={faCloudUploadAlt} className="mr-2" />
              {this.state.translations.dashboardMenuLabel}
            </NavLink>
          </Nav.Item>

          {this.state.userPermissions.includes("APP_SETTINGS_READ") && (
            <SubMenuSettings userPermissions={this.state.userPermissions}
              translations={this.state.translations}
              title={this.state.translations.adminPanelMenuLabel} />
          )}

          {this.state.userPermissions.includes("USER_READ") && (
            <SubMenuUserManager userPermissions={this.state.userPermissions}
              translations={this.state.translations}
              title={this.state.translations.userManagerMenuLabel} />
          )}

          {this.state.userPermissions.includes("APP_STATUS_READ") && (
            <SubMenuSystemAdmin userPermissions={this.state.userPermissions}
              translations={this.state.translations}
              title={this.state.translations.systemAdminMenuLabel} />
          )}

          {this.state.userPermissions.includes("RELATED_PRODUCT_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/relatedProducts" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/relatedProducts");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.relatedProductMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}

          {this.state.userPermissions.includes("PRODUCT_RATING_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/productRatings" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/productRatings");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.productRatingMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("BRAND_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/brands" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/brands");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.brandMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("WEBSITE_INFO_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/websiteInfos" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/websiteInfos");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.websiteInfoMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("WEBSITE_FEATURE_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/websiteFeatures" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/websiteFeatures");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.websiteFeatureMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("PAYMENT_METHOD_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/paymentMethods" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/paymentMethods");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.paymentMethodMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("PURCHASE_ORDER_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/purchaseOrders" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/purchaseOrders");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.purchaseOrderMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("CUSTOMER_ORDER_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/customerOrders" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/customerOrders");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.customerOrderMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("CUSTOMER_READ") && (
            <Nav.Item>
              <Nav.Link as="div">
                <NavLink to="/admin/customers" className="nav-link nav-item" isActive={(match, location) => {
                  if (!match) return false;
                  return location.pathname.startsWith("/admin/customers");
                }}>
                  <FontAwesomeIcon icon={faImage} className="mr-2" />
                  {thisContext.state.translations.customerMenuLabel}
                </NavLink>
              </Nav.Link>
            </Nav.Item>
          )}
          {this.state.userPermissions.includes("SALES_ORDER_READ") && (
<Nav.Item>
            <Nav.Link as="div">
              <NavLink to="/admin/salesOrders" className="nav-link nav-item" isActive={(match, location) => {
                if (!match) return false;
                return location.pathname.startsWith("/admin/salesOrders");
              }}>
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                {thisContext.state.translations.salesOrderMenuLabel}
 </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{/*newNavLinks*/}






        </Nav>
      </div>
    );
  }
}

export default AdminPanelSideBar;
