
import React, { Component } from 'react';
import {Button, Modal} from 'react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrash,
  faPencilAlt
} from "@fortawesome/free-solid-svg-icons";
class DeleteConfirmModal extends Component {
    render() {
      return (
        <React.Fragment>
          <Button variant="light" size='sm' disabled={this.props.disabled} onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faTrash} />
        </Button>
          <Modal
            show={this.props.show}
            onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          >
            <Modal.Header>
              <Modal.Title>Delete Action</Modal.Title>
            </Modal.Header>
  
            <Modal.Body>
              <h4>Are you sure ?</h4>
            </Modal.Body>
  
            <Modal.Footer>
              <Button
                variant="secondary"
                onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
              >
                Cancel
              </Button>
              <Button
                variant="danger"
                onClick={() => this.props.onConfirm(this.props.item)}
              >
                Delete
              </Button>
            </Modal.Footer>
          </Modal>
        </React.Fragment>
      );
    }
  }

  export default DeleteConfirmModal;