import React, { useState, useEffect } from "react";
// import FloatingLabel from "floating-label-react";
// import Form from "react-bootstrap/Form";

const UxCrudInput = ({ isRequired, isVisible, name, label, placeholder, type, readonly, validationMessage, error, onChange, ...rest }) => {
  const preparedClassName = isVisible ? "form-group" : "form-group d-none";
  const requiredHtml = isRequired ? <span style={{ color: 'red' }}>*</span> : "";
  const customFontStyle = {
    fontFamily: 'Diodrum Arabic Semibold, sans-serif',
    fontWeight: '5',
  };

  const smallFontStyle = {
    fontFamily: 'sans-serif',
    fontWeight: '1',
  };

  const [errorMsg, setErrorMsg] = useState('');
  const [inputValidationMessage, setInputValidationMessage] = useState('');

  useEffect(() => {
    setErrorMsg(error);
    // console.log("error: " + error);
    if (error === undefined || error === '') {
      // console.log('validation message empty');
      setInputValidationMessage('');
    }
    // console.log('error change');
  }, [error]);

  useEffect(() => {
    setErrorMsg(validationMessage);
    setInputValidationMessage(validationMessage);
    // console.log('validationMessage change');
  }, [validationMessage]);

  const handleFocusIn = ({ currentTarget: input }) => {
    console.log(input.value);
    if (input.value == '') {
      setErrorMsg(' required ');
    } else if (input.value.trim() == '') {
      setErrorMsg(' required ');
    } else {
      setErrorMsg('');
      setInputValidationMessage('');
      validationMessage = '';
    }
  };

  const handleCheckboxChange = (event) => {
    onChange({ currentTarget: event.target});
  };

  const handleChange = ({ currentTarget: input }) => {
    if (input.value == '') {
      setErrorMsg(' required ');
    } else if (input.value.trim() == '') {
      setErrorMsg(' required ');
    } else {
      setErrorMsg('');
      setInputValidationMessage('');
    }
    onChange({ currentTarget: input });
  };

  const handleFocusOut = ({ currentTarget: input }) => {
    if (input.value == '') {
      setErrorMsg(' required ');
    } else if (input.value.trim() == '') {
      setErrorMsg(' required ');
    } else {
      setErrorMsg('');
      setInputValidationMessage('');
    }
  };

  // const inputClass = (error && error != '') || (validationMessage && validationMessage != '') ?
  //   'form-control border border-danger' :
  //   'form-control';

  const inputClass = (isRequired && errorMsg && errorMsg != '') ?
    'form-control border border-danger' :
    'form-control';

  if (type === 'textarea' || type === 'textArea') {
    return (
      <React.Fragment>
        {/* <div className={preparedClassName}>
          <div className="col-md-3">
            <b><label htmlFor={name} style={customFontStyle}>{label}</label></b>
            {requiredHtml}
          </div>
          <div className="col-md-6">
            <textarea
              {...rest}
              name={name}
              type={type}
              id={name}
              placeholder={placeholder}
              className={inputClass}
              onChange={handleChange}
              onFocus={handleFocusIn}
              onBlur={handleFocusOut}
              readOnly={readonly}
              style={customFontStyle}
            />
          </div>

          <div className="col-md-3">
            {error && <div className="alert alert-danger">{error}</div>}
            {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
          </div>
        </div> */}
        <div className="form-group">
          <b><label htmlFor={name}>{label}</label></b>
          &nbsp;{requiredHtml}
          <textarea
            {...rest}
            name={name}
            type={type}
            id={name}
            placeholder={placeholder}
            className={inputClass}
            onChange={handleChange}
            onFocus={handleFocusIn}
            onBlur={handleFocusOut}
            readOnly={readonly}
          />
          {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
        </div>
      </React.Fragment>
    );
  } else if (type === 'textBox' || type === 'text') {
    return (
      <React.Fragment>
        {/* <div className={preparedClassName}>
          <div className="col-md-3">
            <b><label htmlFor={name} style={customFontStyle}>{label}</label></b>
            &nbsp;{requiredHtml}
          </div>
          <div className="col-md-6">
            <input
              style={customFontStyle}
              {...rest}
              name={name}
              type={type}
              id={name}
              placeholder={placeholder}
              className={inputClass}
              onChange={handleChange}
              onFocus={handleFocusIn}
              onBlur={handleFocusOut}
              readOnly={readonly}
            />
          </div>
          <div className="col-md-3">
            {error && <div className="alert alert-danger">{error}</div>}
            {inputValidationMessage && <div className="alert alert-danger" style={smallFontStyle}>{inputValidationMessage}</div>}
          </div>
        </div> */}
        <div className="form-group">
          <b><label htmlFor={name}>{label}</label></b>
          &nbsp;{requiredHtml}
          <input
            {...rest}
            name={name}
            type={type}
            id={name}
            placeholder={placeholder}
            className={inputClass}
            onChange={handleChange}
            onFocus={handleFocusIn}
            onBlur={handleFocusOut}
            readOnly={readonly}
          />
          {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
        </div>
      </React.Fragment>
    );
  } else if (type === 'checkbox' || type === 'radio') {
    return (
      <React.Fragment>
        {/* <div className={preparedClassName}>

          <div className="col-md-3">
            <b><label htmlFor={name} style={customFontStyle}>{label}</label></b>
            &nbsp;{requiredHtml}
          </div>
          <div className="col-md-6">
            <input
              {...rest}
              name={name}
              type={type}
              id={name}
              placeholder={placeholder}
              // className="form-control"
              onChange={handleChange}
              readOnly={readonly}
              style={customFontStyle}
            />
          </div>

          <div className="col-md-3">
            {error && <div className="alert alert-danger">{error}</div>}
            {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
          </div>
        </div> */}
        <div className="form-group">
          <b><label htmlFor={name}>{label}</label></b>
          &nbsp;{requiredHtml}
          <input
            {...rest}
            name={name}
            type={type}
            id={name}
            placeholder={placeholder}
            // className="form-control"
            onChange={handleCheckboxChange}
            readOnly={readonly}
            style={customFontStyle}
            className="form-control"
          />
          {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
        </div>
      </React.Fragment>
    );
  } else {
    return (
      <React.Fragment>
        {/* <div className={preparedClassName}>
          <div className="col-md-3">
            <b><label htmlFor={name} style={customFontStyle}>{label}</label></b>
            &nbsp;{requiredHtml}
          </div>
          <div className="col-md-6">
            <input
              name={name}
              type={type}
              id={name}
              placeholder={placeholder}
              className={inputClass}
              onChange={handleChange}
              onFocus={handleFocusIn}
              onBlur={handleFocusOut}
              readOnly={readonly}
              required={isRequired}
              style={customFontStyle}
              {...rest}
            />
          </div>
          <div className="col-md-3">
            {error && <div className="alert alert-danger">{error}</div>}
            {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
          </div>

        </div> */}
        <div className={preparedClassName}>
          <b><label htmlFor={name}>{label}</label></b>
          &nbsp;{requiredHtml}
          <input
            name={name}
            type={type}
            id={name}
            placeholder={placeholder}
            className={inputClass}
            onChange={handleChange}
            onFocus={handleFocusIn}
            onBlur={handleFocusOut}
            readOnly={readonly}
            required={isRequired}
            style={customFontStyle}
            {...rest}
          />
          {inputValidationMessage && <div className="alert alert-danger">{inputValidationMessage}</div>}
        </div>
      </React.Fragment>
    );
  }

};

export default UxCrudInput;