import React from "react";
import UxCrudForm from "../../common/UxCrudForm";
import { save, get } from "./appPermissionService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
// import FileBase64 from "react-file-base64";

class AppPermissionForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["APP_PERMISSION_CREATE", "APP_PERMISSION_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/appPermissions/",
    userPermissions: [],
    data: { id: null, permissionName: "", isActive: "false" },
    errors: {},
    validationMessage: {
      permissionName: "",
      isActive: "",
    },
    commonValidationMessage: "",
    translations: {
      titleLabel: "App Permission",
      homeLabel: "Home",
      cancelLabel: "Cancel",
      saveLabel: "Save",
      permissionNameLabel: "Permission Name",
      permissionNamePlaceholder: "Enter Permission Name",
      isActiveLabel: "Is Active",
      isActivePlaceholder: "Enter Is Active",
    },
  };

  /* schema = {
    id: Joi.number().allow(null),
    permissionName: Joi.string().required().label({this.state.translations.permissionNameLabel}),isActive: Joi.string().required().label({this.state.translations.isActiveLabel}),
  };
*/
  async populateData() {
    try {
      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
        this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj[
            "i18n.appPermission.titleLabel"
          ]
            ? translationsObj["i18n.appPermission.titleLabel"]
            : thisContext.state.translations.titleLabel,

          homeLabel: translationsObj["i18n.user.homeLabel"]
            ? translationsObj["i18n.user.homeLabel"]
            : thisContext.state.translations.homeLabel,

          saveLabel: translationsObj["i18n.user.saveLabel"]
            ? translationsObj["i18n.user.saveLabel"]
            : thisContext.state.translations.saveLabel,

          cancelLabel: translationsObj["i18n.user.cancelLabel"]
            ? translationsObj["i18n.user.cancelLabel"]
            : thisContext.state.translations.cancelLabel,

          permissionNameLabel: translationsObj[
            "i18n.appPermission.permissionName.label"
          ]
            ? translationsObj["i18n.appPermission.permissionName.label"]
            : thisContext.state.translations.permissionNameLabel,

          permissionNamePlaceholder: translationsObj[
            "i18n.appPermission.permissionName.placeholder"
          ]
            ? translationsObj["i18n.appPermission.permissionName.placeholder"]
            : thisContext.state.translations.permissionNamePlaceholder,
          isActiveLabel: translationsObj["i18n.appPermission.isActive.label"]
            ? translationsObj["i18n.appPermission.isActive.label"]
            : thisContext.state.translations.isActiveLabel,
          isActivePlaceholder: translationsObj[
            "i18n.appPermission.isActive.placeholder"
          ]
            ? translationsObj["i18n.appPermission.isActive.placeholder"]
            : thisContext.state.translations.isActivePlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(
        thisContext.state.userPermissions.includes("APP_PERMISSION_CREATE") ||
        thisContext.state.userPermissions.includes("APP_PERMISSION_UPDATE")
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      permissionName: obj.permissionName,
      isActive: obj.isActive,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status === "FAILED" ||
        response.data.validationMessages !== undefined
      ) {
        if (response.data.validationMessages !== undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message !== undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="pt-2 pb-2">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black mr-3 mb-3">
                  <Card.Title className="card-form-title-light">
                    <Row>
                      <Col md='1'>
                        {
                          this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                            e.preventDefault();
                            thisContext.props.history.push(thisContext.state.basicUrl);
                          })
                        }
                      </Col>
                      <Col md='11'>
                        <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                      </Col>
                    </Row>
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello App Permission please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <Row>
                      {this.renderInput(false, false, "id", "Id", "hidden")}
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "permissionName",
                          this.state.translations.permissionNameLabel,
                          this.state.translations.permissionNamePlaceholder,
                          "text",
                          (data.id !== null),
                          this.state.validationMessage.permissionName
                        )}
                      </Col>
                      <Col>
                        {this.renderInput(
                          true,
                          true,
                          "isActive",
                          this.state.translations.isActiveLabel,
                          this.state.translations.isActivePlaceholder,
                          "checkbox",
                          false,
                          this.state.validationMessage.isActive
                        )}
                      </Col>
                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </Row>
                  </Card.Body>
                  {thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default AppPermissionForm;
