import React from "react";
import { Col, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

const CustomCardTitleContents = ({
  fromDate,
  toDate,
  searchToken,
  categories,
  currentItem,
  onCategoryChange,
  onSearchTokenChange,
  onFromDateChange,
  onToDateChange,
  onSearch,
}) => {
  return (
    <>

      <Col md='8'>
        <div className="input-group">
            <label className="label form-control">From Date</label>
        <input
          className="form-control"
          name="fromDate"
          type="date"
          value={fromDate}
          onChange={onFromDateChange}
        />
        <label className="label form-control">To Date</label>
        <input
          className="form-control"
          name="toDate"
          type="date"
          value={toDate}
          onChange={onToDateChange}
        />
        <select
          className="form-control"
          value={currentItem}
          onChange={(e) => onCategoryChange(e.target.value)}
        >
          <option value={""}>-Select Status-</option>
          {categories.map((category) => (
            <option value={category} key={category}>
              {category}
            </option>
          ))}
        </select>
          <input
            type="text"
            onChange={onSearchTokenChange}
            value={searchToken}
            className="form-control"
            placeholder="Search..."
            aria-label="Search Token"
          />
          <div className="input-group-append">
            <div className="input-group-text form-control">
              <Button onClick={onSearch} variant="none" size="sm" className="mr-2">
                <FontAwesomeIcon icon={faSearch} />
              </Button>
            </div>
          </div>
        </div>
      </Col>
    </>
  );
};

export default CustomCardTitleContents;
