import React from "react";
import { Nav } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faCaretUp, faCopy, faImage } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";

class SubMenuSystemAdmin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeMenu: null // Stores the active menu item
    };
    this.toggleNavbar = this.toggleNavbar.bind(this);
  }

  componentDidMount() {
    const currentPath = window.location.pathname;

    // Check the current URL path to determine which menu should be active
    if (currentPath.startsWith("/adminpanel/admin/i18n/importTranslations")) {
      this.setState({ activeMenu: "importTranslations" });
    } else if (currentPath.startsWith("/adminpanel/admin/appStatus")) {
      this.setState({ activeMenu: "appStatus" });
    } else if (currentPath.startsWith("/adminpanel/admin/translationss")) {
      this.setState({ activeMenu: "translationss" });
    }

    if (currentPath.startsWith("/adminpanel/admin/i18n/importTranslations")
      || currentPath.startsWith("/adminpanel/admin/appStatus")
    || currentPath.startsWith("/adminpanel/admin/translationss")) {
      this.toggleNavbar("systemAdminSettings");
    }

  }

  toggleNavbar(menu) {
    // Toggle the specific menu passed
    this.setState((prevState) => ({
      activeMenu: prevState.activeMenu === menu ? null : menu
    }));
  }

  renderMenuItem(to, label, icon, menuKey) {
    const { activeMenu } = this.state;
    return (
      <Nav.Item>
        <Nav.Link as="div">
          <NavLink
            to={to}
            className={classNames("nav-link", "nav-item", { open: activeMenu === menuKey })}
          >
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {label}
          </NavLink>
        </Nav.Link>
      </Nav.Item>
    );
  }

  render() {
    const { icon, title, translations, userPermissions } = this.props;
    const { activeMenu } = this.state;

    return (
      <Nav.Item className={classNames({ open: activeMenu })}>
        <Nav.Link
          variant="link"
          className={classNames("d-flex justify-content-between align-items-center nav-link nav-item", { active: activeMenu })}
          onClick={() => this.toggleNavbar("systemAdminSettings")}
        >
          <div>
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {title}
          </div>
          <FontAwesomeIcon icon={activeMenu === "systemAdminSettings" ? faCaretUp : faCaretDown} />
        </Nav.Link>

        {activeMenu === "systemAdminSettings" && (
          <nav className="nav flex-column">
            {userPermissions.includes("TRANSLATIONS_MANAGER") &&
              this.renderMenuItem("/admin/i18n/importTranslations", translations.translationsManagerMenuLabel, faImage, "importTranslations")}

            {userPermissions.includes("APP_STATUS_READ") && this.renderMenuItem("/admin/appStatus", translations.dataStatusMenuLabel, faCopy, "appStatus")}
            {userPermissions.includes("TRANSLATIONS_READ") && this.renderMenuItem("/admin/translationss", translations.translationsMenuLabel, faCopy, "translations")}

          </nav>
        )}
      </Nav.Item>
    );
  }
}

export default SubMenuSystemAdmin;
