import React, { useState } from 'react';
import { Container, Row, Col, Navbar, Nav } from 'react-bootstrap';
import "../../css/dashboard.css";
import DashboardCard from './DashboardCard';

const initialModules = [
  { id: 1, name: 'App 1', logo: 'app1.png', installed: false },
  { id: 2, name: 'App 2', logo: 'app2.png', installed: false },
  { id: 3, name: 'App 3', logo: 'app3.png', installed: false },
  { id: 4, name: 'App 4', logo: 'app4.png', installed: false },
  { id: 5, name: 'App 5', logo: 'app5.png', installed: false },
  { id: 6, name: 'App 6', logo: 'app6.png', installed: false },
  { id: 7, name: 'App 7', logo: 'app7.png', installed: false },
  { id: 8, name: 'App 8', logo: 'app8.png', installed: false },
];

const AppsDashboard = () => {
  const [modules, setModules] = useState(initialModules);

  const handleInstall = (id) => {
    setModules(modules.map(module =>
      module.id === id ? { ...module, installed: true } : module
    ));
  };

  const handleUninstall = (id) => {
    setModules(modules.map(module =>
      module.id === id ? { ...module, installed: false } : module
    ));
  };

  return (
    <Container fluid>
      <Navbar bg="light" style={{ alignItems: "center" }} expand="lg">
        <Navbar.Brand href="#">Apps Dashboard</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            {modules.filter(module => module.installed).map(module => (
              <Nav.Link key={module.id} href="#">{module.name}</Nav.Link>
            ))}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <Row>
        <div className="card-list">
          {modules.map((module, index) => (
            <DashboardCard key={index} content={module.name} />
          ))}
        </div>
      </Row>
    </Container>
  );
};

export default AppsDashboard;
