import config from "../../config.json";
import axios from "axios";
// import { useHistory } from 'react-router-dom';
const tokenKey = "token";
const permissionsKey = "permissions";
const registrationApiEndPoint = config.apiUrl + "/register";
const authenticationApiEndPoint = config.apiUrl + "/user/authenticate";
const userApiEndPoint = config.apiUrl + "/user";
const loadTranslationsApiEndPoint = config.apiUrl + "/i18n/v2?language=";
const loadTranslationsAuthlessApiEndPoint = config.apiUrl + "/i18n/v2_authless?language=";

export function doRegister(username, password, fullName) {
  return new Promise((resolve, reject) => {
    if (username && password) {
      let credentials = {
        username: username,
        password: password,
      };

      axios
        .post(registrationApiEndPoint, credentials)
        .then((response) => {
          localStorage.removeItem(tokenKey);
          localStorage.setItem(tokenKey, response.data.token);
          resolve(true);
        })
        .catch((err) => {
          resolve(false);
        });
    } else {
      resolve(false);
    }
  });
}

export function loginAction(username, password) {
  return new Promise((resolve, reject) => {
    if (username && password) {
      let credentials = {
        username: username,
        password: password,
      };

      axios
        .post(authenticationApiEndPoint, credentials)
        .then((response) => {
          if (response.data.status == "SUCCESS") {
            localStorage.removeItem(tokenKey);
            localStorage.removeItem(permissionsKey);
            let permissions = response.data.permissions;
            localStorage.setItem(tokenKey, response.data.token);
            localStorage.setItem(permissionsKey, permissions);
            resolve(true);
          } else {
            resolve("Username password mismatch.");
          }
        })
        .catch((err) => {
          if (err.message.includes("400")) {
            resolve("Please provide username and password in correct format");
          } else {
            resolve("Please contact system administrator: " + err.message);
          }
        });
    } else {
      resolve("Failed to authenticate, Please contact system administrator!");
    }
  });
}

export function logout() {
  localStorage.removeItem(tokenKey);
}

export async function checkAuthentication() {
  return userDetails();
}

export async function loadUser() {
  return userDetails();
}


async function userDetails() {
  // Get the current 'global' time from an API using Promise
  return new Promise((resolve, reject) => {
    let token = localStorage.getItem("token");
    if (token == null || token == "null" || token == "") {
      resolve(false);
      return;
    }
    var config = {
      headers: { Authorization: "Bearer " + token },
    };

    axios
      .get(userApiEndPoint, config)
      .then((response) => {
        let authorities = response.data.authorities;
        if (authorities.length > 0 && response.data.activated) {
          let user = {
            id : response.data.id,
            username: response.data.username,
            firstName: response.data.firstName,
            lastName: response.data.lastName,
            mobileNo: response.data.mobileNo,
            designation: response.data.designation,
            email: response.data.email,
            companyId: response.data.companyId,
            membershipId: response.data.membershipId,
            membershipType: response.data.membershipType,
            profilePicture: response.data.profilePicture,
          };
          localStorage.setItem("user", JSON.stringify(user));
          localStorage.setItem("authorities", JSON.stringify(authorities));
          resolve(true);
        } else {
          resolve(false);
        }
      })
      .catch((err) => {
        if (err == "Error: Request failed with status code 401") {
          resolve(false);
        } else if (err == "Error: Request failed with status code 500") {
          resolve(false);
        } else {
          // reject('Service Invokation error!');
          reject("Loading...");
        }
      });
  });
}

export async function loadTranslations(activeLanguage) {
  // Get the current 'global' time from an API using Promise
  return new Promise((resolve, reject) => {
    let token = localStorage.getItem("token");
    if (token == null || token == "null" || token == "") {
      resolve(false);
      return;
    }
    var config = {
      headers: { Authorization: "Bearer " + token },
    };
    let url = loadTranslationsApiEndPoint + activeLanguage;
    axios
      .get(url, config)
      .then((response) => {
        let translations = response.data;
        if (translations) {
          localStorage.setItem("activeLanguage", activeLanguage);
          localStorage.setItem("translations", JSON.stringify(translations));
          resolve(true);
        } else {
          resolve(false);
        }
      })
      .catch((err) => {
        if (err == "Error: Request failed with status code 401") {
          resolve(false);
        } else if (err == "Error: Request failed with status code 500") {
          resolve(false);
        } else {
          reject("Service Invokation error!");
        }
      });
  });
}


// the loadTranslationsAuthless function to work without authentication
export async function loadTranslationsAuthless(activeLanguage) {
  return new Promise((resolve, reject) => {
    let url = `${loadTranslationsAuthlessApiEndPoint}${activeLanguage}`;
    axios
      .get(url)
      .then((response) => {
        let translations = response.data;
        if (translations) {
          localStorage.setItem("activeLanguage", activeLanguage);
          localStorage.setItem("translations", JSON.stringify(translations));
          resolve(true);
        } else {
          resolve(false);
        }
      })
      .catch((err) => {
        if (err.response && (err.response.status === 401 || err.response.status === 500)) {
          resolve(false);
        } else {
          reject("Service Invocation error!");
        }
      });
  });
}



