import React, { Component } from "react";
import Table from "../common/table";
import {Button, Form, Modal} from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	  faEye,
	  faPencilAlt,
	  faCheck
	} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class ProductViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" onClick={this.props.onClick}>
        <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
          	<Form>
          	  <div className="form-group row">
        <div className="col-md-6">
          <b><label>Title</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.title}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Default Image</label></b>
        </div>
        <div className="col-md-6">
          <img src={this.props.data.defaultImage} width={'200px'} height={'100px'}/>
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Current Price</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.currentPrice}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Previous Price</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.previousPrice}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Description</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.description}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Specification</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.specification}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Remarks</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.remarks}
        </div>
      </div>
	
	          {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
	        </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class ProductTable extends UxCrudTable {
  state = {
	DATA_CREATE: "PRODUCT_CREATE",
    DATA_READ: "PRODUCT_READ",
    DATA_UPDATE: "PRODUCT_UPDATE",
    DATA_DELETE: "PRODUCT_DELETE",
    DATA_APPROVE: "PRODUCT_APPROVE",
	userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    translations: {
	  titleLabel: "Product",
	  actionsLabel: "Actions",
	  idLabel: "Id",
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      supplierIdLabel: "Supplier",
categoryIdLabel: "Category",
subCategoryIdLabel: "Sub Category ",
titleLabel: "Product Title",
defaultImageLabel: "Default Image",
productImageLabel: "Product Image",
productPriceLabel: "Product Image",
productVariantLabel: "Product Variant",
currentPriceLabel: "Current Price",
previousPriceLabel: "Previous Price",
descriptionLabel: "Product Description",
specificationLabel: "Product Specification",
remarksLabel: "Remarks",

    },
  };

 
  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,
          idLabel: translationsObj["i18n.common.idLabel"]
            ? translationsObj["i18n.common.idLabel"]
            : thisContext.state.translations.idLabel,
         actionsLabel: translationsObj["i18n.common.actionsLabel"]
            ? translationsObj["i18n.common.actionsLabel"]
            : thisContext.state.translations.actionsLabel,

          supplierIdLabel: translationsObj["i18n.product.supplierId.label"] ? translationsObj["i18n.product.supplierId.label"] : thisContext.state.translations.supplierIdLabel,
categoryIdLabel: translationsObj["i18n.product.categoryId.label"] ? translationsObj["i18n.product.categoryId.label"] : thisContext.state.translations.categoryIdLabel,
subCategoryIdLabel: translationsObj["i18n.product.subCategoryId.label"] ? translationsObj["i18n.product.subCategoryId.label"] : thisContext.state.translations.subCategoryIdLabel,
titleLabel: translationsObj["i18n.product.title.label"] ? translationsObj["i18n.product.title.label"] : thisContext.state.translations.titleLabel,
defaultImageLabel: translationsObj["i18n.product.defaultImage.label"] ? translationsObj["i18n.product.defaultImage.label"] : thisContext.state.translations.defaultImageLabel,
productImageLabel: translationsObj["i18n.product.productImage.label"] ? translationsObj["i18n.product.productImage.label"] : thisContext.state.translations.productImageLabel,
productPriceLabel: translationsObj["i18n.product.productPrice.label"] ? translationsObj["i18n.product.productPrice.label"] : thisContext.state.translations.productPriceLabel,
productVariantLabel: translationsObj["i18n.product.productVariant.label"] ? translationsObj["i18n.product.productVariant.label"] : thisContext.state.translations.productVariantLabel,
currentPriceLabel: translationsObj["i18n.product.currentPrice.label"] ? translationsObj["i18n.product.currentPrice.label"] : thisContext.state.translations.currentPriceLabel,
previousPriceLabel: translationsObj["i18n.product.previousPrice.label"] ? translationsObj["i18n.product.previousPrice.label"] : thisContext.state.translations.previousPriceLabel,
descriptionLabel: translationsObj["i18n.product.description.label"] ? translationsObj["i18n.product.description.label"] : thisContext.state.translations.descriptionLabel,
specificationLabel: translationsObj["i18n.product.specification.label"] ? translationsObj["i18n.product.specification.label"] : thisContext.state.translations.specificationLabel,
remarksLabel: translationsObj["i18n.product.remarks.label"] ? translationsObj["i18n.product.remarks.label"] : thisContext.state.translations.remarksLabel,

        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

 async loadColumns() {
	this.columns = [
	    { path: "id", label: this.state.translations.idLabel },
	    {
        path: "title",
        label: this.state.translations.titleLabel,
      },
{
      key: "defaultImage",
      label: this.state.translations.defaultImageLabel,
      content: (item) => (
        <img src={item.defaultImage}  width={'60px'} height={'30px'} />
      ),
    },{
        path: "currentPrice",
        label: this.state.translations.currentPriceLabel,
      },
{
        path: "previousPrice",
        label: this.state.translations.previousPriceLabel,
      },
{
        path: "description",
        label: this.state.translations.descriptionLabel,
      },
{
        path: "specification",
        label: this.state.translations.specificationLabel,
      },
{
        path: "remarks",
        label: this.state.translations.remarksLabel,
      },

	  // {
      //   key: "view",
      //   label: this.state.translations.viewLabel,
      //   content: (item) => (
      //     <ProductViewModal
      //       itemId={this.state.itemId}
      //       show={this.state.showView}
      //       title={this.state.title}
      //       body={this.state.body}
      //       data={this.state.data}
      //       onClick={() => this.handleViewShow(item)}
      //       onHide={this.handleViewClose}
      //     />
      //   ),
      // },
  	];

    this.loadActionColumns();
	this.setState({ columns: this.columns });
  }

}

export default ProductTable;
