import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./relatedProductService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';
import { getAll as getAllProduct } from "../product/productService";

class RelatedProductForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["RELATED_PRODUCT_CREATE","RELATED_PRODUCT_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/relatedProducts/",
	userPermissions: [],
    data: { id: null , productId: "", relatedPrdId: "", rating: "" },
    errors: {},
    validationMessage: {
      product: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Related Product",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	productIdLabel: "Product",
productIdPlaceholder: "Select Product",
relatedPrdIdLabel: "Related Product",
relatedPrdIdPlaceholder: "Enter Related Product",
ratingLabel: "Rating",
ratingPlaceholder: "Enter Rating",

    },
	  productOptions : [],
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    product: Joi.number().label("Product"),relatedPrdId: Joi.string().label({this.state.translations.relatedPrdIdLabel}),rating: Joi.string().label({this.state.translations.ratingLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      await getAllProduct().then(function (result) {
        let productOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          productOptions.push({
            value: result.data[i].id,
            label: result.data[i].title,
          });
        }
        thisContext.setState({ productOptions: productOptions });
      });

      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.relatedProduct.titleLabel"] ? translationsObj["i18n.relatedProduct.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          productIdLabel: translationsObj["i18n.relatedProduct.productId.label"] ? translationsObj["i18n.relatedProduct.productId.label"] : thisContext.state.translations.productIdLabel,
productIdPlaceholder: translationsObj["i18n.relatedProduct.productId.placeholder"] ? translationsObj["i18n.relatedProduct.productId.placeholder"] : thisContext.state.translations.productIdPlaceholder,
relatedPrdIdLabel: translationsObj["i18n.relatedProduct.relatedPrdId.label"] ? translationsObj["i18n.relatedProduct.relatedPrdId.label"] : thisContext.state.translations.relatedPrdIdLabel,
relatedPrdIdPlaceholder: translationsObj["i18n.relatedProduct.relatedPrdId.placeholder"] ? translationsObj["i18n.relatedProduct.relatedPrdId.placeholder"] : thisContext.state.translations.relatedPrdIdPlaceholder,
ratingLabel: translationsObj["i18n.relatedProduct.rating.label"] ? translationsObj["i18n.relatedProduct.rating.label"] : thisContext.state.translations.ratingLabel,
ratingPlaceholder: translationsObj["i18n.relatedProduct.rating.placeholder"] ? translationsObj["i18n.relatedProduct.rating.placeholder"] : thisContext.state.translations.ratingPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("RELATED_PRODUCT_CREATE") ||
      thisContext.state.userPermissions.includes("RELATED_PRODUCT_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      productId: obj.productId,relatedPrdId: obj.relatedPrdId,rating: obj.rating,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-light">
                <Row>
                  <Col md='1'>
                    {
                      this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                        e.preventDefault();
                        thisContext.props.history.push(thisContext.state.basicUrl);
                      })
                    }
                  </Col>
                  <Col md='11'>
                    <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                  </Col>
                </Row>
              </Card.Title>
			{/*<Card.Header>
				<p>Hello Related Product please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}

                {this.renderInput(false,false,"id", "Id", "hidden")}
                <Row><Col>{this.renderSelect(true,false,"productId", this.state.translations.productIdLabel, this.state.translations.productIdPlaceholder, thisContext.state.productOptions, this.state.validationMessage.productId)}</Col></Row>
<Row><Col>{this.renderInput(false,true,"relatedPrdId", this.state.translations.relatedPrdIdLabel,this.state.translations.relatedPrdIdPlaceholder  ,"number",false,this.state.validationMessage.relatedPrdId)}</Col></Row>
<Row><Col>{this.renderInput(false,true,"rating", this.state.translations.ratingLabel,this.state.translations.ratingPlaceholder  ,"number",false,this.state.validationMessage.rating)}</Col></Row>

                {/* {this.renderInput("dueDate", "Due Date")} */}

                
            </Card.Body>

				{thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
          		</Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default RelatedProductForm;
