import React, { Component } from "react";
import { Col, Row, Breadcrumb } from "react-bootstrap";
import { NavLink } from "react-router-dom";

class CustomBreadcrumb extends Component {
  state = {
    moduleUrl: this.props.moduleUrl,
    basicUrl: this.props.basicUrl,
    titleLabel: this.props.titleLabel,
  };
  render() {
    return (
      <Row>
        <Col>
          <Breadcrumb>
            <Breadcrumb.Item href={this.state.moduleUrl}>Home</Breadcrumb.Item>
            <Breadcrumb.Item active>
              <NavLink to={this.state.basicUrl}>
                {this.state.titleLabel}
              </NavLink>
            </Breadcrumb.Item>
          </Breadcrumb>
        </Col>
      </Row>
    );
  }
}

export default CustomBreadcrumb;
