import http from "../../common/httpService";
import config from "../../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/authoritys";

function authorityUrl(id) {
  return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(
    apiEndpoint + "?page=" + page + "&size=" + size,
    headerOptions
  );
}
export function getAll() {
  let headerOptions = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(authorityId) {
  const options = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.get(authorityUrl(authorityId), options);
}

export function save(datObj) {
  return datObj.name !== null ? update(datObj) : create(datObj);
}

export function create(authority) {
  const options = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.post(apiEndpoint, authority, options);
}

export function update(authority) {
  const options = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.put(apiEndpoint, authority, options);
}

export function remove(name) {
  const options = {
    headers: { Authorization: "Bearer " + localStorage.getItem("token") },
  };
  return http.delete(authorityUrl(name), options);
}
